export const LeaderbordChartOptions = (text: string, labels: any) => ({
  indexAxis: "y" as const,
  scales: {
    x: {
      autoSkip: false,
      stacked: false,
      beginAtZero: true,
    },
    y: {
      stacked: true,
      beginAtZero: true,
      ticks: {
        autoSkip: false,
        callback: (value: any) => {
          return labels[value].length > 35
            ? labels[value].slice(0, 33) + "..."
            : labels[value];
        },
      },
    },
  },
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text,
    },
    tooltip: {
      intersect: false,
      mode: "y",
    },
  },
});

