export const HistogramChartOptions = (
  text: string,
  xLabel: string,
  yLabel: string
) => ({
  // crossAlign: "near",
  barPercentage: 1.25,
  scales: {
    x: {
      type: "linear",
      ticks: {
        source: "auto",
        stepSize: 1,
      },
      title: {
        display: true,
        text: xLabel,
      },
    },
    y: {
      beginAtZero: true,
      ticks: {
        stepSize: 1,
      },
      title: {
        display: true,
        text: yLabel,
      },
    },
  },
  responsive: true,
  plugins: {
    legend: {
      display: false,
    },
    title: {
      display: true,
      text,
    },
    tooltip: {
      intersect: false,
      mode: "x",
      callbacks: {
        label: (tooltipItem: any) => {
          const dataset = tooltipItem.dataset;
          const total = dataset.data.reduce(function (
            previousValue: any,
            currentValue: any
          ) {
            return previousValue + currentValue;
          });
          const currentValue = dataset.data[tooltipItem.dataIndex];
          const percentage = Math.floor((currentValue / total) * 100 + 0.5);
          return ` ${currentValue} deals (${percentage}%)`;
        },
        title: (tooltipItem: any) => {
          const label = parseInt(tooltipItem[0].label);
          return `Duration from ${label - 1} to ${label} hours`;
        },
      },
    },
  },
});

