import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { IBot } from "../../../../interfaces/Bot";
import {
  getBaseOrderSize,
  getCurrencyFormat,
  getSafetyOrderSize,
} from "../../../../utils/BotsUtils";
import "./DCABotDetailsSettingsInfo.css";

export const DCABotDetailsSettingsInfo = ({
  account,
  bot,
}: {
  account: any;
  bot: IBot;
}) => {
  const { t } = useTranslation();

  const [baseOrderSize, setBaseOrderSize] = useState({
    value: 0,
    currency: "",
    additionalCurrency: "",
  });
  const [safetyOrderSize, setSafetyOrderSize] = useState({
    value: 0,
    currency: "",
    additionalCurrency: "",
  });

  useEffect(() => {
    if (bot.id && account.id) {
      setBaseOrderSize(getBaseOrderSize(bot, account));
      setSafetyOrderSize(getSafetyOrderSize(bot, account));
    }
  }, [bot, account]);

  const leverage = bot.leverage_custom_value
    ? parseInt(bot.leverage_custom_value)
    : "";
  const leverageType =
    bot.leverage_type !== "not_specified" ? bot.leverage_type : "";

  return (
    <div className="BotInfoTable">
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Profit currency</div>
        <div className="BotInfoTableRow">
          {bot.profit_currency === "quote_currency" ? "Quote" : "Base"}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Take profit (type)</div>
        <div className="BotInfoTableRow">
          <div>
            {`${getCurrencyFormat({
              value: parseFloat(bot.take_profit),
              currency: "%",
            })}%`}
          </div>
          <div>
            {bot.take_profit_type === "total"
              ? "(from total volume)"
              : "(from base volume)"}
          </div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Trailing</div>
        <div className="BotInfoTableRow">
          {bot.trailing_enabled &&
            `${getCurrencyFormat({
              value: parseFloat(bot.trailing_deviation),
              currency: "%",
            })}%`}
          {!bot.trailing_enabled && "OFF"}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Stop loss (timeout)</div>
        <div className="BotInfoTableRow">{`${getCurrencyFormat({
          value: parseFloat(bot.stop_loss_percentage),
          currency: "%",
        })}% (${bot.stop_loss_timeout_in_seconds} s)`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Base order size (type)</div>
        <div className="BotInfoTableRow">
          <div>
            <div>{`${getCurrencyFormat(baseOrderSize)} ${
              baseOrderSize.currency
            }${
              baseOrderSize.additionalCurrency
                ? ` (${baseOrderSize.additionalCurrency})`
                : ""
            }`}</div>
            <div>{`(${bot.start_order_type})`}</div>
          </div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Leverage (type)</div>
        <div className="BotInfoTableRow">
          <div>{leverage || "0"}</div>
          <div>{leverageType}</div>
        </div>
      </div>
    </div>
  );
};
