import React, { useMemo } from "react";
import "./CurrencyIcon.css";
import iconsData from "./coins_icons1.json";
import _ from "lodash";

const CurrencyIcon = ({ currency, size = 15 }: any) => {
  const formattedCurrency =
    currency.length > 5
      ? currency
          .replace("USDT", "")
          .replace("BUSD", "")
          .replace("-PERP", "")
          .replace("USD", "")
      : currency;

  const icons = useMemo(
    () => _.chain(iconsData).keyBy("symbol").mapValues("id").value(),
    []
  );

  return (
    <div
      className="CurrencyIcon"
      style={{
        width: size,
        height: size,
        borderRadius: size,
        backgroundSize: size,
        backgroundImage: `url(https://s2.coinmarketcap.com/static/img/coins/64x64/${
          icons[formattedCurrency.toUpperCase()]
        }.png)`,
      }}
    ></div>
  );
};

export default CurrencyIcon;
