import React, { useContext } from "react";
import { AccountsContext, SmartTradesContext } from "../../../../App";
import { useTable } from "react-table";
import { Badge, Table } from "react-bootstrap";

import "./SmartTradesTable.css";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import CurrencyIcon from "../../../CommonComponents/CurrencyIcon/CurrencyIcon";
import _ from "lodash";
import moment from "moment";

const smartTradeTypes: any = {
  simple_buy: "Simple Buy",
  simple_sell: "Simple Sell",
  smart_sell: "Smart Sell",
  smart_trade: "Smart Trade",
  smart_cover: "Smart Cover",
  smart_buy: "Smart Buy",
};

const SmartTradesTable = ({ setModalSmartTradeId }: any) => {
  const { smartTradesState } = useContext(SmartTradesContext);
  const { list, smartTrades } = smartTradesState;

  const {
    accountsState: { accounts, list: accountsList },
  } = useContext(AccountsContext);

  const columns = React.useMemo(
    () =>
      [
        {
          Header: "Account",
          id: "account",
          accessor: (originalRow: any, rowIndex: any) => {
            return originalRow.account.id;
          },
          disableSortBy: true,
          Cell: ({ cell: { value } }: any) => {
            const accountId = accountsList.filter(
              (accId: any) => accId === value
            )[0];
            return (
              <div style={{ display: "flex" }}>
                <div
                  className="AccountMarketIcon"
                  style={{
                    backgroundImage: `url(${accounts[accountId].market_icon})`,
                  }}
                ></div>
                <div style={{ width: "100px" }}>{accounts[accountId].name}</div>
              </div>
            );
          },
        },

        {
          Header: "Pair",
          Cell: ({ cell: { value } }: any) => {
            const baseCurrency = value.split("_")[1];

            return (
              <div style={{ display: "flex" }}>
                <CurrencyIcon currency={baseCurrency} />
                <div style={{ marginLeft: "4px" }}>
                  {value.split("_").reverse().join("/")}
                </div>
              </div>
            );
          },
          accessor: "pair",
          disableSortBy: true,
        },
        {
          Header: "Side",
          accessor: (originalRow: any, rowIndex: any) => {
            const side = originalRow.position.type === "buy" ? "LONG" : "SHORT";
            return side;
          },
          id: "side",
          disableSortBy: true,
        },
        {
          Header: "Type",
          accessor: (originalRow: any, rowIndex: any) =>
            smartTradeTypes[originalRow.data.type],

          id: "type",
          disableSortBy: true,
        },
        {
          Header: "Status",
          accessor: (originalRow: any, rowIndex: any) =>
            originalRow.status.title,

          id: "status",
          disableSortBy: true,
        },
        {
          Header: "PnL, %",
          accessor: (originalRow: any, rowIndex: any) => {
            const pnl_percent = parseFloat(originalRow.profit.percent);
            return pnl_percent ? (
              <div
                className={
                  pnl_percent >= 0 ? "PositiveNumber" : "NegativeNumber"
                }
              >{`${getCurrencyFormat({
                value: pnl_percent,
                currency: "USD",
              })}%`}</div>
            ) : (
              ""
            );
          },
          id: "pnl_percent",
          disableSortBy: true,
        },
        {
          Header: "PnL",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];

            const pnl = parseFloat(originalRow.profit.volume);
            return pnl ? (
              <div
                className={pnl >= 0 ? "PositiveNumber" : "NegativeNumber"}
              >{`${getCurrencyFormat({
                value: pnl,
                currency: quoteCurrency,
              })} ${quoteCurrency}`}</div>
            ) : (
              ""
            );
          },
          id: "pnl_quote",
          disableSortBy: true,
        },
        {
          Header: "PnL, $",
          accessor: (originalRow: any, rowIndex: any) => {
            const pnl_usd = parseFloat(originalRow.profit.usd);
            return pnl_usd ? (
              <Badge
                bg={pnl_usd >= 0 ? "success" : "danger"}
                style={{ fontSize: "13px" }}
              >{`${getCurrencyFormat({
                value: pnl_usd,
                currency: "USD",
              })}$`}</Badge>
            ) : (
              ""
            );
          },
          id: "pnl_usd",
          disableSortBy: true,
        },
        {
          Header: "Volume",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];
            const baseCurrency = originalRow.pair.split("_")[1];

            return (
              <>
                <div style={{ display: "flex" }}>
                  <CurrencyIcon currency={baseCurrency} />
                  <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                    value: originalRow.position.units.value,
                    currency: baseCurrency,
                  })} ${baseCurrency}`}</div>
                </div>
                <div style={{ display: "flex" }}>
                  <CurrencyIcon currency={quoteCurrency} />
                  <div style={{ marginLeft: "4px" }}>{`${getCurrencyFormat({
                    value: originalRow.position.total.value,
                    currency: quoteCurrency,
                  })} ${quoteCurrency}`}</div>
                </div>
              </>
            );
          },

          id: "volume",
          disableSortBy: true,
        },
        {
          Header: "Margin and Leverage",
          accessor: (originalRow: any, rowIndex: any) => {
            const baseCurrency = originalRow.pair.split("_")[1];

            return originalRow.leverage.enabled ? (
              <>
                <div style={{ display: "flex" }}>
                  <div>{`${getCurrencyFormat({
                    value: originalRow.margin.total,
                    currency: baseCurrency,
                  })} ${baseCurrency}`}</div>
                </div>
                <div style={{ display: "flex" }}>
                  {originalRow.leverage && (
                    <div>
                      {`x${Math.floor(originalRow.leverage.value)} ${
                        originalRow.leverage.type
                      }`}
                    </div>
                  )}
                </div>
              </>
            ) : (
              ""
            );
          },

          id: "margin",
          disableSortBy: true,
        },
        {
          Header: "Created at",
          accessor: (originalRow: any, rowIndex: any) => {
            const date = moment(originalRow.data.created_at);
            return (
              <>
                <div>{date.format("DD/MM/YYYY")}</div>
                <div>{date.format("HH:mm")}</div>
              </>
            );
          },

          id: "created_at",
          disableSortBy: true,
        },
        {
          Header: "Closed at",
          accessor: (originalRow: any, rowIndex: any) => {
            const date = moment(originalRow.data.closed_at);
            return (
              originalRow.data.closed_at && (
                <>
                  <div>{date.format("DD/MM/YYYY")}</div>
                  <div>{date.format("HH:mm")}</div>
                </>
              )
            );
          },

          id: "closed_at",
          disableSortBy: true,
        },
      ] as any,
    []
  );

  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable({
      columns,
      data: list.map((smartTradeId: number) => smartTrades[smartTradeId]),
    });

  return (
    <div className="SmartTradesTable">
      <Table hover {...getTableProps()}>
        <thead style={{ fontSize: "14px" }}>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps()}>
                  <div style={{ display: "flex", width: "100%" }}>
                    {column.render("Header")}
                    <div
                      style={{
                        marginLeft: "auto",
                        cursor: column.disableSortBy ? "auto" : "pointer",
                      }}
                    >
                      {column.isSorted
                        ? column.isSortedDesc
                          ? " 🔽"
                          : " 🔼"
                        : "   "}
                    </div>
                  </div>
                </th>
              ))}
            </tr>
          ))}
        </thead>

        <tbody style={{ fontSize: "14px" }} {...getTableBodyProps()}>
          {rows.map((row: any, i) => {
            prepareRow(row);
            return (
              <tr
                style={{ height: "112px", cursor: "pointer" }}
                {...row.getRowProps()}
                onClick={() => {
                  setModalSmartTradeId(row.original.id);
                }}
              >
                {row.cells.map((cell: any) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
};

export default SmartTradesTable;
