import React, { useEffect, useState } from "react";
import { Bar, Line } from "react-chartjs-2";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import {
  AccumulativeChartOptions,
  LeaderbordChartOptions,
  ProfitChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  getOneBotCurrencyLeaderbord,
  getOneBotAccumulativeProfitsChartData,
  getOneBotProfitsChartData,
} from "../../../../utils/DealsUtils";
import "./DCABotDetailsProfitsCharts.css";

export const DCABotDetailsProfitsCharts = ({
  deals,
  timeRangeValue,
  dateLabelsDays,
  bot,
}: {
  deals: IDeal[];
  timeRangeValue: string;
  dateLabelsDays: string[];
  bot: IBot;
}) => {
  const [accumulativeProfitsData, setAccumulativeProfitsData] = useState<any>([
    [],
    [],
  ]);
  const [profitsData, setProfitsData] = useState<any>([[], []]);
  const [pairsLeaderbordData, setPairsLeaderbordData] = useState<any>([[], []]);

  useEffect(() => {
    setAccumulativeProfitsData(getOneBotAccumulativeProfitsChartData(deals));
    setProfitsData(getOneBotProfitsChartData(deals));
    setPairsLeaderbordData(getOneBotCurrencyLeaderbord(deals));
  }, [deals]);
  return (
    <>
      <div className="DCABotDetailsChartContainer">
        <Line
          options={AccumulativeChartOptions("Accumulative Profit, $") as any}
          data={{
            labels: accumulativeProfitsData[0],
            datasets: [
              {
                label: "Accumulative Profit, $",
                data: accumulativeProfitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
                fill: { value: 0 },
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={ProfitChartOptions("Daily Profit, $") as any}
          data={{
            labels: profitsData[0],
            datasets: [
              {
                label: "Profit, $",
                data: profitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
      {bot.type !== "Bot::SingleBot" && (
        <div className="DCABotDetailsChartContainer">
          <Bar
            options={
              LeaderbordChartOptions(
                "Pairs leaderboard by profit",
                pairsLeaderbordData[0]
              ) as any
            }
            data={{
              labels: pairsLeaderbordData[0],
              datasets: [
                {
                  label: "Profit, $",
                  data: pairsLeaderbordData[1],
                  borderColor: "rgb(53, 162, 235)",
                  backgroundColor: "rgba(53, 162, 235, 0.5)",
                },
              ],
            }}
          />
        </div>
      )}
    </>
  );
};
