import _ from "lodash";
import moment from "moment";
import { IGridBot } from "../interfaces/GridBot";
import {
  differenceInDays,
  differenceInHours,
  getAccumulativeProfitChartData,
  getProfitChartData,
} from "./DealsUtils";
import { getCurrencyValueInUsd } from "./GeneralUtils";

const cryptoCoins: any = {
  BTC: "bitcoin",
  ETH: "ethereum",
  BNB: "binancecoin",
  USDT: "tether",
  USDC: "usd-coin",
  XRP: "ripple",
  TRX: "tron",
  BUSD: "binance-usd",
  DOGE: "dogecoin",
};

export const getInitialInvestment = async (gridBot: IGridBot) => {
  const startPrice = parseFloat(gridBot.start_price);
  const quantityPerGrid = parseFloat(gridBot.quantity_per_grid);

  const pureInitialGrids = gridBot.grid_lines
    .slice()
    .sort((a: any, b: any) => {
      return (
        Math.abs(startPrice - parseFloat(a.price)) -
        Math.abs(startPrice - parseFloat(b.price))
      );
    })
    .slice();

  pureInitialGrids.shift();

  const baseInvestment = _.sum(
    pureInitialGrids
      .filter((gridLine: any) => parseFloat(gridLine.price) > startPrice)
      .map((gridLine: any) => quantityPerGrid)
  );

  const quoteInvestment = _.sum(
    pureInitialGrids
      .filter((gridLine: any) => parseFloat(gridLine.price) < startPrice)
      .map((gridLine: any) => parseFloat(gridLine.price) * quantityPerGrid)
  );

  const allInQuote = quoteInvestment + baseInvestment * startPrice;

  let allInUsd = 0;

  const quoteCurrency = gridBot.pair.split("_")[0];

  const createdOnDay = moment.utc(gridBot.updated_at);

  if (quoteCurrency.includes("USD")) {
    allInUsd = allInQuote;
  } else if (quoteCurrency in cryptoCoins) {
    const marketData: any = await fetch(
      `https://api.coingecko.com/api/v3/coins/${
        cryptoCoins[quoteCurrency]
      }/history?date=${createdOnDay.format("DD-MM-YYYY")}&localization=false`
    ).then((response: any) => response.json());

    allInUsd = allInQuote * marketData.market_data.current_price.usd;
  } else {
    if (moment().utc().diff(createdOnDay, "days") < 366) {
      const marketData: any = await fetch(
        `https://api.currconv.com/api/v7/convert?apiKey=8ae78b023a9240049d4d56f97c81020a&q=${quoteCurrency}_USD&compact=ultra&date=${createdOnDay.format(
          "YYYY-MM-DD"
        )}&apiKey=e4dcebf8eb9df2180846`
      ).then((response: any) => response.json());

      allInUsd =
        allInQuote *
        marketData[`${quoteCurrency}_USD`][createdOnDay.format("YYYY-MM-DD")];
    }
  }

  return { baseInvestment, quoteInvestment, allInQuote, allInUsd };
};

export const getCurrentInvestment = async (gridBot: IGridBot) => {
  const currentPrice = parseFloat(gridBot.current_price);

  const baseInvestment = parseFloat(gridBot.investment_base_currency);

  const quoteInvestment = parseFloat(gridBot.investment_quote_currency);

  const allInQuote = quoteInvestment + baseInvestment * currentPrice;

  const quoteCurrency = gridBot.pair.split("_")[0];

  const allInUsd = await getCurrencyValueInUsd(
    "binance",
    quoteCurrency,
    allInQuote
  );

  return { baseInvestment, quoteInvestment, allInQuote, allInUsd };
};

const hoursWithin30Days = 24 * 30;

export const getProfitAgeInHours = (profit: any) =>
  differenceInHours(moment(), moment(profit.created_at));

export const getProfitsWithin30days = (profits: any) =>
  profits.profits.filter(
    (profit: any) => getProfitAgeInHours(profit) <= hoursWithin30Days
  );

export const getMarketOrderAgeInHours = (marketOrder: any) =>
  differenceInHours(moment(), moment(marketOrder.created_at));

export const getMarketOrdersWithin30days = (marketOrders: any) =>
  marketOrders.grid_lines_orders.filter(
    (marketOrder: any) =>
      getMarketOrderAgeInHours(marketOrder) <= hoursWithin30Days
  );

export const getObjectOfProfitPerDay = (
  profits: Array<any>,
  marketOrders: any[],
  tradingFee: number
) => {
  const dateStringProfits = (item: any) =>
    moment(item.created_at).format("YYYY-MM-DD");
  let resultProfits = _.groupBy(profits, dateStringProfits);

  const dateStringMarketOrders = (item: any) =>
    moment(item.updated_at).format("YYYY-MM-DD");
  let resultMarketOrders = _.groupBy(marketOrders, dateStringMarketOrders);

  const objectProfits = _.mapValues(resultProfits, (profits: Array<any>) =>
    _.sum(profits.map((profit: any) => parseFloat(profit.profit)))
  );

  const objectMarketOrders = _.mapValues(
    resultMarketOrders,
    (marketOrders: Array<any>) =>
      _.sum(
        marketOrders.map((marketOrder: any) => parseFloat(marketOrder.total))
      ) *
      tradingFee *
      0.01 *
      -1
  );

  return _.mergeWith({}, objectProfits, objectMarketOrders, _.add);
};

export const getOneGridBotAccumulativeProfitsChartData = (
  profits: any[],
  gridBot: IGridBot,
  timeRangeValue: any,
  marketOrders: any[],
  tradingFee: number
) => {
  const botsProfits = getObjectOfProfitPerDay(
    profits,
    marketOrders,
    tradingFee
  );
  const difference =
    timeRangeValue === "30d"
      ? 31
      : Math.max(
          differenceInDays(moment(), moment(gridBot.created_at)) + 1,
          31
        );
  return getAccumulativeProfitChartData(botsProfits, difference);
};

export const getOneGridBotProfitsChartData = (
  profits: any[],
  gridBot: IGridBot,
  timeRangeValue: any,
  marketOrders: any[],
  tradingFee: number
) => {
  const botsProfits = getObjectOfProfitPerDay(
    profits,
    marketOrders,
    tradingFee
  );
  const difference =
    timeRangeValue === "30d"
      ? 31
      : Math.max(
          differenceInDays(moment(), moment(gridBot.created_at)) + 1,
          31
        );
  return getProfitChartData(botsProfits, difference);
};
