import _ from "lodash";
import React, { FC, useContext, useEffect, useState } from "react";
import { Button, Table } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { AccountsContext, ErrorsContext } from "../../../App";
import { IAccount } from "../../../interfaces/Account";
import {
  fetchAccount,
  fetchAccountPieChartData,
  fetchAccountTableData,
} from "../../../state/accounts/AccountsEffects";
import { getCurrencyFormat } from "../../../utils/BotsUtils";
import { AccountPieChart } from "../AccountsList/AccountPreview/AccountPreview";
import "./AccountsSummaryPreview.css";

interface AccountsSummaryPreviewProps {}

const AccountsSummaryPreview: FC<AccountsSummaryPreviewProps> = () => {
  const { accountsState, accountsDispatch } = useContext(AccountsContext);

  const { accounts, accountPieChartData, accountTableData } = accountsState;

  const { errorsDispatch } = useContext(ErrorsContext);
  const [summary, setSummary] = useState<IAccount | null>(null);
  const [summaryPieChart, setSummaryPieChart] = useState<any>(null);
  const [summaryTableData, setSummaryTableData] = useState<any>(null);

  const navigate = useNavigate();

  useEffect(() => {
    fetchAccountPieChartData(
      accountsDispatch,
      accountsState,
      errorsDispatch,
      "summary"
    );
    fetchAccountTableData(
      accountsDispatch,
      accountsState,
      errorsDispatch,
      "summary"
    );
    fetchAccount(accountsDispatch, accountsState, errorsDispatch, "summary");
  }, []);

  useEffect(() => {
    if ("summary" in accountPieChartData) {
      setSummaryPieChart(accountPieChartData["summary"]);
    }
  }, [accountPieChartData]);

  useEffect(() => {
    if ("summary" in accountTableData) {
      setSummaryTableData(
        _.orderBy(accountTableData["summary"], ["percentage"], ["desc"]).slice(
          0,
          3
        )
      );
    }
  }, [accountTableData]);

  useEffect(() => {
    if ("summary" in accounts) {
      setSummary(accounts["summary"]);
    }
  }, [accounts]);

  return (
    <div className="AccountsSummaryPreview">
      <div className="AccountsSummaryPreviewColumn">
        <div className="AccountNameContainer">
          <span className="AccountName">Agrregate / Summary account</span>
        </div>
        <div className="AccountsSummaryPreviewCharts">
          <div className="AccountsSummaryPreviewPieChart">
            {summary && summaryPieChart && (
              <AccountPieChart accountPieChart={summaryPieChart} />
            )}
          </div>
          {summary && (
            <div className="AccountsSummaryPreviewBalanceTable">
              <Table size="sm" style={{ fontSize: "12px" }}>
                <thead>
                  <tr>
                    <th>Balance value</th>
                    <th>24 hr change</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>{`${getCurrencyFormat({
                      value: parseFloat(summary.usd_amount),
                      currency: "USD",
                    })} $`}</td>
                    <td
                      className={
                        parseFloat(summary.day_profit_usd) >= 0
                          ? "showGreen"
                          : "showRed"
                      }
                    >{`${getCurrencyFormat({
                      value: parseFloat(summary.day_profit_usd),
                      currency: "USD",
                    })} $`}</td>
                  </tr>
                  <tr>
                    <td>{`${getCurrencyFormat({
                      value: parseFloat(summary.btc_amount),
                      currency: "BTC",
                    })} BTC`}</td>
                    <td
                      className={
                        parseFloat(summary.day_profit_btc) >= 0
                          ? "showGreen"
                          : "showRed"
                      }
                    >{`${getCurrencyFormat({
                      value: parseFloat(summary.day_profit_btc),
                      currency: "BTC",
                    })} BTC`}</td>
                  </tr>
                </tbody>
              </Table>
            </div>
          )}
        </div>
      </div>
      <div className="AccountsSummaryPreviewColumn">
        <div className="AccountNameContainer">
          <span className="AccountName">Top assets</span>
        </div>
        {summaryTableData && (
          <div className="AccountsSummaryPreviewAssetsTable">
            <Table size="sm" style={{ fontSize: "12px" }}>
              <thead>
                <tr>
                  <th>Token</th>
                  <th>Amount</th>
                  <th>Total value, $</th>
                </tr>
              </thead>
              <tbody>
                {summaryTableData.map((asset: any) => (
                  <tr key={asset.currency_code}>
                    <td>{asset.currency_code}</td>
                    <td>{`${getCurrencyFormat({
                      value: asset.position,
                      currency: asset.currency_code,
                    })}`}</td>
                    <td>{`${getCurrencyFormat({
                      value: asset.usd_value,
                      currency: "USD",
                    })} $`}</td>
                  </tr>
                ))}
              </tbody>
            </Table>
          </div>
        )}
      </div>
      <div className="AccountsSummaryButtonContainer">
        <Button
          className="AccountPreviewButton"
          onClick={() => {
            navigate({
              pathname: `/accounts/summary`,
              search: window.location.search,
            });
          }}
        >
          View
        </Button>
      </div>
    </div>
  );
};

export default AccountsSummaryPreview;
