import { getDeals } from "../../utils/ApiUtils";
import { getAllDealsForOneBot } from "../../utils/DealsUtils";
import {
  getActiveDealsList,
  getDealsList,
  resetActiveDeals,
  resetDeals,
  setOffset,
} from "./DealsActions";
import { IDealsState } from "./DealsReducer";
import {
  INFINITY_IN_DAYS,
  is30daysDealsLoaded,
  isAllDealsLoaded,
} from "./DealsSelectors";

export async function fetchDealsForBotFor30Days(
  dealsState: IDealsState,
  dealsDispatch: any,
  errorsDispatch: any,
  botId: number
) {
  try {
    if (is30daysDealsLoaded(dealsState, botId)) {
      return;
    }
    const data = await getAllDealsForOneBot(botId, 30);
    dealsDispatch(getDealsList(data, botId, 30));
  } catch (e: any) {
    if (!e.message.includes("This limiter has been stopped")) {
      errorsDispatch({
        type: "addError",
        payload: `Error when fetching deals for bot ${botId}`,
      });
    }
  }
}

export async function fetchAllDealsForBot(
  dealsState: IDealsState,
  dealsDispatch: any,
  errorsDispatch: any,
  botId: number,
  reset = false
) {
  try {
    if (isAllDealsLoaded(dealsState, botId) && !reset) {
      return;
    }

    if (reset) {
      dealsDispatch(resetDeals(botId));
    }

    await fetchDealsForBotFor30Days(
      dealsState,
      dealsDispatch,
      errorsDispatch,
      botId
    );
    const data = await getAllDealsForOneBot(
      botId,
      INFINITY_IN_DAYS,
      (offset: number) => {
        dealsDispatch(setOffset(botId, offset));
      }
    );
    dealsDispatch(getDealsList(data, botId, INFINITY_IN_DAYS));
  } catch (e: any) {
    if (!e.message.includes("This limiter has been stopped")) {
      errorsDispatch({
        type: "addError",
        payload: `Error when fetching deals for bot ${botId}`,
      });
    }
  }
}

export const getAllActiveDeals = async (
  dealsDispatch: any,
  errorsDispatch: any,
  botId: number,
  reset = false
) => {
  try {
    if (reset) {
      dealsDispatch(resetActiveDeals(botId));
    }
    const data = await getDeals(botId, 0, 1000, "active", "created_at");
    dealsDispatch(getActiveDealsList(data, botId));
  } catch (e: any) {
    if (!e.message.includes("This limiter has been stopped")) {
      errorsDispatch({
        type: "addError",
        payload: `Error when fetching active deals for bot ${botId}`,
      });
    }
    return Promise.resolve([]);
  }
};
