import React, { useEffect, useState } from "react";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { filterAllSuccessfulDeals } from "../../../../state/deals/DealsSelectors";
import { getCurrencyFormat, getReturns } from "../../../../utils/BotsUtils";
import {
  getAverageDailyProfit,
  getAverageProfitFromDeal,
  getAverageTp,
  getTotalProfitUsd,
} from "../../../../utils/DealsUtils";
import { useTranslation } from "react-i18next";

import "./DCABotDetailsProfitsInfo.css";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

export const DCABotDetailsProfitsInfo = ({
  account,
  bot,
  deals,
  fromFilter,
  toFilter,
}: {
  account: any;
  bot: IBot;
  deals: IDeal[];
  fromFilter: any;
  toFilter: any;
}) => {
  const { t } = useTranslation();

  const formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    signDisplay: "always",
  });
  const percentFormatter = new Intl.NumberFormat("en-US", {
    style: "percent",
    minimumFractionDigits: 2,
    signDisplay: "always",
  });

  const [averageTp, setAverageTp] = useState({
    value: 0,
    currency: "",
  });
  const [averageProfitFromDeal, setAverageProfitFromDeal] = useState({
    value: 0,
    currency: "",
  });
  const [averageDailyProfit, setAverageDailyProfit] = useState({
    value: 0,
    currency: "",
  });
  const [returns, setReturns] = useState({
    value: 0,
    currency: "",
  });

  const [totalProfitUsd, setTotalProfitUsd] = useState(0);
  const [filteredDeals, setFilteredDeals] = useState<any>([]);

  useEffect(() => {
    setFilteredDeals(filterAllSuccessfulDeals(deals));
    setAverageDailyProfit(
      getAverageDailyProfit(bot.created_at, deals, fromFilter, toFilter)
    );
    if (bot.maxUsage) {
      setReturns({ value: getReturns(bot.maxUsage, deals), currency: "%" });
    }
    setTotalProfitUsd(getTotalProfitUsd(deals));
  }, [deals, bot]);

  useEffect(() => {
    setAverageTp(getAverageTp(filteredDeals));
    setAverageProfitFromDeal(getAverageProfitFromDeal(filteredDeals));
  }, [filteredDeals]);

  return (
    <div className="BotInfoTable">
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Today's profit/loss</div>
        <div
          className={`BotInfoTableRow ${
            (bot.todayUsdProfit || 0) < 0 ? "showRed" : "showGreen"
          }`}
        >
          {formatter.format(bot.todayUsdProfit || 0)}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Total profit/loss</div>
        <div
          className={`BotInfoTableRow ${
            totalProfitUsd < 0 ? "showRed" : "showGreen"
          }`}
        >
          {formatter.format(totalProfitUsd)}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotCurrentPnl")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Current uPnL</div>
        </OverlayTrigger>
        <div
          className={`BotInfoTableRow ${
            parseFloat(bot.active_deals_usd_profit) < 0
              ? "showRed"
              : "showGreen"
          }`}
        >
          {formatter.format(parseFloat(bot.active_deals_usd_profit))}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotAverageTP")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Average TP, %</div>
        </OverlayTrigger>
        <div
          className={`BotInfoTableRow ${
            averageTp.value < 0 ? "showRed" : "showGreen"
          }`}
        >
          {`${getCurrencyFormat(averageTp)}%`}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Average profit from deal</div>
        <div
          className={`BotInfoTableRow ${
            averageProfitFromDeal.value < 0 ? "showRed" : "showGreen"
          }`}
        >
          {formatter.format(averageProfitFromDeal.value || 0)}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Average daily profit</div>
        <div
          className={`BotInfoTableRow ${
            averageDailyProfit.value < 0 ? "showRed" : "showGreen"
          }`}
        >
          {formatter.format(averageDailyProfit.value)}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotMaxReturnsTime")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Returns</div>
        </OverlayTrigger>
        <div
          className={`BotInfoTableRow ${
            returns.value < 0 ? "showRed" : "showGreen"
          }`}
        >
          {percentFormatter.format(returns.value)}
        </div>
      </div>
    </div>
  );
};
