import React, { FC, useContext, useEffect } from "react";
import { ErrorsContext, SmartTradesContext } from "../../App";
import { fetchSmartTrades } from "../../state/smartTrades/SmartTradesEffects";
import "./SmartTrades.css";
import SmartTradesList from "./SmartTradesList/SmartTradesList";

interface SmartTradesProps {}

const SmartTrades: FC<SmartTradesProps> = () => {
  const { smartTradesState, smartTradesDispatch } =
    useContext(SmartTradesContext);
  const { errorsDispatch } = useContext(ErrorsContext);
  const { filters } = smartTradesState;

  useEffect(() => {
    fetchSmartTrades(
      smartTradesDispatch,
      smartTradesState,
      errorsDispatch
    ).then();
  }, [filters]);

  return (
    <div className="SmartTrades">
      <SmartTradesList />
    </div>
  );
};

export default SmartTrades;
