import _ from "lodash";
import { IBot } from "../interfaces/Bot";
import { IDeal } from "../interfaces/Deal";
import { IBotsState } from "../state/bots/BotsReducer";
import { callApi } from "./ApiUtils";
import { calculateProfitPerBot } from "./DealsUtils";
import { getCurrencyValueInUsd } from "./GeneralUtils";

export async function getMaxUsage(bot: any, account: any) {
  if (!bot || !account || !account.id) {
    return 0;
  }

  const maxActiveDeals = parseInt(bot.max_active_deals);
  let baseOrderVolume = parseFloat(bot.base_order_volume);
  const maxSafetyOrders = parseInt(bot.max_safety_orders);
  let safetyOrderVolume = parseFloat(bot.safety_order_volume);
  const leverage = parseFloat(bot.leverage_custom_value);
  const martingale = parseFloat(bot.martingale_volume_coefficient);

  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];

  if (bot.base_order_volume_type === "quote_currency") {
    const orderVolums = await convertOrderVolumesToUsdFromOtherCurrencies(
      account.market_code,
      quoteCurrency,
      baseOrderVolume,
      safetyOrderVolume
    );
    baseOrderVolume = orderVolums.baseOrderVolume;
    safetyOrderVolume = orderVolums.safetyOrderVolume;
  } else if (bot.base_order_volume_type === "base_currency") {
    const orderVolums = await convertOrderVolumesToUsdFromOtherCurrencies(
      account.market_code,
      baseCurrency,
      baseOrderVolume,
      safetyOrderVolume
    );
    baseOrderVolume = orderVolums.baseOrderVolume;
    safetyOrderVolume = orderVolums.safetyOrderVolume;
  } else if (bot.base_order_volume_type === "percent") {
    const orderVolums = await convertOrderVolumesToUsdFromPercents(
      bot.account_id,
      quoteCurrency,
      baseCurrency,
      account.market_code,
      bot.strategy,
      baseOrderVolume,
      safetyOrderVolume
    );
    baseOrderVolume = orderVolums.baseOrderVolume;
    safetyOrderVolume = orderVolums.safetyOrderVolume;
  }

  if (leverage) {
    baseOrderVolume = baseOrderVolume / leverage;
    safetyOrderVolume = safetyOrderVolume / leverage;
  }
  let maxUsage = 0;
  if (martingale !== 1 && maxSafetyOrders > 1) {
    const safetyOrderVolumes =
      (safetyOrderVolume * (1 - martingale ** maxSafetyOrders)) /
      (1 - martingale);
    maxUsage = (safetyOrderVolumes + baseOrderVolume) * maxActiveDeals;
  } else {
    maxUsage =
      (maxSafetyOrders * safetyOrderVolume + baseOrderVolume) * maxActiveDeals;
  }

  return maxUsage;
}

const convertOrderVolumesToUsdFromOtherCurrencies = async (
  market_code: any,
  currency: any,
  baseOrderVolume: any,
  safetyOrderVolume: any
) => {
  return {
    baseOrderVolume: await getCurrencyValueInUsd(
      market_code,
      currency,
      baseOrderVolume
    ),
    safetyOrderVolume: await getCurrencyValueInUsd(
      market_code,
      currency,
      safetyOrderVolume
    ),
  };
};

const convertOrderVolumesToUsdFromPercents = async (
  accountId: any,
  quoteCurrency: any,
  baseCurency: any,
  marketCode: any,
  botStrategy: any,
  baseOrderVolume: any,
  safetyOrderVolume: any
) => {
  const currencyForCalc =
    isSpotAccount(marketCode) && botStrategy === "short"
      ? baseCurency
      : quoteCurrency;

  const accountBalances = await callApi(
    "POST",
    `/public/api/ver1/accounts/${accountId}/account_table_data`
  ).then((response: any) => response.json());
  const balance = accountBalances.find(
    (accountBalance: any) => accountBalance.currency_code === currencyForCalc
  );

  let available = 0;
  if (balance) {
    available =
      (parseFloat(balance.equity) - parseFloat(balance.on_orders)) *
      parseFloat(balance.current_price_usd);
  }

  return {
    baseOrderVolume: (baseOrderVolume * available) / 100,
    safetyOrderVolume: (safetyOrderVolume * available) / 100,
  };
};

export const isSpotAccount = (marketCode: any) =>
  [
    "binance_futures",
    "binance_futures_coin",
    "bybit_usdt_perpetual",
    "bybit",
    "ftx_futures",
    "bitmex",
  ].indexOf(marketCode) === -1;

export function getFundsLocked(bot: any) {
  const leverage = parseFloat(bot.leverage_custom_value);
  return leverage ? bot.fundsLocked / leverage : bot.fundsLocked;
}

export function getFundsLockedInBotCurrency(
  activeDeals: IDeal[],
  bot: IBot,
  account: any
) {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  let fundsLocked = 0;
  let currency = quoteCurrency;

  if (bot.strategy === "long") {
    fundsLocked = activeDeals.reduce(
      (sum, deal) => sum + parseFloat(deal.bought_volume),
      0
    );
    currency = quoteCurrency;
  } else if (bot.strategy === "short" && isSpotAccount(account.market_code)) {
    fundsLocked = activeDeals.reduce(
      (sum, deal) => sum + parseFloat(deal.sold_amount),
      0
    );
    currency = baseCurrency;
  } else if (bot.strategy === "short" && !isSpotAccount(account.market_code)) {
    fundsLocked = activeDeals.reduce(
      (sum, deal) => sum + parseFloat(deal.sold_volume),
      0
    );
    currency = quoteCurrency;
  }
  const leverage = parseFloat(bot.leverage_custom_value);

  return {
    value: leverage ? fundsLocked / leverage : fundsLocked,
    currency,
  };
}

export const getBaseOrderSize = (bot: IBot, account: any) => {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  let currency = quoteCurrency;
  let additionalCurrency = "";

  if (bot.base_order_volume_type === "percent") {
    currency = "%";
    additionalCurrency =
      bot.strategy === "short" && isSpotAccount(account.market_code)
        ? baseCurrency
        : "";
  } else {
    currency =
      bot.base_order_volume_type === "quote_currency"
        ? quoteCurrency
        : baseCurrency;
  }
  return {
    value: parseFloat(bot.base_order_volume),
    currency,
    additionalCurrency,
  };
};

export const getSafetyOrderSize = (bot: IBot, account: any) => {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  let currency = quoteCurrency;

  let additionalCurrency = "";

  if (bot.safety_order_volume_type === "percent") {
    currency = "%";
    additionalCurrency =
      bot.strategy === "short" && isSpotAccount(account.market_code)
        ? baseCurrency
        : "";
  } else {
    currency =
      bot.safety_order_volume_type === "quote_currency"
        ? quoteCurrency
        : baseCurrency;
  }
  return {
    value: parseFloat(bot.safety_order_volume),
    currency,
    additionalCurrency,
  };
};

export const getCurrencyFormat = (
  { value, currency }: any,
  signDisplay: any = "auto"
) => {
  if (currency.includes("BTC")) {
    return new Intl.NumberFormat("en-US", {
      maximumFractionDigits: 6,
      signDisplay,
    }).format(value);
  } else if (currency === "marketPrice") {
    return new Intl.NumberFormat("en-US", {
      maximumFractionDigits: 8,
      signDisplay,
    }).format(value);
  } else {
    return new Intl.NumberFormat("en-US", {
      maximumFractionDigits: 2,
      signDisplay,
    }).format(value);
  }
};

export async function getMaxUsageInBotCurrency(bot: IBot, account: any) {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  let currency = quoteCurrency;
  let maxUsageInUsd = 0;

  if (bot.maxUsage) {
    maxUsageInUsd = bot.maxUsage;
  } else {
    maxUsageInUsd = await getMaxUsage(bot, account);
  }

  if (bot.strategy === "short" && isSpotAccount(account.market_code)) {
    currency = baseCurrency;
  } else {
    currency = quoteCurrency;
  }
  const currencyPriceInUsd = await getCurrencyValueInUsd(
    account.market_code,
    currency,
    1
  );
  return {
    value: maxUsageInUsd / currencyPriceInUsd,
    currency,
  };
}

export function getReservedFundsInBotCurrency(
  activeDeals: IDeal[],
  bot: IBot,
  account: any
) {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  let currency = quoteCurrency;
  let reservedFunds = 0;

  if (bot.strategy === "short" && isSpotAccount(account.market_code)) {
    reservedFunds = activeDeals.reduce(
      (sum, deal) => sum + parseFloat(deal.reserved_base_funds.toString()),
      0
    );
    currency = baseCurrency;
  } else {
    reservedFunds = activeDeals.reduce(
      (sum, deal) => sum + parseFloat(deal.reserved_quote_funds.toString()),
      0
    );
    currency = quoteCurrency;
  }
  return {
    value: reservedFunds,
    currency,
  };
}

export function getReturns(maxUsage: any, deals: any) {
  if (maxUsage === 0) {
    return 0;
  }
  const profit30Days = deals.reduce(
    (currDeal: any, nextDeal: any) => ({
      usd_final_profit:
        parseFloat(currDeal.usd_final_profit) +
        parseFloat(nextDeal.usd_final_profit),
    }),
    { usd_final_profit: 0 }
  )["usd_final_profit"];

  return profit30Days / maxUsage;
}

export function getBotsLeaderboardChartData(
  botsState: IBotsState,
  dealsState: any
) {
  const bots = botsState.list.map((botId: number) => {
    const bot = botsState.bots[botId];
    return {
      profit: calculateProfitPerBot(dealsState, botId) || 0,
      name: bot.name,
    };
  });
  const sortedAndSelected10FirstBots = _.sortBy(bots, ["profit"])
    .reverse()
    .slice(0, 10);
  return [
    sortedAndSelected10FirstBots.map((bot) => bot.name),
    sortedAndSelected10FirstBots.map(
      (bot) => Math.round((bot.profit + Number.EPSILON) * 100) / 100
    ),
  ];
}

export function getAllActiveDeals(botsState: IBotsState) {
  return botsState.list.reduce((acc, botId: number) => {
    const bot = botsState.bots[botId];
    return acc + bot.active_deals_count;
  }, 0);
}

export function getSummirizedUpnl(botsState: IBotsState) {
  return botsState.list.reduce((acc, botId: number) => {
    const bot = botsState.bots[botId];
    return acc + parseFloat(bot.active_deals_usd_profit);
  }, 0);
}

export function getSummirizedFundsLocked(botsState: IBotsState) {
  return botsState.list.reduce((acc, botId: number) => {
    const bot = botsState.bots[botId];
    return acc + getFundsLocked(bot);
  }, 0);
}
