import React, { useContext } from "react";
import { Badge, Table } from "react-bootstrap";
import { SmartTradesContext } from "../../../../App";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import "./SmartTradeDetailsStatusTable.css";

const SmartTradeDetailsStatusTable = ({ smartTradeId }: any) => {
  const { smartTradesState } = useContext(SmartTradesContext);

  const { smartTrades } = smartTradesState;

  const smartTrade = smartTrades[smartTradeId];

  return (
    <div className="SmartTradeDetailsStatusTable">
      {smartTrade && (
        <Table striped>
          <thead>
            <tr>
              <th>Average entry</th>
              <th>Average close</th>
              <th>Current price</th>
              <th>PnL, $</th>
            </tr>
          </thead>
          <tbody>
            <tr style={{ fontSize: "14px" }}>
              <td>{`${
                smartTrade.data.type === "smart_sell" ||
                smartTrade.data.type === "smart_buy"
                  ? smartTrade.position.price.value
                  : smartTrade.data.average_enter_price
              }`}</td>
              <td>{`${
                smartTrade.data.average_close_price
                  ? smartTrade.data.average_close_price
                  : ""
              }`}</td>
              <td>{`${
                smartTrade.data.current_price
                  ? smartTrade.data.current_price.last
                  : ""
              }`}</td>
              <td>
                {smartTrade.profit.usd ? (
                  <Badge
                    bg={smartTrade.profit.usd >= 0 ? "success" : "danger"}
                    style={{ fontSize: "13px" }}
                  >{`${getCurrencyFormat({
                    value: smartTrade.profit.usd,
                    currency: "USD",
                  })}$`}</Badge>
                ) : (
                  ""
                )}
              </td>
            </tr>
          </tbody>
        </Table>
      )}
    </div>
  );
};

export default SmartTradeDetailsStatusTable;
