import React, { useEffect, useContext, useState } from "react";
import {
  Button,
  ButtonGroup,
  FormControl,
  InputGroup,
  Modal,
  ToggleButton,
} from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import { AccountsContext, ErrorsContext, GridBotsContext } from "../../../App";
import { fetchDetails } from "../../../state/gridBots/GridBotsEffects";
import {
  getMarketOrdersWithin30days,
  getProfitsWithin30days,
} from "../../../utils/GridBotsUtils";
import "./GridBotDetails.css";
import GridBotDetailsCharts from "./GridBotDetailsCharts/GridBotDetailsCharts";
import GridBotDetailsTable from "./GridBotDetailsTable/GridBotDetailsTable";

const timeRangeRadios = [
  { name: "30 days", value: "30d" },
  { name: "All time", value: "allTime" },
];

const GridBotDetails = ({ modalIsOpen, setModalIsOpen, gridBotId }: any) => {
  const { gridBotsState, gridBotsDispatch } = useContext(GridBotsContext);
  const { errorsDispatch } = useContext(ErrorsContext);

  const { gridBots } = gridBotsState;

  const gridBot = gridBots[gridBotId];

  const [tradingFee, setTradingFee] = useState(0);

  const [timeRangeValue, setTimeRangeValue] = useState("30d");

  const [marketOrders, setMarketOrders] = useState([]);
  const [profits, setProfits] = useState();

  const [account, setAccount] = useState<any>(null);

  const navigate = useNavigate();

  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);

  const inputTradingFee = (e: any) => {
    if (e.target.value >= 0 && e.target.value <= 100) {
      setTradingFee(e.target.value);
    }
  };

  useEffect(() => {
    if (gridBot && gridBot.profits && !gridBot.marketOrders) {
      fetchDetails(
        gridBotsDispatch,
        gridBotsState,
        errorsDispatch,
        gridBotId
      ).then();
    }
    if (gridBot) {
      setAccount(accounts[gridBot.account_id]);
    }
  }, [gridBot]);

  useEffect(() => {
    if (gridBot && gridBot.profits && gridBot.marketOrders) {
      if (timeRangeValue === "30d") {
        setMarketOrders(
          getMarketOrdersWithin30days(gridBot.marketOrders).filter(
            (marketOrder: any) => marketOrder.status_string === "Filled"
          )
        );
        setProfits(getProfitsWithin30days(gridBot.profits));
      } else {
        setMarketOrders(
          gridBot.marketOrders.grid_lines_orders.filter(
            (marketOrder: any) => marketOrder.status_string === "Filled"
          )
        );
        setProfits(gridBot.profits.profits);
      }
    }
  }, [gridBot, timeRangeValue]);

  return (
    <>
      {gridBot && (
        <Modal
          show={modalIsOpen}
          onHide={() => {
            setModalIsOpen("");
          }}
          scrollable
          size="xl"
        >
          <Modal.Header closeButton>
            <Modal.Title>
              {`${gridBot.name}: ${gridBot.pair
                .split("_")
                .reverse()
                .join("/")}`}
              {account && (
                <div
                  style={{
                    display: "flex",
                    fontSize: "14px",
                    marginTop: "4px",
                  }}
                >
                  {"market_icon" in account && (
                    <div
                      className="BotMarketIcon"
                      style={{
                        backgroundImage: `url(${account.market_icon})`,
                      }}
                    ></div>
                  )}
                  <a
                    onClick={(e: any) => {
                      e.preventDefault();
                      navigate({
                        pathname: `/accounts/${account.id}`,
                        search: window.location.search,
                      });
                    }}
                    href="#"
                  >
                    {account.name}
                  </a>
                </div>
              )}
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <div className="GridBotsDetailsFilters">
              <div className="GridBotListControl">
                <div className="FilterTitle">Time range</div>
                <ButtonGroup>
                  {timeRangeRadios.map((radio, idx) => (
                    <ToggleButton
                      key={idx}
                      id={`detail-range-radio-${idx}`}
                      type="radio"
                      variant="outline-primary"
                      name="radio"
                      value={radio.value}
                      checked={timeRangeValue === radio.value}
                      onChange={(e) => setTimeRangeValue(e.currentTarget.value)}
                    >
                      {radio.name}
                    </ToggleButton>
                  ))}
                </ButtonGroup>
              </div>
              {/* <div
                className="GridBotListControl"
                style={{ width: "150px", marginLeft: "auto" }}
              >
                <div className="FilterTitle">Trading fee</div>
                <InputGroup>
                  <FormControl
                    onChange={inputTradingFee}
                    value={tradingFee}
                    type="number"
                  />
                  <InputGroup.Text>%</InputGroup.Text>
                </InputGroup>
              </div> */}
            </div>
            {profits && (
              <>
                <GridBotDetailsTable
                  gridBotId={gridBotId}
                  profits={profits}
                  marketOrders={marketOrders}
                  tradingFee={tradingFee}
                  timeRangeValue={timeRangeValue}
                />
                <GridBotDetailsCharts
                  gridBotId={gridBotId}
                  profits={profits}
                  marketOrders={marketOrders}
                  tradingFee={tradingFee}
                  timeRangeValue={timeRangeValue}
                />
              </>
            )}
          </Modal.Body>
        </Modal>
      )}
    </>
  );
};

export default GridBotDetails;
