import { IDeal } from "../../interfaces/Deal";

interface IGetDealsList {
  type: "getDealsList";
  payload: { deals: Array<IDeal>; botId: number; periodInDays: number };
}

export const getDealsList = (
  deals: Array<IDeal>,
  botId: number,
  periodInDays: number
): IGetDealsList => ({
  type: "getDealsList",
  payload: { deals, botId, periodInDays },
});

interface IGetActiveDealsList {
  type: "getActiveDealsList";
  payload: { activeDeals: Array<IDeal>; botId: number };
}
export const getActiveDealsList = (
  activeDeals: Array<IDeal>,
  botId: number
): IGetActiveDealsList => ({
  type: "getActiveDealsList",
  payload: { activeDeals, botId },
});

interface ISetOffset {
  type: "setOffset";
  payload: { botId: number; offset: number };
}

export const setOffset = (botId: number, offset: number): ISetOffset => ({
  type: "setOffset",
  payload: { botId, offset },
});

interface IResetDeals {
  type: "resetDeals";
  payload: { botId: number };
}

export const resetDeals = (botId: number): IResetDeals => ({
  type: "resetDeals",
  payload: { botId },
});
interface IResetActiveDeals {
  type: "resetActiveDeals";
  payload: { botId: number };
}

export const resetActiveDeals = (botId: number): IResetActiveDeals => ({
  type: "resetActiveDeals",
  payload: { botId },
});

export type DealsAction =
  | IGetDealsList
  | ISetOffset
  | IResetDeals
  | IGetActiveDealsList
  | IResetActiveDeals;
