import CryptoJS from "crypto-js";
import Bottleneck from "bottleneck";
import { IBotApi } from "../interfaces/Bot";
import { IDealApi } from "../interfaces/Deal";
import { IGridBotApi } from "../interfaces/GridBot";

const commonLimiter = new Bottleneck({
  reservoir: 13,
  reservoirRefreshAmount: 13,
  reservoirRefreshInterval: 1000,
});

export const DEAL_LIMITER_SETTINGS = {
  id: "DEAL_LIMITER",
  reservoir: 0,
  reservoirRefreshAmount: 1,
  reservoirRefreshInterval: 1000,
  minTime: 1000,
  maxConcurrent: 1,
  clearDatastore: true,
  timeout: 2000,
};

const API_KEY =
  new URLSearchParams(window.location.search).get("api_key") || "";
const SECRET = new URLSearchParams(window.location.search).get("secret") || "";

//we need that hack for stopping loading heavy deals request
export let dealsLimiter = new Bottleneck(DEAL_LIMITER_SETTINGS);

export function setDealsLimiter(limiterId: any) {
  dealsLimiter = new Bottleneck({ ...DEAL_LIMITER_SETTINGS, id: limiterId });
}

export function getDealsLimiter() {
  return dealsLimiter;
}

export function getSig(path: string, secret: string) {
  return CryptoJS.algo.HMAC.create(CryptoJS.algo.SHA256, secret)
    .update(path)
    .finalize()
    .toString(CryptoJS.enc.Hex);
}
export const callApi = (
  method: string,
  path: string,
  limiter: any = commonLimiter
) => {
  return limiter.schedule(() =>
    fetch(`https://api.3commas.io${path}`, {
      method: method,
      headers: {
        APIKEY: API_KEY,
        Signature: getSig(path, SECRET),
      },
    })
  );
};

export const getBots = async (
  offset: number,
  limit: number,
  sorting: string,
  scope: string,
  type: string,
  accountId: string,
  resetOffset = false
): Promise<Array<IBotApi>> => {
  const [sortBy, sortDirection] = sorting.split("-");

  const getBotsUrl = () =>
    `/public/api/ver1/bots?offset=${
      resetOffset ? 0 : offset
    }&limit=${limit}&sort_by=${sortBy}&sort_direction=${sortDirection}${
      scope ? `&scope=${scope}` : ""
    }${type ? `&strategy=${type}` : ""}${
      accountId ? `&account_id=${accountId}` : ""
    }`;

  return callApi("GET", getBotsUrl()).then((response: any) => response.json());
};

export const getGridBots = async (
  offset: number,
  limit: number,
  sorting: string,
  accountId: string,
  accountTypes: string[],
  state = "enabled"
): Promise<Array<IGridBotApi>> => {
  const [sortBy, sortDirection] = sorting.split("-");

  const accountTypesParams = accountTypes
    .map((accountType) => `&account_types[]=${accountType}`)
    .join("");

  const getBotsUrl = () =>
    `/public/api/ver1/grid_bots?offset=${offset}&limit=${limit}&sort_by=${sortBy}&sort_direction=${sortDirection}${
      accountId ? `&account_ids[]=${accountId}` : accountTypesParams
    }${state ? `&state=${state}` : ""}`;

  return callApi("GET", getBotsUrl()).then((response: any) => response.json());
};

export const getSmartTrades = async (
  page: number,
  perPage: number,
  sorting: string,
  accountId: string,
  type: string,
  pair: string,
  status: string,
  from: string
): Promise<Array<IGridBotApi>> => {
  const [sortBy, sortDirection] = sorting.split("-");

  const getSmartTradesUrl = () =>
    `/public/api/v2/smart_trades?page=${page}&per_page=${perPage}&order_by=${sortBy}&order_direction=${sortDirection}${
      accountId ? `&account_id=${accountId}` : ""
    }${type ? `&type=${type}` : ""}${
      pair ? `&pair=${pair}` : ""
    }&status=${status}${from ? `&from=${from}` : ""}`;

  return callApi("GET", getSmartTradesUrl()).then((response: any) =>
    response.json()
  );
};

