import moment from "moment";
import React, { FC, useContext, useEffect, useState } from "react";
import {
  ButtonGroup,
  FormControl,
  Table,
  ToggleButton,
  FormCheck,
  Button,
} from "react-bootstrap";
import { useNavigate, useParams } from "react-router";
import { AccountsContext, ErrorsContext } from "../../../App";
import { IAccount } from "../../../interfaces/Account";
import {
  fetchAccount,
  fetchAccountPieChartData,
  fetchAccountTableData,
} from "../../../state/accounts/AccountsEffects";
import { getCurrencyFormat } from "../../../utils/BotsUtils";
import { AccountPieChart } from "../AccountsList/AccountPreview/AccountPreview";
import "./AccountDetails.css";
import DatePicker from "react-datepicker";
import { getAccountBalanceChartData } from "../../../utils/ApiUtils";
import { Line } from "react-chartjs-2";
import { TimeLineChartOptions } from "../../../utils/ChartsUtils";
import _ from "lodash";

const chartTooltipFormatLabel =
  (quoteCurrency: string) => (tooltipItem: any) => {
    return `${tooltipItem.dataset.label}: ${getCurrencyFormat({
      value: tooltipItem.parsed.y,
      currency: quoteCurrency,
    })}`;
  };

const AccountDetails: FC<any> = () => {
  const { accountsState, accountsDispatch } = useContext(AccountsContext);

  const { accounts, accountPieChartData, accountTableData } = accountsState;

  const { errorsDispatch } = useContext(ErrorsContext);

  const [accountId, setAccountId] = useState<any>(null);
  const [account, setAccount] = useState<IAccount | null>(null);
  const [accountPieChart, setAccountPieChart] = useState<any>(null);
  const [accountTable, setAccountTable] = useState<any>(null);
  const [accountHistoryData, setAccountHistoryData] = useState<any>(null);
  const [accountMode, setAccountMode] = useState<any>("current");
  const [historyCurrency, setHistoryCurrency] = useState<any>("usd");
  const [historyDate, setHistoryDate] = useState<any>(
    moment().subtract(1, "months").toDate()
  );

  let params = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    if (params.accountId) {
      setAccountId(
        params.accountId === "summary"
          ? params.accountId
          : parseInt(params.accountId)
      );
    }
  }, [params]);

  useEffect(() => {
    if (accountId) {
      fetchAccountPieChartData(
        accountsDispatch,
        accountsState,
        errorsDispatch,
        accountId
      );
      fetchAccountTableData(
        accountsDispatch,
        accountsState,
        errorsDispatch,
        accountId
      );
      fetchAccount(accountsDispatch, accountsState, errorsDispatch, accountId);
    }
  }, [accountId]);

  useEffect(() => {
    if (accountPieChartData && accountId in accountPieChartData) {
      setAccountPieChart(accountPieChartData[accountId]);
    }
  }, [accountPieChartData, accountId]);

  useEffect(() => {
    if (accountTableData && accountId in accountTableData) {
      setAccountTable(accountTableData[accountId]);
    }
  }, [accountTableData, accountId]);

  useEffect(() => {
    if (accounts && accountId in accounts) {
      setAccount(accounts[accountId]);
    }
  }, [accounts, accountId]);

  useEffect(() => {
    if (accountId && historyDate) {
      getAccountBalanceChartData(
        accountId,
        moment(historyDate).format("DD-MM-YYYY")
      ).then((result: any) => {
        setAccountHistoryData(result);
      });
    }
  }, [historyDate, accountId]);

  return (
    <div className="AccountDetails">
      <div style={{ display: "flex" }}>
        <Button
          variant="primary"
          size="sm"
          className="AccountDetailsBackButton"
          onClick={() => {
            navigate(-1);
          }}
        >
          {`< Back`}
        </Button>
        <Button
          style={{ marginLeft: "16px" }}
          className="AccountDetailsBackButton"
          variant="primary"
          size="sm"
          onClick={() => {
            window.open(`https://3commas.io/accounts/${accountId}`, "_blank");
          }}
        >
          Open in 3commas
        </Button>
      </div>
      <div className="AccountDetailsMode">
        <div className="AccountDetailsControl">
          <ButtonGroup id="foo">
            <ToggleButton
              key="current"
              id={`radio-account-current`}
              type="radio"
              variant="outline-primary"
              name="radioAccountMode"
              value={"current"}
              checked={accountMode === "current"}
              onChange={(e) => {
                setAccountMode(e.currentTarget.value);
              }}
            >
              Current
            </ToggleButton>
            <ToggleButton
              key="history"
              id={`radio-account-history`}
              type="radio"
              variant="outline-primary"
              name="radioAccountMode"
              value={"history"}
              checked={accountMode === "history"}
              onChange={(e) => {
                setAccountMode(e.currentTarget.value);
              }}
            >
              History
            </ToggleButton>
          </ButtonGroup>
        </div>
        {accountMode === "history" && (
          <div className="AccountDetailsControl">
            <ButtonGroup id="foo">
              <ToggleButton
                key="usd"
                id={`radio-account-currency-usd`}
                type="radio"
                variant="outline-primary"
                name="radioAccountCurrencyMode"
                value={"usd"}
                checked={historyCurrency === "usd"}
                onChange={(e) => {
                  setHistoryCurrency(e.currentTarget.value);
                }}
              >
                USD
              </ToggleButton>
              <ToggleButton
                key="btc"
                id={`radio-account-currency-btc`}
                type="radio"
                variant="outline-primary"
                name="radioAccountCurrencyMode"
                value={"btc"}
                checked={historyCurrency === "btc"}
                onChange={(e) => {
                  setHistoryCurrency(e.currentTarget.value);
                }}
              >
                BTC
              </ToggleButton>
            </ButtonGroup>
          </div>
        )}
        {accountMode === "history" && (
          <div className="AccountDetailsNamedControl">
            <div className="AccountDetailsFilterTitle">From:</div>
            <DatePicker
              dateFormat="dd/MM/yyyy"
              selected={historyDate}
              onChange={(date) => {
                setHistoryDate(date as any);
              }}
              selectsStart
              className="AccountDetailsDateFilter"
              strictParsing
            />
          </div>
        )}
      </div>
      <div className="AccountDetailsOverview">
        {accountMode === "current" && (
          <>
            <div className="AccountDetailsOverviewPieChart">
              {account && accountPieChart && (
                <AccountPieChart accountPieChart={accountPieChart} />
              )}
            </div>
            {account && (
              <div className="AccountDetailsOverviewTable">
                <div className="AccountNameContainer">
                  {"market_icon" in account && (
                    <div
                      className="AccountDetailsAccountIcon"
                      style={{
                        backgroundImage: `url(${account.market_icon})`,
                      }}
                    ></div>
                  )}
                  <span className="AccountName">{account.name}</span>
                </div>
                <Table size="sm" style={{ fontSize: "12px" }}>
                  <thead>
                    <tr>
                      <th>Balance value</th>
                      <th>24 hr change</th>
                      <th>30 days change</th>
                      <th>30 days change, %</th>
                      <th>All time change</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{`${getCurrencyFormat({
                        value: parseFloat(account.usd_amount),
                        currency: "USD",
                      })} $`}</td>
                      <td
                        className={
                          parseFloat(account.day_profit_usd) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.day_profit_usd),
                        currency: "USD",
                      })} $`}</td>
                      <td
                        className={
                          parseFloat(account.usd_profit) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.usd_profit),
                        currency: "USD",
                      })} $`}</td>
                      <td
                        className={
                          parseFloat(account.usd_profit_percentage) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.usd_profit_percentage),
                        currency: "USD",
                      })} %`}</td>
                      <td
                        className={
                          parseFloat(account.total_usd_profit) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.total_usd_profit),
                        currency: "USD",
                      })} $`}</td>
                    </tr>
                    <tr>
                      <td>{`${getCurrencyFormat({
                        value: parseFloat(account.btc_amount),
                        currency: "BTC",
                      })} BTC`}</td>
                      <td
                        className={
                          parseFloat(account.day_profit_btc) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.day_profit_btc),
                        currency: "BTC",
                      })} BTC`}</td>
                      <td
                        className={
                          parseFloat(account.btc_profit) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.btc_profit),
                        currency: "BTC",
                      })} BTC`}</td>
                      <td
                        className={
                          parseFloat(account.btc_profit_percentage) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.btc_profit_percentage),
                        currency: "USD",
                      })} %`}</td>
                      <td
                        className={
                          parseFloat(account.total_btc_profit) >= 0
                            ? "showGreen"
                            : "showRed"
                        }
                      >{`${getCurrencyFormat({
                        value: parseFloat(account.total_btc_profit),
                        currency: "BTC",
                      })} BTC`}</td>
                    </tr>
                  </tbody>
                </Table>
              </div>
            )}
          </>
        )}
        {accountMode === "history" && accountHistoryData && (
          <div className="AccountDetailsOverviewBalances">
            {account && (
              <div className="AccountNameContainer">
                {"market_icon" in account && (
                  <div
                    className="AccountDetailsAccountIcon"
                    style={{
                      backgroundImage: `url(${account.market_icon})`,
                    }}
                  ></div>
                )}
                <span className="AccountName">{account.name}</span>
              </div>
            )}
            <div className="AccountDetailsOverviewBalancesChart">
              <Line
                options={
                  TimeLineChartOptions(
                    "",
                    chartTooltipFormatLabel(
                      historyCurrency === "usd" ? "USD" : "BTC"
                    )
                  ) as any
                }
                data={{
                  labels: accountHistoryData.map((historyData: any) =>
                    moment.unix(historyData.date).toDate()
                  ),
                  datasets: [
                    {
                      label: historyCurrency === "usd" ? "USD" : "BTC",
                      data: accountHistoryData.map((historyData: any) =>
                        historyCurrency === "usd"
                          ? historyData.usd
                          : historyData.btc
                      ),
                      borderColor: "rgb(53, 162, 235)",
                      backgroundColor: "rgba(53, 162, 235, 0.5)",
                    },
                  ],
                }}
              />
            </div>
          </div>
        )}
      </div>
      {accountTable && (
        <AccountBalancesTable
          accountTable={accountTable}
          accountId={accountId}
        />
      )}
    </div>
  );
};

const AccountBalancesTable: FC<any> = ({ accountTable, accountId }) => {
  const [accountFilteredTable, setAccountFilteredTable] = useState<any>(null);
  const [accountFilter, setAccountFilter] = useState<any>("");
  const [hideSmallBalances, setHideSmallBalances] = useState<any>(false);
  const [balancesCurrency, setBalancesCurrency] = useState<any>("usd");

  useEffect(() => {
    if (accountTable && !accountFilter && !hideSmallBalances) {
      setAccountFilteredTable(
        _.orderBy(accountTable, ["percentage"], ["desc"])
      );
    } else if (accountTable) {
      setAccountFilteredTable(
        _.orderBy(
          accountTable
            .filter(
              (tableData: any) =>
                tableData.currency_code &&
                tableData.currency_code.includes(accountFilter.toUpperCase())
            )
            .filter((tableData: any) =>
              hideSmallBalances
                ? tableData.usd_value > 10 && tableData.percentage > 1
                : true
            ),
          ["percentage"],
          ["desc"]
        )
      );
    }
  }, [accountTable, accountFilter, hideSmallBalances]);

  return (
    <div className="AccountDetailsTableContainer">
      <div className="AccountDetailsTableFilters">
        <FormControl
          onChange={(e: any) => {
            setAccountFilter(e.target.value);
          }}
          value={accountFilter}
          type="text"
          style={{ width: "200px", marginRight: "16px" }}
        />
        <FormCheck
          type="switch"
          onChange={(e: any) => {
            setHideSmallBalances(e.target.checked);
          }}
          value={hideSmallBalances}
          id={`default-f`}
          label={`Hide small balances`}
        />
        <ButtonGroup style={{ marginLeft: "auto" }}>
          <ToggleButton
            key="usd"
            id={`radio-account-balances-currency-usd`}
            type="radio"
            variant="outline-primary"
            name="radioAccountBalancesCurrencyMode"
            value={"usd"}
            checked={balancesCurrency === "usd"}
            onChange={(e) => {
              setBalancesCurrency(e.currentTarget.value);
            }}
          >
            USD
          </ToggleButton>
          <ToggleButton
            key="btc"
            id={`radio-account-balances-currency-btc`}
            type="radio"
            variant="outline-primary"
            name="radioAccountBalancesCurrencyMode"
            value={"btc"}
            checked={balancesCurrency === "btc"}
            onChange={(e) => {
              setBalancesCurrency(e.currentTarget.value);
            }}
          >
            BTC
          </ToggleButton>
        </ButtonGroup>
      </div>
      <div className="AccountDetailsTable">
        {accountFilteredTable && (
          <Table size="sm" style={{ fontSize: "12px" }}>
            <thead>
              <tr>
                <th>Token</th>
                <th>Share</th>
                <th>Price</th>
                <th>Price change, 24h</th>
                <th>Amount</th>
                {accountId !== "summary" && <th>On orders</th>}
                <th>Total</th>
              </tr>
            </thead>
            <tbody>
              {accountFilteredTable.map((row: any) => (
                <tr key={row.currency_code}>
                  <td>
                    {row.currency_name
                      ? `${row.currency_name} (${row.currency_code})`
                      : row.currency_code}
                  </td>
                  <td>{`${getCurrencyFormat({
                    value: row.percentage,
                    currency: "USD",
                  })} %`}</td>
                  <td>
                    {balancesCurrency === "usd"
                      ? `${getCurrencyFormat({
                          value: row.current_price_usd,
                          currency: "USD",
                        })} $`
                      : `${getCurrencyFormat({
                          value: row.current_price,
                          currency: "BTC",
                        })} BTC`}
                  </td>
                  <td
                    className={
                      parseFloat(
                        balancesCurrency === "usd"
                          ? row.day_change_percent_usd
                          : row.day_change_percent_btc
                      ) >= 0
                        ? "showGreen"
                        : "showRed"
                    }
                  >{`${getCurrencyFormat({
                    value:
                      balancesCurrency === "usd"
                        ? row.day_change_percent_usd
                        : row.day_change_percent_btc,
                    currency: "USD",
                  })} %`}</td>
                  <td>{`${getCurrencyFormat({
                    value: row.position,
                    currency: row.currency_code,
                  })}`}</td>
                  {accountId !== "summary" && (
                    <td>{`${getCurrencyFormat({
                      value: row.on_orders,
                      currency: row.currency_code,
                    })}`}</td>
                  )}
                  <td>
                    {balancesCurrency === "usd"
                      ? `${getCurrencyFormat({
                          value: row.usd_value,
                          currency: "USD",
                        })} $`
                      : `${getCurrencyFormat({
                          value: row.btc_value,
                          currency: "BTC",
                        })} BTC`}
                  </td>
                </tr>
              ))}
            </tbody>
          </Table>
        )}
      </div>
    </div>
  );
};

export default AccountDetails;
