import { DealsAction } from "./DealsActions";
import { IDeal } from "../../interfaces/Deal";
import _ from "lodash";

export interface IDealsState {
  deals: {
    [key: string]: { list: Array<IDeal>; period: number; offset?: number };
  };
  activeDeals: {
    [key: string]: { list: Array<IDeal> };
  };
}

export const initialDealsState: IDealsState = {
  deals: {},
  activeDeals: {},
};

export function dealsReducer(
  state: IDealsState = initialDealsState,
  action: DealsAction
): IDealsState {
  switch (action.type) {
    case "getDealsList":
      return {
        ...state,
        deals: {
          ...state.deals,
          [action.payload.botId]: {
            ...state.deals[action.payload.botId],
            list: action.payload.deals,
            period: action.payload.periodInDays,
          },
        },
      };
    case "getActiveDealsList":
      return {
        ...state,
        activeDeals: {
          ...state.activeDeals,
          [action.payload.botId]: {
            ...state.activeDeals[action.payload.botId],
            list: action.payload.activeDeals,
          },
        },
      };
    case "setOffset":
      return {
        ...state,
        deals: {
          ...state.deals,
          [action.payload.botId]: {
            ...state.deals[action.payload.botId],
            offset: action.payload.offset,
          },
        },
      };
    case "resetDeals":
      if (action.payload.botId in state.deals) {
        const newDeals = _.omit(state.deals, [action.payload.botId]);
        return {
          ...state,
          deals: newDeals,
        };
      } else {
        return { ...state };
      }

    case "resetActiveDeals":
      if (action.payload.botId in state.activeDeals) {
        const newDeals = _.omit(state.activeDeals, [action.payload.botId]);
        return {
          ...state,
          activeDeals: newDeals,
        };
      } else {
        return { ...state };
      }

    default:
      throw new Error();
  }
}
