import moment from "moment";
import React, { FC, useContext, useEffect, useState } from "react";
import { Button, Table } from "react-bootstrap";
import { Pie } from "react-chartjs-2";
import { useNavigate } from "react-router-dom";
import { AccountsContext, ErrorsContext } from "../../../../App";
import { IAccount } from "../../../../interfaces/Account";
import {
  fetchAccountActiveTradingEntities,
  fetchAccountPieChartData,
} from "../../../../state/accounts/AccountsEffects";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import "./AccountPreview.css";

var stringToColour = function (str: any) {
  var hash = 0;
  for (var i = 0; i < str.length; i++) {
    hash = str.charCodeAt(i) + ((hash << 5) - hash);
  }
  var colour = "#";
  for (var i = 0; i < 3; i++) {
    var value = (hash >> (i * 8)) & 0xff;
    colour += ("00" + value.toString(16)).substr(-2);
  }
  return colour;
};

const AccountPreview: FC<any> = ({ accountId }: any) => {
  const { accountsState, accountsDispatch } = useContext(AccountsContext);

  const { accounts, accountPieChartData, accountActiveTradingEntities } =
    accountsState;

  const { errorsDispatch } = useContext(ErrorsContext);

  const [account, setAccount] = useState<IAccount | null>(null);
  const [accountPieChart, setAccountPieChart] = useState<any>(null);
  const [accountActiveTrading, setAccountActiveTrading] = useState<any>(null);

  const navigate = useNavigate();

  useEffect(() => {
    fetchAccountPieChartData(
      accountsDispatch,
      accountsState,
      errorsDispatch,
      accountId
    );
    fetchAccountActiveTradingEntities(
      accountsDispatch,
      accountsState,
      errorsDispatch,
      accountId
    );
    setAccount(accounts[accountId]);
  }, [accountId]);

  useEffect(() => {
    if (accountId in accountPieChartData) {
      setAccountPieChart(accountPieChartData[accountId]);
    }
  }, [accountPieChartData]);

  useEffect(() => {
    if (accountId in accountActiveTradingEntities) {
      setAccountActiveTrading(accountActiveTradingEntities[accountId]);
    }
  }, [accountActiveTradingEntities]);

  return (
    <div className="AccountPreview">
      <div className="AccountPreviewColumn">
        {account && accountPieChart && (
          <AccountIcons account={account} accountPieChart={accountPieChart} />
        )}
        {account && <AccountInfo account={account} />}
      </div>
      <div
        className="AccountPreviewColumn"
        style={{ flexGrow: 1, marginLeft: "16px" }}
      >
        {account && (
          <>
            <div className="AccountNameContainer">
              <span className="AccountName">{account.name}</span>
            </div>

            <div className="AccountPreviewTable">
              <Table size="sm" style={{ fontSize: "12px" }}>
                <thead>
                  <tr>
                    <th>Balance value</th>
                    <th>24 hr change</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td style={{ fontWeight: "bold" }}>{`${getCurrencyFormat({
                      value: parseFloat(account.usd_amount),
                      currency: "USD",
                    })} $`}</td>
                    <td
                      className={
                        parseFloat(account.day_profit_usd) >= 0
                          ? "showGreen"
                          : "showRed"
                      }
                    >{`${getCurrencyFormat({
                      value: parseFloat(account.day_profit_usd),
                      currency: "USD",
                    })} $`}</td>
                  </tr>
                  <tr>
                    <td style={{ fontWeight: "bold" }}>{`${getCurrencyFormat({
                      value: parseFloat(account.btc_amount),
                      currency: "BTC",
                    })} BTC`}</td>
                    <td
                      className={
                        parseFloat(account.day_profit_btc) >= 0
                          ? "showGreen"
                          : "showRed"
                      }
                    >{`${getCurrencyFormat({
                      value: parseFloat(account.day_profit_btc),
                      currency: "BTC",
                    })} BTC`}</td>
                  </tr>
                </tbody>
              </Table>
            </div>
          </>
        )}
        {accountActiveTrading && accountActiveTrading.data && (
          <div className="AccountPreviewTable" style={{ marginTop: "16px" }}>
            <Table size="sm" style={{ fontSize: "12px" }}>
              <thead>
                <tr>
                  <th>DCA bots</th>
                  <th>Deals</th>
                  <th>Smart Trades</th>
                  <th>Grid Bots</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{accountActiveTrading.data.active_bots_count}</td>
                  <td>{accountActiveTrading.data.active_deals_count}</td>
                  <td>{accountActiveTrading.data.active_smart_trades_count}</td>
                  <td>{accountActiveTrading.data.active_grid_bots_count}</td>
                </tr>
              </tbody>
            </Table>
          </div>
        )}
        <div className="AccountPreviewButtonContainer">
          <Button
            className="AccountPreviewButton"
            onClick={() => {
              navigate({
                pathname: `/accounts/${accountId}`,
                search: window.location.search,
              });
            }}
          >
            View
          </Button>
        </div>
      </div>
    </div>
  );
};

const marketTypes = { spot: "S", margin: "M", futures: "F" } as any;

export const AccountIcons = ({ account, accountPieChart }: any) => (
  <div className="AccountPieChart">
    <div className="AccountTypeIconContainer">
      {marketTypes[account.supported_market_types[0]]}
    </div>
    {"market_icon" in account && (
      <div className="AccountIconContainer">
        <div
          className="AccountIcon"
          style={{
            backgroundImage: `url(${account.market_icon})`,
          }}
        ></div>
      </div>
    )}
    {account && accountPieChart.length > 0 ? (
      <div className="AccountPieChartContainer">
        <AccountPieChart accountPieChart={accountPieChart} />
      </div>
    ) : (
      <div className="AccountPieChartContainerEmpty"></div>
    )}
  </div>
);

export const AccountPieChart = ({ accountPieChart }: any) => (
  <Pie
    data={{
      labels: accountPieChart.map((dataItem: any) => dataItem.code),
      datasets: [
        {
          data: accountPieChart.map((dataItem: any) => dataItem.y),
          backgroundColor: accountPieChart.map((dataItem: any) =>
            dataItem.code.includes("USD") && dataItem.code.length < 6
              ? "#00a59a"
              : stringToColour(dataItem.code)
          ),
          borderWidth: 0,
        },
      ],
    }}
    options={
      {
        plugins: {
          legend: {
            display: false,
          },
          tooltip: {
            callbacks: {
              label: (tooltipItem: any) => {
                const dataset = tooltipItem.dataset;
                const currentValue = dataset.data[tooltipItem.dataIndex];
                return `${tooltipItem.label} ${currentValue}%`;
              },
            },
            bodyFont: { size: 10 },
          },
        },
      } as any
    }
  />
);

export const AccountInfo = ({ account }: any) => (
  <div className="AccountInfoContainer">
    <div className="AccountInfoRecordContainer">
      <div className="AccountInfoLabel">Created:</div>
      <div className="AccountInfoValue">{`${moment(account.created_at).format(
        "DD/MM/YYYY"
      )}`}</div>
    </div>
    <div className="AccountInfoRecordContainer">
      <div className="AccountInfoLabel">Updated:</div>
      <div className="AccountInfoValue">{`${moment(account.updated_at).format(
        "DD/MM/YYYY"
      )}`}</div>
    </div>
    <div className="AccountInfoRecordContainer">
      <div className="AccountInfoLabel">API status:</div>
      <div className="AccountInfoValue">{`${
        account.api_key_invalid ? "Invalid" : "OK"
      }`}</div>
    </div>
  </div>
);

export default AccountPreview;
