import _ from "lodash";
import React, { useContext, useEffect, useState } from "react";
import { Bar, Line } from "react-chartjs-2";
import { GridBotsContext } from "../../../../App";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import {
  AccumulativeChartOptions,
  ProfitChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  getOneGridBotAccumulativeProfitsChartData,
  getOneGridBotProfitsChartData,
} from "../../../../utils/GridBotsUtils";
import "./GridBotDetailsCharts.css";

const GridBotDetailsCharts = ({
  gridBotId,
  profits,
  marketOrders,
  tradingFee,
  timeRangeValue,
}: any) => {
  const { gridBotsState } = useContext(GridBotsContext);

  const { gridBots } = gridBotsState;

  const gridBot = gridBots[gridBotId];

  const quoteCurrency = gridBot.pair.split("_")[0];

  const [accumulativeProfitsData, setAccumulativeProfitsData] = useState<any>([
    [],
    [],
  ]);
  const [profitsData, setProfitsData] = useState<any>([[], []]);

  const chartTooltipFormatLabel = (tooltipItem: any) => {
    return `${tooltipItem.dataset.label}: ${getCurrencyFormat({
      value: tooltipItem.parsed.y,
      currency: quoteCurrency,
    })}`;
  };

  useEffect(() => {
    setAccumulativeProfitsData(
      getOneGridBotAccumulativeProfitsChartData(
        profits,
        gridBot,
        timeRangeValue,
        marketOrders,
        tradingFee
      )
    );
    setProfitsData(
      getOneGridBotProfitsChartData(
        profits,
        gridBot,
        timeRangeValue,
        marketOrders,
        tradingFee
      )
    );
  }, [profits, marketOrders, tradingFee]);

  return (
    <div className="GridBotDetailsCharts">
      <div className="DCABotDetailsChartContainer">
        <Line
          options={
            AccumulativeChartOptions(
              `Accumulative Profit, ${quoteCurrency}`,
              chartTooltipFormatLabel
            ) as any
          }
          data={{
            labels: accumulativeProfitsData[0],
            datasets: [
              {
                label: `Accumulative Profit, ${quoteCurrency}`,
                data: accumulativeProfitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
                fill: { value: 0 },
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            ProfitChartOptions(
              `Daily Profit, ${quoteCurrency}`,
              chartTooltipFormatLabel
            ) as any
          }
          data={{
            labels: profitsData[0],
            datasets: [
              {
                label: `Profit, ${quoteCurrency}`,
                data: profitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
    </div>
  );
};

export default GridBotDetailsCharts;
