import _ from "lodash";
import {
  getGridBot,
  getGridBots,
  getGridBotsMarketOrders,
  getGridBotsProfits,
} from "../../utils/ApiUtils";
import { getCurrencyValueInUsd } from "../../utils/GeneralUtils";
import {
  getCurrentInvestment,
  getInitialInvestment,
} from "../../utils/GridBotsUtils";
import {
  addToGridBotsList,
  setHasMore,
  setNewGridBotsList,
  setGridBot,
} from "./GridBotsActions";
import { IGridBotsState } from "./GridBotsReducer";

const spotAccountsTypes = [
  "Account::BinanceAccount",
  "Accounts::Ftx",
  "Account::HuobiAccount",
  "Account::OkexAccount",
  "Account::KucoinAccount",
  "Account::GateIoAccount",
  "Account::KrakenAccount",
  "Account::BitfinexAccount",
  "Account::BinanceUsAccount",
  "Account::BitstampAccount",
  "Accounts::GeminiAccount",
  "Account::BittrexAccount",
  "Account::GdaxAccount",
  "Account::PaperTradingAccount",
];

export async function fetchGridBots(
  gridBotsDispatch: any,
  gridBotsState: IGridBotsState,
  errorsDispatch: any,
  customLimit?: number
) {
  gridBotsDispatch(setHasMore(true));
  gridBotsDispatch(setNewGridBotsList([]));
  const {
    limit,
    filters: { sorting, account },
  } = gridBotsState;

  const offsetLimit = customLimit ? customLimit : limit;
  try {
    let offset = 0;
    let hasMore = true;

    while (hasMore) {
      const data: any = await getGridBots(
        offset,
        offsetLimit,
        sorting,
        account,
        spotAccountsTypes
      );
      if (data.error) {
        break;
      }
      gridBotsDispatch(addToGridBotsList(data));
      offset = offset + offsetLimit;
      if (data.length < offsetLimit) {
        hasMore = false;
      }
    }
    gridBotsDispatch(setHasMore(false));
  } catch {
    gridBotsDispatch(setNewGridBotsList([]));
    gridBotsDispatch(setHasMore(false));
    errorsDispatch({
      type: "addError",
      payload: "Error when fetching grid bots list",
    });
  }
}

export async function fetchGridBot(
  gridBotsDispatch: any,
  gridBotsState: IGridBotsState,
  errorsDispatch: any,
  gridBotId: number
) {
  try {
    const gridBot = await getGridBot(gridBotId);
    gridBotsDispatch(setGridBot(gridBotId, gridBot));
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching grid bot ${gridBotId}`,
    });
  }
}

export async function fetchInitialInvestments(
  gridBotsState: IGridBotsState,
  errorsDispatch: any,
  gridBotId: number
) {
  const { gridBots } = gridBotsState;
  const gridBotItem = gridBots[gridBotId];
  try {
    if (
      !("initialInvestments" in gridBotItem) ||
      !gridBotItem.initialInvestments
    ) {
      const initialInvestments = await getInitialInvestment(gridBotItem);
      return initialInvestments;
    } else {
      return gridBotItem.initialInvestments;
    }
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching initial investments for grid bot ${gridBotItem.name}`,
    });
    return {};
  }
}

