export async function fetchProfits(
  gridBotsState: IGridBotsState,
  errorsDispatch: any,
  gridBotId: number
) {
  const { gridBots } = gridBotsState;
  const gridBotItem = gridBots[gridBotId];
  const quoteCurrency = gridBotItem.pair.split("_")[0];
  const baseCurrency = gridBotItem.pair.split("_")[1];
  const currency =
    gridBotItem.profit_currency_type === "quote" ? quoteCurrency : baseCurrency;
  try {
    if (!("profits" in gridBotItem) || !gridBotItem.profits) {
      const profits = await getGridBotsProfits(gridBotId);
      const sumProfits = _.sum(
        profits.map((profit: any) => parseFloat(profit.profit))
      );
      const sumProfitsUsd = await getCurrencyValueInUsd(
        "binance",
        currency,
        sumProfits
      );
      return { profits, sumProfitsUsd };
    } else {
      return gridBotItem.profits;
    }
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching profits for grid bot ${gridBotItem.name}`,
    });
    return {};
  }
}

