import {
  getAccount,
  getAccountActiveTradingEntities,
  getAccountPieChartData,
  getAccounts,
  getAccountTableData,
} from "../../utils/ApiUtils";

import {
  setNewAccountsList,
  setAccountTableData,
  setAccountPieChartData,
  setAccountActiveTradingEntities,
  setHasMore,
  addToAccountsList,
  setAccount,
} from "./AccountsActions";
import { IAccountsState } from "./AccountsReducer";

export async function fetchAccounts(
  accountsDispatch: any,
  accountsState: IAccountsState,
  errorsDispatch: any,
  customLimit?: number
) {
  accountsDispatch(setHasMore(true));
  accountsDispatch(setNewAccountsList([]));
  const { limit } = accountsState;

  const perPage = customLimit ? customLimit : limit;
  try {
    let hasMore = true;
    let page = 1;

    while (hasMore) {
      const data: any = await getAccounts(page, perPage);
      if (data.error) {
        break;
      }
      accountsDispatch(addToAccountsList(data));
      page = page + 1;
      if (data.length < perPage) {
        hasMore = false;
      }
    }
    accountsDispatch(setHasMore(false));
  } catch {
    accountsDispatch(setNewAccountsList([]));
    accountsDispatch(setHasMore(false));
    errorsDispatch({
      type: "addError",
      payload: "Error when fetching accounts list",
    });
  }
}

export async function fetchAccount(
  accountsDispatch: any,
  accountsState: IAccountsState,
  errorsDispatch: any,
  accountId: number | string
) {
  try {
    const account = await getAccount(accountId);
    accountsDispatch(setAccount(accountId, account));
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching account ${accountId}`,
    });
  }
}

export async function fetchAccountTableData(
  accountsDispatch: any,
  accountsState: IAccountsState,
  errorsDispatch: any,
  accountId: number | string
) {
  const { accountTableData } = accountsState;
  try {
    if (!(accountId in accountTableData)) {
      const accountTableData = await getAccountTableData(accountId);
      accountsDispatch(setAccountTableData(accountId, accountTableData));
    }
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching account table data`,
    });
  }
}
