export async function fetchAccountActiveTradingEntities(
  accountsDispatch: any,
  accountsState: IAccountsState,
  errorsDispatch: any,
  accountId: number | string
) {
  const { accountActiveTradingEntities } = accountsState;
  try {
    if (!(accountId in accountActiveTradingEntities)) {
      const accountActiveTradingEntities =
        await getAccountActiveTradingEntities(accountId);
      accountsDispatch(
        setAccountActiveTradingEntities(accountId, accountActiveTradingEntities)
      );
    }
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching account active trading entities data`,
    });
  }
}
