import { ISmartTrade, ISmartTradeFilters } from "../../interfaces/SmartTrade";

interface ISetNewSmartTradesList {
  type: "setNewSmartTradesList";
  payload: Array<ISmartTrade>;
}

export const setNewSmartTradesList = (
  smartTradesList: Array<ISmartTrade>
): ISetNewSmartTradesList => ({
  type: "setNewSmartTradesList",
  payload: smartTradesList,
});

interface ISetHasMore {
  type: "setHasMore";
  payload: boolean;
}

export const setHasMore = (hasMore: boolean): ISetHasMore => ({
  type: "setHasMore",
  payload: hasMore,
});

interface IAddToSmartTradesList {
  type: "addToSmartTradesList";
  payload: Array<ISmartTrade>;
}

export const addToSmartTradesList = (
  smartTradesList: Array<ISmartTrade>
): IAddToSmartTradesList => ({
  type: "addToSmartTradesList",
  payload: smartTradesList,
});

interface ISetSmartTradesOffset {
  type: "setSmartTradesOffset";
  payload: number;
}

export const setSmartTradesOffset = (
  offset: number
): ISetSmartTradesOffset => ({
  type: "setSmartTradesOffset",
  payload: offset,
});

interface ISetSmartTradesFilters {
  type: "setSmartTradesFilters";
  payload: ISmartTradeFilters;
}

export const setSmartTradesFilters = (
  smartTradesFilter: ISmartTradeFilters
): ISetSmartTradesFilters => ({
  type: "setSmartTradesFilters",
  payload: smartTradesFilter,
});

interface ISetSmartTrade {
  type: "setSmartTrade";
  payload: { smartTradeId: number; smartTrade: ISmartTrade };
}

export const setSmartTrade = (
  smartTradeId: number,
  smartTrade: ISmartTrade
): ISetSmartTrade => ({
  type: "setSmartTrade",
  payload: { smartTradeId, smartTrade },
});

export type SmartTradesAction =
  | ISetNewSmartTradesList
  | ISetHasMore
  | IAddToSmartTradesList
  | ISetSmartTradesOffset
  | ISetSmartTradesFilters
  | ISetSmartTrade;
