import React, { useContext, useEffect, useRef, useState } from "react";
import { BotPreview } from "./BotPreview/BotPreview";
import InfiniteScroll from "react-infinite-scroll-component";
import "./BotsList.css";
import {
  AccountsContext,
  ErrorsContext,
  BotsContext,
  DealsContext,
} from "../../../App";
import { Button, Spinner } from "react-bootstrap";
import { fetchBots } from "../../../state/bots/BotsEffects";
import BotsListCharts from "./BotsListCharts/BotsListCharts";
import { usePrevious } from "../../../utils/GeneralUtils";

const haveAllBotsDealsFor30Days = (botsList: any, deals: any) =>
  botsList.every((botId: any) => botId in deals);

export const BotsList = () => {
  const { botsState, botsDispatch } = useContext(BotsContext);
  const {
    dealsState: { deals },
  } = useContext(DealsContext);
  const { list, hasMore, filters, bots } = botsState;
  const {
    accountsState: { accounts },
  } = useContext(AccountsContext);
  const { errorsDispatch } = useContext(ErrorsContext);

  const [showCharts, setShowCharts] = useState(false);

  const isNothingFound = !hasMore && list.length === 0;

  const previousFilters = usePrevious(filters);

  useEffect(() => {
    if (!previousFilters && list.length) {
      setShowCharts(haveAllBotsDealsFor30Days(list, deals));
    } else {
      setShowCharts(false);
      fetchBots(botsDispatch, botsState, errorsDispatch, true).then();
    }
  }, [filters]);

  return isNothingFound ? (
    <div className="FallbackContainer">
      <p style={{ textAlign: "center" }}>
        <b>No results found =(</b>
      </p>
    </div>
  ) : (
    <InfiniteScroll
      dataLength={list.length}
      next={async () => {
        if (list.length) {
          await fetchBots(botsDispatch, botsState, errorsDispatch, false);
        }
      }}
      hasMore={hasMore}
      loader={
        <div className="SpinnerContainer">
          <Spinner animation="grow" />
        </div>
      }
      endMessage={
        <p style={{ textAlign: "center" }}>
          <b>Yay! You have seen it all</b>
        </p>
      }
    >
      {showCharts ? (
        <BotsListCharts />
      ) : (
        <div className="ShowChartsButtonContainer">
          <Button
            className="ShowChartsButton"
            onClick={() => {
              setShowCharts(true);
            }}
          >
            Calculate profits
          </Button>
        </div>
      )}
      <div className="BotsList">
        {list.map((botId: number) => (
          <BotPreview
            botItem={bots[botId]}
            key={botId}
            account={accounts[bots[botId].account_id]}
          />
        ))}
      </div>
      {list.length === 0 && (
        <div className="SpinnerContainer">
          <Spinner animation="grow" />
        </div>
      )}
    </InfiniteScroll>
  );
};
