import _ from "lodash";
import { IAccount } from "../../interfaces/Account";
import { AccountsAction } from "./AccountsActions";

export interface IAccountsState {
  list: Array<number>;
  accounts: { [key: number]: IAccount };
  accountTableData: { [key: number]: IAccount };
  accountPieChartData: { [key: number]: IAccount };
  accountActiveTradingEntities: { [key: number]: IAccount };
  hasMore: boolean;
  offset: number;
  limit: number;
}

export const initialAccountsState: any = {
  offset: 0,
  hasMore: false,
  list: [],
  limit: 100,
  accounts: {},
  accountTableData: {},
  accountPieChartData: {},
  accountActiveTradingEntities: {},
};

export function accountsReducer(
  state: IAccountsState,
  action: AccountsAction
): IAccountsState {
  switch (action.type) {
    case "setNewAccountsList":
      const newList = action.payload.map((account: any) => account.id);
      const newAccounts = _.keyBy(action.payload, "id");
      return { ...state, list: newList, accounts: newAccounts };
    case "setHasMore":
      return { ...state, hasMore: action.payload };
    case "addToAccountsList":
      const list = action.payload.map((account) => account.id);
      const accounts = _.keyBy(action.payload, "id");
      return {
        ...state,
        list: [...state.list, ...list],
        accounts: { ...state.accounts, ...accounts },
      };
    case "setAccountsOffset":
      return { ...state, offset: action.payload };
    case "setAccount":
      return {
        ...state,
        accounts: {
          ...state.accounts,
          [action.payload.accountId]: action.payload.account,
        },
      };
    case "setAccountTableData":
      return {
        ...state,
        accountTableData: {
          ...state.accountTableData,
          [action.payload.accountId]: action.payload.accountTableData,
        },
      };
    case "setAccountPieChartData":
      return {
        ...state,
        accountPieChartData: {
          ...state.accountPieChartData,
          [action.payload.accountId]: action.payload.accountPieChartData,
        },
      };
    case "setAccountActiveTradingEntities":
      return {
        ...state,
        accountActiveTradingEntities: {
          ...state.accountActiveTradingEntities,
          [action.payload.accountId]:
            action.payload.accountActiveTradingEntities,
        },
      };
    default:
      throw new Error();
  }
}
