import React, { useEffect, useState } from "react";
import { Alert } from "react-bootstrap";
import { Nav } from "react-bootstrap";
import { useNavigate, useLocation } from "react-router-dom";
import "./NavBar.css";

export const NavBar = () => {
  const [activeKey, setActiveKey] = useState("DCA");

  const navigate = useNavigate();
  const location = useLocation();

  const goToGridbots = () => {
    navigate({
      pathname: `/gridbots`,
      search: window.location.search,
    });
  };
  const goToDCABots = () => {
    navigate({
      pathname: `/`,
      search: window.location.search,
    });
  };
  const goToSmartTrades = () => {
    navigate({
      pathname: `/smarttrades`,
      search: window.location.search,
    });
  };

  const goToSmartAccounts = () => {
    navigate({
      pathname: `/accounts`,
      search: window.location.search,
    });
  };

  useEffect(() => {
    if (location.pathname === "/") {
      setActiveKey("DCA");
    } else if (location.pathname === "/gridbots") {
      setActiveKey("grid");
    } else if (location.pathname === "/smarttrades") {
      setActiveKey("smarttrades");
    } else if (location.pathname === "/accounts") {
      setActiveKey("accounts");
    }
  }, [location]);

  return (
    <div className="NavBar">
      <div className="NavBarContainer">
        <div className="LogoContainer">
          <img className="Logo" src={`/BotAn__icon.png`} alt="logo" />
        </div>
        <div className="Navigation">
          <Nav>
            <Nav.Item>
              <Nav.Link active={activeKey === "DCA"} onClick={goToDCABots}>
                DCA Bots
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link active={activeKey === "grid"} onClick={goToGridbots}>
                Grid Bots
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                active={activeKey === "smarttrades"}
                onClick={goToSmartTrades}
              >
                Smart Trades
              </Nav.Link>
            </Nav.Item>
            <Nav.Item>
              <Nav.Link
                active={activeKey === "accounts"}
                onClick={goToSmartAccounts}
              >
                Accounts
              </Nav.Link>
            </Nav.Item>
          </Nav>
        </div>
        <div
          className="CommunityLink"
          onClick={() => {
            window.open("https://t.me/BotAnMainChat", "_blank");
          }}
        >
          <span className="SupportLabel">Community</span>
          <img className="TelegramIcon" src={`image 8.png`} alt="logo" />
        </div>
      </div>
      <Alert key="warning" variant="warning">
        This is the FREE version of the BotAn App - the best bot analysis tool
        for 3Commas. Free version is limited to show only one bot/trade. Please
        subscribe to the BotAn PRO to get all benefits here:{" "}
        <Alert.Link href="https://3commas.io/d/apps/119">
          https://3commas.io/d/apps/119
        </Alert.Link>
        .
      </Alert>
    </div>
  );
};
