import moment from "moment";
import React, { useEffect, useRef } from "react";
import { callApi } from "./ApiUtils";

export const usePrevious = (value: any) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};

export const convertFilterValueToDate = (filterFrom: any) => {
  const dateFormat = "YYYY-MM-DD";

  const filterValue = filterFrom.label;

  if (filterValue === "today") {
    return moment().subtract(1, "day").format(dateFormat);
  } else if (filterValue === "week") {
    return moment().subtract(1, "week").format(dateFormat);
  } else if (filterValue === "month") {
    return moment().subtract(1, "months").format(dateFormat);
  } else if (filterValue === "custom") {
    return filterFrom.from
      ? moment(filterFrom.from).format(dateFormat)
      : moment().format(dateFormat);
  }
  return "";
};

export const getCurrencyValueInUsd = async (
  market_code: any,
  currency: any,
  valueInCurrency: any
) => {
  if (currency.includes("USD")) {
    return Promise.resolve(valueInCurrency);
  } else {
    const responseUsdt = await callApi(
      "GET",
      `/public/api/ver1/accounts/currency_rates?market_code=${market_code}&pair=USDT_${currency}`
    )
      .then((response: any) => response.json())
      .then((marketInfo: any) => parseFloat(marketInfo.last));

    if (responseUsdt) {
      return responseUsdt * valueInCurrency;
    }

    const responseUsd = await callApi(
      "GET",
      `/public/api/ver1/accounts/currency_rates?market_code=${market_code}&pair=USD_${currency}`
    )
      .then((response: any) => response.json())
      .then((marketInfo: any) => parseFloat(marketInfo.last));

    if (responseUsd) {
      return responseUsd * valueInCurrency;
    }

    const responseUsdBack = await callApi(
      "GET",
      `/public/api/ver1/accounts/currency_rates?market_code=${market_code}&pair=${currency}_USD`
    )
      .then((response: any) => response.json())
      .then((marketInfo: any) => parseFloat(marketInfo.last));
    if (responseUsdBack) {
      return valueInCurrency / responseUsdBack;
    }

    const responseUsdtBack = await callApi(
      "GET",
      `/public/api/ver1/accounts/currency_rates?market_code=${market_code}&pair=${currency}_USDT`
    )
      .then((response: any) => response.json())
      .then((marketInfo: any) => parseFloat(marketInfo.last));
    if (responseUsdtBack) {
      return valueInCurrency / responseUsdtBack;
    }

    return NaN;
  }
};
