import React from "react";
import { Tab, Tabs } from "react-bootstrap";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { BotInfo, IconsSet } from "../../BotsList/BotPreview/BotPreview";
import { DCABotDetailsMainInfo } from "../DCABotDetailsMain/DCABotDetailsMainInfo";
import { DCABotDetailsProfitsInfo } from "../DCABotDetailsProfits/DCABotDetailsProfitsInfo";
import { DCABotDetailsSettingsInfo } from "../DCABotDetailsSettings/DCABotDetailsSettingsInfo";
import moment from "moment";
import "./DCABotDetailsInfo.css";
import { differenceInDays } from "../../../../utils/DealsUtils";
import DCABotDetailsSafetyInfo from "../DCABotDetailsSafety/DCABotDetailsSafetyInfo";

export const DCABotDetailsInfo = ({
  bot,
  deals,
  account,
  infoKey,
  setInfoKey,
  fromFilter,
  toFilter,
}: {
  bot: IBot;
  deals: IDeal[];
  account: any;
  infoKey: string;
  setInfoKey: any;
  fromFilter: any;
  toFilter: any;
}) => {
  const age = differenceInDays(moment(), moment(bot.created_at));
  return (
    <div className="DCABotDetailsInfo">
      <div className="DCABotDetailsInfoLogo">
        <IconsSet bot={bot}></IconsSet>
        <BotInfo
          strategy={bot.strategy}
          age={age}
          is_enabled={bot.is_enabled}
        />
      </div>
      <div className="DCABotDetailsInfoTable">
        <Tabs activeKey={infoKey} onSelect={(k) => setInfoKey(k)}>
          <Tab eventKey="main" title="Main" tabClassName="InfoTab">
            <DCABotDetailsMainInfo account={account} bot={bot} deals={deals} />
          </Tab>
          <Tab eventKey="settings" title="Settings" tabClassName="InfoTab">
            <DCABotDetailsSettingsInfo account={account} bot={bot} />
          </Tab>
          <Tab eventKey="profits" title="Profits" tabClassName="InfoTab">
            <DCABotDetailsProfitsInfo
              account={account}
              bot={bot}
              deals={deals}
              fromFilter={fromFilter}
              toFilter={toFilter}
            />
          </Tab>
          <Tab eventKey="safeties" title="Safeties" tabClassName="InfoTab">
            <DCABotDetailsSafetyInfo
              account={account}
              bot={bot}
              deals={deals}
            />
          </Tab>
        </Tabs>
      </div>
    </div>
  );
};
