import React, { useContext, useEffect } from "react";
import { AccountsContext, SmartTradesContext } from "../../../../App";
import Select from "react-select";
import "./SmartTradesFilters.css";
import DatePicker from "react-datepicker";
import { setSmartTradesFilters } from "../../../../state/smartTrades/SmartTradesActions";
import { ButtonGroup, ToggleButton } from "react-bootstrap";
import moment from "moment";

const timeRangeRadios = [
  { name: "Today", value: "today" },
  { name: "Week", value: "week" },
  { name: "Month", value: "month" },
  { name: "All time", value: "allTime" },
  { name: "Custom", value: "custom" },
];

const SmartTradesFilters = () => {
  const { smartTradesState, smartTradesDispatch } =
    useContext(SmartTradesContext);
  const {
    accountsState: { accounts, list: accountsList },
  } = useContext(AccountsContext);

  const { filters, hasMore, list } = smartTradesState;

  const updateFilter = async (filterItem: any) => {
    smartTradesDispatch(
      setSmartTradesFilters({
        ...smartTradesState.filters,
        ...filterItem,
      })
    );
  };

  const accountsOptions = [
    { value: "", label: "All", label_id: "All" },
    ...accountsList.map((accountId: any) => ({
      value: accountId,
      label: (
        <div className="AccountFilterItem">
          <div>
            <div
              className="AccountMarketIcon"
              style={{
                backgroundImage: `url(${accounts[accountId].market_icon})`,
              }}
            ></div>
          </div>
          <span>{accounts[accountId].name}</span>
        </div>
      ),
      label_id: accounts[accountId].name,
    })),
  ];

  const typeOptions: any = [
    { value: "", label: "All" },
    { value: "simple_buy", label: "Simple buy" },
    { value: "simple_sell", label: "Simple sell" },
    { value: "smart_sell", label: "Smart sell" },
    { value: "smart_cover", label: "Smart cover" },
    { value: "smart_buy", label: "Smart buy" },
    { value: "smart_trade", label: "Smart trade" },
  ];

  const statusOptions: any = [
    { value: "all", label: "All" },
    { value: "active", label: "Active" },
    { value: "finished", label: "Finished" },
    { value: "successfully_finished", label: "Successfully finished" },
    { value: "cancelled", label: "Cancelled" },
    { value: "failed", label: "Failed" },
  ];

  const sortingOptions: any = [
    { value: "profit-desc", label: "Profit (Highest First)" },
    { value: "profit-asc", label: "Profit (Lowest First)" },
    {
      value: "profit_percentage-desc",
      label: "Profit percentage (Highest First)",
    },
    {
      value: "profit_percentage-asc",
      label: "Profit percentage (Lowest First)",
    },
    { value: "created_at-desc", label: "Opened (Youngest first)" },
    { value: "created_at-asc", label: "Opened (Oldest first)" },
    { value: "closed_at-desc", label: "Closed (Youngest first)" },
    { value: "closed_at-asc", label: "Closed (Oldest first)" },
    { value: "status-desc", label: "Status (Highest First)" },
    { value: "status-asc", label: "Status (Lowest First)" },
  ];

  useEffect(() => {
    try {
      localStorage.setItem("smartTradesFiltersState", JSON.stringify(filters));
    } catch {}
  }, [filters]);

  return (
    <>
      <div className="SmartTradesFilters">
        <div className="SmartTradesTimeRangeContainer">
          <ButtonGroup>
            {timeRangeRadios.map((radio, idx) => (
              <ToggleButton
                key={idx}
                id={`radio-${idx}`}
                type="radio"
                disabled={hasMore}
                variant="outline-primary"
                name="radio"
                value={radio.value}
                checked={filters.from.label === radio.value}
                onChange={(e) => {
                  updateFilter({
                    from: { ...filters.from, label: e.currentTarget.value },
                  });
                }}
              >
                {radio.name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </div>
        {filters.from.label === "custom" && (
          <div className="SmartTradesTimeRangeContainer">
            <div className="FilterTitle">From</div>
            <DatePicker
              dateFormat="yyyy-MM-dd"
              disabled={hasMore}
              selected={
                filters.from.from
                  ? moment(filters.from.from).toDate()
                  : moment().toDate()
              }
              onChange={(date: any) => {
                updateFilter({
                  from: {
                    label: "custom",
                    from: moment(date).format("YYYY-MM-DD"),
                  },
                });
              }}
              selectsStart
              className="SmartTradesDateFilter"
              strictParsing
            />
          </div>
        )}
        <div className="SmartTradesFilterGroup">
          <div className="FilterTitle">Account</div>
          <Select
            isDisabled={hasMore}
            onChange={(option: any) => {
              updateFilter({ accountId: option.value });
            }}
            value={accountsOptions.find(
              (option: any) => option.value === filters.accountId
            )}
            options={accountsOptions}
            className="AccountFilter"
            filterOption={(candidate, input) =>
              candidate.data.label_id.includes(input)
            }
          />
        </div>
        <div className="SmartTradesFilterGroup">
          <div className="FilterTitle">Status</div>
          <Select
            isDisabled={hasMore}
            onChange={(option: any) => {
              updateFilter({ status: option.value });
            }}
            value={statusOptions.find(
              (option: any) => option.value === filters.status
            )}
            options={statusOptions}
            className="TypeFilter"
          />
        </div>
        <div className="SmartTradesFilterGroup">
          <div className="FilterTitle">Type</div>
          <Select
            isDisabled={hasMore}
            onChange={(option: any) => {
              updateFilter({ type: option.value });
            }}
            value={typeOptions.find(
              (option: any) => option.value === filters.type
            )}
            options={typeOptions}
            className="TypeFilter"
          />
        </div>
        <div className="SmartTradesFilterGroup">
          <div className="FilterTitle">Sort by</div>
          <Select
            isDisabled={hasMore}
            onChange={(option: any) => {
              updateFilter({ sorting: option.value });
            }}
            value={sortingOptions.find(
              (option: any) => option.value === filters.sorting
            )}
            options={sortingOptions}
            className="Sorting"
          />
        </div>
      </div>
      {!hasMore && (
        <div className="SmartTradesFilters">{`Total: ${list.length}`}</div>
      )}
    </>
  );
};

export default SmartTradesFilters;
