import React, { FC, useContext, useEffect, useState } from "react";
import { Spinner } from "react-bootstrap";
import { Bar, Line } from "react-chartjs-2";
import { SmartTradesContext } from "../../../../App";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import {
  AccumulativeChartOptions,
  LeaderbordChartOptions,
  ProfitChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  getSmartTradesAccumulativeProfitsChartData,
  getSmartTradesPairsLeaderboardChartData,
  getSmartTradesProfitsChartData,
} from "../../../../utils/SmartTradesUtil";
import "./SmartTradesCharts.css";

interface SmartTradesChartsProps {}

const SmartTradesCharts: FC<SmartTradesChartsProps> = () => {
  const { smartTradesState } = useContext(SmartTradesContext);
  const { list, hasMore, smartTrades, filters } = smartTradesState;

  const [accumulativeProfitsData, setAccumulativeProfitsData] = useState<any>([
    [],
    [],
  ]);
  const [profitsData, setProfitsData] = useState<any>([[], []]);
  const [pairProfitsLeaderboardData, setPairProfitsLeaderboardData] =
    useState<any>([[], []]);

  const chartTooltipFormatLabel = (tooltipItem: any) => {
    return `${tooltipItem.dataset.label}: ${getCurrencyFormat({
      value: tooltipItem.parsed.y,
      currency: "USD",
    })}`;
  };

  useEffect(() => {
    setAccumulativeProfitsData(
      getSmartTradesAccumulativeProfitsChartData(
        list.map((id: any) => smartTrades[id]),
        filters.from
      )
    );
    setProfitsData(
      getSmartTradesProfitsChartData(
        list.map((id: any) => smartTrades[id]),
        filters.from
      )
    );
    setPairProfitsLeaderboardData(
      getSmartTradesPairsLeaderboardChartData(
        list.map((id: any) => smartTrades[id])
      )
    );
  }, [list]);

  return !hasMore ? (
    <div className="SmartTradesCharts">
      <div className="DCABotDetailsChartContainer">
        <Line
          options={
            AccumulativeChartOptions(
              `Accumulative Profit, $`,
              chartTooltipFormatLabel
            ) as any
          }
          data={{
            labels: accumulativeProfitsData[0],
            datasets: [
              {
                label: `Accumulative Profit, $`,
                data: accumulativeProfitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
                fill: { value: 0 },
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            ProfitChartOptions(
              `Daily Profit, $`,
              chartTooltipFormatLabel
            ) as any
          }
          data={{
            labels: profitsData[0],
            datasets: [
              {
                label: `Profit, $`,
                data: profitsData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            LeaderbordChartOptions(
              "Pairs leaderboard by profit",
              pairProfitsLeaderboardData[0]
            ) as any
          }
          data={{
            labels: pairProfitsLeaderboardData[0],
            datasets: [
              {
                label: "Profit, $",
                data: pairProfitsLeaderboardData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
    </div>
  ) : (
    <div className="SmartTradesChartsSpiner">
      <Spinner animation="grow" />
    </div>
  );
};

export default SmartTradesCharts;
