import { IGridBot, IGridBotFilters } from "../../interfaces/GridBot";

interface ISetNewGridBotsList {
  type: "setNewGridBotsList";
  payload: Array<IGridBot>;
}

export const setNewGridBotsList = (
  gridBotsList: Array<IGridBot>
): ISetNewGridBotsList => ({
  type: "setNewGridBotsList",
  payload: gridBotsList,
});

interface ISetHasMore {
  type: "setHasMore";
  payload: boolean;
}

export const setHasMore = (hasMore: boolean): ISetHasMore => ({
  type: "setHasMore",
  payload: hasMore,
});

interface IAddToGridBotsList {
  type: "addToGridBotsList";
  payload: Array<IGridBot>;
}

export const addToGridBotsList = (
  gridBotsList: Array<IGridBot>
): IAddToGridBotsList => ({
  type: "addToGridBotsList",
  payload: gridBotsList,
});

interface ISetGridBotsOffset {
  type: "setGridBotsOffset";
  payload: number;
}

export const setGridBotsOffset = (offset: number): ISetGridBotsOffset => ({
  type: "setGridBotsOffset",
  payload: offset,
});

interface ISetGridBotsFilters {
  type: "setGridBotsFilters";
  payload: IGridBotFilters;
}

export const setGridBotsFilters = (
  gridBotsFilter: IGridBotFilters
): ISetGridBotsFilters => ({
  type: "setGridBotsFilters",
  payload: gridBotsFilter,
});

interface ISetGridBot {
  type: "setGridBot";
  payload: { gridBotId: number; gridBot: IGridBot };
}

export const setGridBot = (
  gridBotId: number,
  gridBot: IGridBot
): ISetGridBot => ({
  type: "setGridBot",
  payload: { gridBotId, gridBot },
});

export type GridBotsAction =
  | ISetNewGridBotsList
  | ISetHasMore
  | IAddToGridBotsList
  | ISetGridBotsOffset
  | ISetGridBotsFilters
  | ISetGridBot;
