import React, { useContext, useEffect, useState } from "react";
import { ButtonGroup, ToggleButton } from "react-bootstrap";
import { AccountsContext, ErrorsContext, GridBotsContext } from "../../../App";
import {
  fetchGridBotsMetrics,
  fetchGridBots,
} from "../../../state/gridBots/GridBotsEffects";
import Select from "react-select";

import "./GridBotsList.css";
import GridBotsListTable from "./GridBotsListTable/GridBotsListTable";
import { setGridBotsFilters } from "../../../state/gridBots/GridBotsActions";
import { isSpotAccount } from "../../../utils/BotsUtils";
import GridBotDetails from "../GridBotDetails/GridBotDetails";
import GridBotListMetrics from "./GridBotListMetrics/GridBotListMetrics";

export const investmentRadios = [
  { name: "Value", value: "value" },
  { name: "Balances", value: "balances" },
];
export const valuesRadios = [
  { name: "USD", value: "usd" },
  { name: "Quote", value: "quote" },
];

const GridBotsList = () => {
  const { gridBotsState, gridBotsDispatch } = useContext(GridBotsContext);
  const { list, hasMore, filters } = gridBotsState;
  const {
    accountsState: { accounts, list: accountsList },
  } = useContext(AccountsContext);
  const { errorsDispatch } = useContext(ErrorsContext);
  const [investment, setInvestment] = useState("value");
  const [values, setValues] = useState("usd");
  const [modalGridBotId, setModalGridBotId] = useState("");

  const updateFilter = async (filterItem: any) => {
    gridBotsDispatch(
      setGridBotsFilters({
        ...gridBotsState.filters,
        ...filterItem,
      })
    );
  };

  const isNothingFound = !hasMore && list.length === 0;

  const accountsOptions = [
    { value: "", label: "All spot accounts", label_id: "All" },
    ...accountsList
      .filter((accountId: any) =>
        isSpotAccount(accounts[accountId].market_code)
      )
      .map((accountId: any) => ({
        value: accountId,
        label: (
          <div className="AccountFilterItem">
            <div>
              <div
                className="AccountMarketIcon"
                style={{
                  backgroundImage: `url(${accounts[accountId].market_icon})`,
                }}
              ></div>
            </div>
            <span>{accounts[accountId].name}</span>
          </div>
        ),
        label_id: accounts[accountId].name,
      })),
  ];

  useEffect(() => {
    fetchGridBots(gridBotsDispatch, gridBotsState, errorsDispatch).then();
  }, [filters]);

  useEffect(() => {
    if (!hasMore) {
      for (const gridBotId of list) {
        fetchGridBotsMetrics(
          gridBotsDispatch,
          gridBotsState,
          errorsDispatch,
          gridBotId
        ).then();
      }
    }
  }, [hasMore, list]);

  return (
    <>
      {/* <GridBotListMetrics /> */}
      <div className="GridBotsFilters">
        <div className="GridBotsFilterGroup">
          <div className="FilterTitle">Account</div>
          <Select
            onChange={(option: any) => {
              updateFilter({ account: option.value });
            }}
            value={accountsOptions.find(
              (option: any) => option.value === filters.account
            )}
            options={accountsOptions}
            className="AccountFilter"
            filterOption={(candidate, input) =>
              candidate.data.label_id.includes(input)
            }
          />
        </div>
        {/* <div className="GridBotListControl">
          <div className="FilterTitle">Investment</div>
          <ButtonGroup id="foo">
            {investmentRadios.map((radio, idx) => (
              <ToggleButton
                key={radio.value}
                id={`radio-${radio.value}`}
                type="radio"
                variant="outline-primary"
                name="radioInvestment"
                value={radio.value}
                checked={investment === radio.value}
                onChange={(e) => {
                  setInvestment(e.currentTarget.value);
                }}
              >
                {radio.name}
              </ToggleButton>
            ))}
          </ButtonGroup>
        </div> */}
        {/* {investment === "value" && (
          <div className="GridBotListControl">
            <div className="FilterTitle">Show values in</div>
            <ButtonGroup id="bar">
              {valuesRadios.map((radio, idx) => (
                <ToggleButton
                  key={radio.value}
                  id={`radio-${radio.value}`}
                  type="radio"
                  variant="outline-primary"
                  name="radioValues"
                  value={radio.value}
                  checked={values === radio.value}
                  onChange={(e) => setValues(e.currentTarget.value)}
                >
                  {radio.name}
                </ToggleButton>
              ))}
            </ButtonGroup>
          </div>
        )} */}
      </div>
      {isNothingFound ? (
        <div className="FallbackContainer">
          <p style={{ textAlign: "center" }}>
            <b>No results found =(</b>
          </p>
        </div>
      ) : (
        <div className="GridBotsList">
          <GridBotsListTable
            values={values}
            investment={investment}
            setModalGridBotId={setModalGridBotId}
          />
          {!!modalGridBotId && (
            <GridBotDetails
              modalIsOpen={!!modalGridBotId}
              setModalIsOpen={setModalGridBotId}
              gridBotId={modalGridBotId}
            />
          )}
        </div>
      )}
    </>
  );
};

export default GridBotsList;
