import React, { useEffect, useState } from "react";
import { Bar } from "react-chartjs-2";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { filterAllSuccessfulDeals } from "../../../../state/deals/DealsSelectors";
import {
  NumberHistogramChartOptions,
  LeaderbordChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  getDealsSOHistogramData,
  getOneBotSOLeaderbord,
} from "../../../../utils/DealsUtils";
import "./DCABotDetailsSafetyCharts.css";

export const DCABotDetailsSafetyCharts = ({
  deals,
  timeRangeValue,
  dateLabelsDays,
  bot,
}: {
  deals: IDeal[];
  timeRangeValue: string;
  dateLabelsDays: string[];
  bot: IBot;
}) => {
  const [filteredDeals, setFilteredDeals] = useState<any>([]);
  const [dealsSOHistogramData, setDealsSOHistogramData] = useState<any>([]);
  const [pairsLeaderbordData, setPairsLeaderbordData] = useState<any>([[], []]);
  const [antiPairsLeaderbordData, setAntiPairsLeaderbordData] = useState<any>([
    [],
    [],
  ]);

  useEffect(() => {
    setFilteredDeals(filterAllSuccessfulDeals(deals));
  }, [deals]);

  useEffect(() => {
    setDealsSOHistogramData(getDealsSOHistogramData(filteredDeals));
    setPairsLeaderbordData(getOneBotSOLeaderbord(filteredDeals));
    setAntiPairsLeaderbordData(getOneBotSOLeaderbord(filteredDeals, "asc"));
  }, [filteredDeals]);

  return (
    <>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            LeaderbordChartOptions(
              "Pairs with least SO completed",
              antiPairsLeaderbordData[0]
            ) as any
          }
          data={{
            labels: antiPairsLeaderbordData[0],
            datasets: [
              {
                label: "Average completed safety orders",
                data: antiPairsLeaderbordData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            LeaderbordChartOptions(
              "Pairs with most SO completed",
              pairsLeaderbordData[0]
            ) as any
          }
          data={{
            labels: pairsLeaderbordData[0],
            datasets: [
              {
                label: "Average completed safety orders",
                data: pairsLeaderbordData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            NumberHistogramChartOptions(
              "Histogram of completed SO per deal",
              "Number of completed safety orders",
              "Number of deals"
            ) as any
          }
          data={{
            labels: dealsSOHistogramData[0],
            datasets: [
              {
                label: "Finished deals",
                data: dealsSOHistogramData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
    </>
  );
};
