import React, { FC, useContext, useState } from "react";
import { AccountsContext } from "../../../App";
import SmartTradeDetails from "../SmartTradeDetails/SmartTradeDetails";
import SmartTradesCharts from "./SmartTradesCharts/SmartTradesCharts";
import SmartTradesFilters from "./SmartTradesFilters/SmartTradesFilters";
import "./SmartTradesList.css";
import SmartTradesTable from "./SmartTradesTable/SmartTradesTable";

interface SmartTradesListProps {}

const SmartTradesList: FC<SmartTradesListProps> = () => {
  const [modalSmartTradeId, setModalSmartTradeId] = useState("");

  return (
    <div className="SmartTradesList">
      <SmartTradesCharts />
      <SmartTradesFilters />
      <SmartTradesTable setModalSmartTradeId={setModalSmartTradeId} />
      {!!modalSmartTradeId && (
        <SmartTradeDetails
          modalIsOpen={!!modalSmartTradeId}
          setModalIsOpen={setModalSmartTradeId}
          smartTradeId={modalSmartTradeId}
        />
      )}
    </div>
  );
};

export default SmartTradesList;
