import React, { useEffect, useState } from "react";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { useTable, usePagination, useSortBy } from "react-table";

import "./DCABotDetailsDealsTable.css";
import { Badge, Modal, Pagination, Table, Button } from "react-bootstrap";
import moment from "moment";
import { getCurrencyFormat } from "../../../../utils/BotsUtils";
import { getDeal } from "../../../../utils/ApiUtils";

const badgeVariant = {
  Cancelled: "secondary",
  StopLoss: "danger",
  Failed: "warning",
  Completed: "success",
  "Closed at Market Price": "dark",
  Liquidated: "dark",
  Switched: "secondary",
};

export const DCABotDetailsDealsTable = ({
  bot,
  deals,
  dealStateSwitcher,
}: {
  bot: IBot;
  deals: IDeal[];
  dealStateSwitcher: string;
}) => {
  const columns = React.useMemo(
    () =>
      [
        {
          Header: "ID",
          accessor: "id",
          disableSortBy: true,
        },
        {
          Header: "Pair",
          accessor: (originalRow: any, rowIndex: any) => {
            return originalRow.pair.split("_").reverse().join("/");
          },
          id: "pair",
          disableSortBy: true,
        },
        {
          Header: "Status",
          accessor: "localized_status",
          Cell: ({ cell: { value } }: any) => (
            // @ts-ignore
            <Badge style={{ fontSize: "12px" }} bg={badgeVariant[value]}>
              {value}
            </Badge>
          ),
          disableSortBy: true,
        },
        {
          Header: "Start",
          accessor: (originalRow: any, rowIndex: any) => {
            return new Date(originalRow.created_at);
          },
          id: "created_at",
          sortType: "datetime",
          Cell: ({ cell: { value } }: any) => (
            <>
              <div>{moment(value).format("D MMM, YYYY")}</div>
              <div>{moment(value).format("HH:mm")}</div>
            </>
          ),
        },
        {
          Header: "End",
          accessor: (originalRow: any, rowIndex: any) => {
            return new Date(originalRow.closed_at);
          },
          id: "closed_at",
          sortType: "datetime",
          Cell: ({ cell: { value } }: any) => {
            return dealStateSwitcher === "finished" ? (
              <>
                <div>{moment(value).format("D MMM, YYYY")}</div>
                <div>{moment(value).format("HH:mm")}</div>
              </>
            ) : (
              <div />
            );
          },
        },
        {
          Header: "Duration",
          accessor: (originalRow: any, rowIndex: any) => {
            let start = moment.utc(originalRow.created_at);
            let end = originalRow.closed_at
              ? moment.utc(originalRow.closed_at)
              : moment.utc();
            return end.diff(start);
          },
          id: "duration",
          Cell: ({ cell: { value } }: any) => {
            const duration = moment.duration(value);
            const days = Math.floor(duration.asDays());
            const daysFormatted = days ? `${days}d ` : "";

            //Get Hours
            const hours = duration.hours();
            const hoursFormatted = `${hours}h `;

            //Get Minutes
            const minutes = duration.minutes();
            const minutesFormatted = `${minutes}m`;

            return `${daysFormatted} ${hoursFormatted} ${minutesFormatted}`;
          },
        },
        {
          Header: "Avg. entry price",
          id: "bought_average_price",
          accessor: (originalRow: any, rowIndex: any) =>
            originalRow.strategy === "long"
              ? originalRow.bought_average_price
              : originalRow.sold_average_price,
          Cell: ({ cell: { value } }: any) =>
            value
              ? getCurrencyFormat({
                  value: parseFloat(value),
                  currency: "marketPrice",
                })
              : "",
          disableSortBy: true,
        },
        {
          Header:
            dealStateSwitcher === "finished" ? "Close price" : "Current price",
          id: "sold_average_price",
          accessor: (originalRow: any, rowIndex: any) =>
            dealStateSwitcher === "finished"
              ? originalRow.strategy === "long"
                ? originalRow.sold_average_price
                : originalRow.bought_average_price
              : originalRow.current_price,
          Cell: ({ cell: { value } }: any) =>
            value
              ? getCurrencyFormat({
                  value: parseFloat(value),
                  currency: "marketPrice",
                })
              : "",
          disableSortBy: true,
        },
        {
          Header: "TP price",
          accessor: "take_profit_price",
          disableSortBy: true,
        },
        {
          Header: "Volume",
          accessor: (originalRow: any, rowIndex: any) => {
            const quoteCurrency = originalRow.pair.split("_")[0];
            const baseCurrency = originalRow.pair.split("_")[1];
            const amount =
              originalRow.strategy === "long"
                ? getCurrencyFormat({
                    value: originalRow.bought_amount,
                    currency: baseCurrency,
                  })
                : getCurrencyFormat({
                    value: originalRow.sold_amount,
                    currency: baseCurrency,
                  });
            const volume =
              originalRow.strategy === "long"
                ? getCurrencyFormat({
                    value: originalRow.bought_volume,
                    currency: quoteCurrency,
                  })
                : getCurrencyFormat({
                    value: originalRow.sold_volume,
                    currency: quoteCurrency,
                  });
            return `${amount} ${baseCurrency}!${volume} ${quoteCurrency}`;
          },
          id: "volume",
          Cell: ({ cell: { value } }: any) => {
            const values = value.split("!");
            return (
              <>
                <div>{values[0]}</div>
                <div>{values[1]}</div>
              </>
            );
          },
          disableSortBy: true,
        },
        {
          Header: "Filled SO",
          id: "completed_safety_orders_count",
          accessor: (originalRow: any, rowIndex: any) =>
            `${originalRow.completed_safety_orders_count}${
              originalRow.completed_manual_safety_orders_count
                ? ` (+${originalRow.completed_manual_safety_orders_count})`
                : ""
            }`,
          disableSortBy: true,
        },
        {
          Header: "Profit, %",
          accessor:
            dealStateSwitcher === "finished"
              ? "final_profit_percentage"
              : "actual_profit_percentage",
          Cell: ({ cell: { value } }: any) => `${value}%`,
          sortType: (a: any, b: any) => {
            a =
              dealStateSwitcher === "finished"
                ? Number(a.original.final_profit_percentage)
                : Number(a.original.actual_profit_percentage);
            b =
              dealStateSwitcher === "finished"
                ? Number(b.original.final_profit_percentage)
                : Number(b.original.actual_profit_percentage);
            return a > b ? 1 : -1;
          },
        },
        {
          Header: "Profit, $",
          accessor:
            dealStateSwitcher === "finished"
              ? "usd_final_profit"
              : "actual_usd_profit",
          sortType: (a: any, b: any) => {
            a =
              dealStateSwitcher === "finished"
                ? Number(a.original.usd_final_profit)
                : Number(a.original.actual_usd_profit);
            b =
              dealStateSwitcher === "finished"
                ? Number(b.original.usd_final_profit)
                : Number(b.original.actual_usd_profit);
            return a > b ? 1 : -1;
          },
          Cell: ({ cell: { value } }: any) => {
            const numberValue = parseFloat(value);
            return (
              <Badge
                style={{ fontSize: "13px" }}
                bg={numberValue >= 0 ? "success" : "danger"}
              >{`${getCurrencyFormat(
                {
                  value: numberValue,
                  currency: "USD",
                },
                "always"
              )} $`}</Badge>
            );
          },
        },
        {
          Header: "Profit in Base",
          id: "profitBase",
          accessor: (originalRow: any, rowIndex: any) =>
            `${
              parseFloat(originalRow.bought_amount) -
              parseFloat(originalRow.sold_amount)
            }!${originalRow.pair.split("_")[1]}`,

          Cell: ({ cell: { value } }: any) => {
            const numberValue = parseFloat(value.split("!")[0]);
            const currency = value.split("!")[1];
            return dealStateSwitcher === "finished" ? (
              <span
                style={{ color: numberValue >= 0 ? "green" : "red" }}
              >{`${getCurrencyFormat(
                {
                  value: numberValue,
                  currency,
                },
                "always"
              )} ${currency}`}</span>
            ) : (
              ""
            );
          },
          disableSortBy: true,
        },
        {
          Header: "Profit in Quote",
          id: "profitQuote",
          accessor: (originalRow: any, rowIndex: any) =>
            `${
              parseFloat(originalRow.sold_volume) -
              parseFloat(originalRow.bought_volume)
            }!${originalRow.pair.split("_")[0]}`,

          Cell: ({ cell: { value } }: any) => {
            const numberValue = parseFloat(value.split("!")[0]);
            const currency = value.split("!")[1];
            return dealStateSwitcher === "finished" ? (
              <span
                style={{ color: numberValue >= 0 ? "green" : "red" }}
              >{`${getCurrencyFormat(
                {
                  value: numberValue,
                  currency,
                },
                "always"
              )} ${currency}`}</span>
            ) : (
              ""
            );
          },
          disableSortBy: true,
        },
      ] as any,
    []
  );
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    state: { pageIndex },
  } = useTable(
    {
      columns,
      data: deals,
      disableSortRemove: true,
      autoResetPage: true,
      autoResetSortBy: false,
      initialState: {
        pageSize: 50,
        sortBy:
          dealStateSwitcher === "finished"
            ? [{ id: "closed_at", desc: true }]
            : [{ id: "created_at", desc: true }],
      },
    },
    useSortBy,
    usePagination
  );
  const [modalIsOpen, setModalIsOpen] = React.useState(false);
  const [selectedDeal, setSelectedDeal] = React.useState(null as any);

  return (
    <div className="DCABotDetailsDealsTable">
      <div className="DCABotDetailsDealsTableContainer">
        <DealModal
          modalIsOpen={modalIsOpen}
          setModalIsOpen={setModalIsOpen}
          dealId={selectedDeal}
        />
        {pageCount > 1 && (
          <TablePagination
            pageIndex={pageIndex}
            pageCount={pageCount}
            gotoPage={gotoPage}
            nextPage={nextPage}
            previousPage={previousPage}
          />
        )}
        <Table striped bordered {...getTableProps()} hover>
          <thead style={{ fontSize: "14px" }}>
            {headerGroups.map((headerGroup) => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map((column) => (
                  <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                    <div style={{ display: "flex", width: "100%" }}>
                      {column.render("Header")}
                      <div
                        style={{
                          marginLeft: "auto",
                          cursor: column.disableSortBy ? "auto" : "pointer",
                        }}
                      >
                        {column.isSorted
                          ? column.isSortedDesc
                            ? " 🔽"
                            : " 🔼"
                          : column.disableSortBy
                          ? "   "
                          : " 📶"}
                      </div>
                    </div>
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody style={{ fontSize: "14px" }} {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row);
              return (
                <tr
                  {...row.getRowProps()}
                  onClick={() => {
                    setSelectedDeal(row.original.id);
                    setModalIsOpen(true);
                  }}
                  style={{ cursor: "pointer" }}
                >
                  {row.cells.map((cell) => {
                    return (
                      <td {...cell.getCellProps()}>{cell.render("Cell")}</td>
                    );
                  })}
                </tr>
              );
            })}
          </tbody>
        </Table>
      </div>
    </div>
  );
};

const TablePagination = ({
  pageIndex,
  pageCount,
  gotoPage,
  nextPage,
  previousPage,
}: any) => {
  const range = (start: number, end: number) =>
    Array(end - start + 1)
      .fill(0)
      .map((_, idx) => start + idx);

  const renderPaginationNumberItem = (page: any, pageIndex: any) => (
    <Pagination.Item
      active={page === pageIndex}
      onClick={() => {
        gotoPage(page);
      }}
      key={page}
    >
      {page + 1}
    </Pagination.Item>
  );

  const renderPaginationBeginning = () => (
    <>
      <Pagination.First
        onClick={() => {
          gotoPage(0);
        }}
      />
      <Pagination.Prev
        onClick={() => {
          previousPage();
        }}
      />
      {pageIndex - 2 < 4 &&
        renderPaginationNumberItem(pageIndex - 2, pageIndex)}
      {pageIndex - 1 < 4 &&
        renderPaginationNumberItem(pageIndex - 1, pageIndex)}
    </>
  );
  const renderPaginationEnd = () => (
    <>
      <Pagination.Next
        onClick={() => {
          nextPage();
        }}
      />
      <Pagination.Last
        onClick={() => {
          gotoPage(pageCount - 1);
        }}
      />
    </>
  );

  return (
    <>
      {pageCount <= 16 && (
        <Pagination>
          {range(0, pageCount - 1).map((page) =>
            renderPaginationNumberItem(page, pageIndex)
          )}
        </Pagination>
      )}
      {pageCount > 16 && (
        <Pagination>
          {pageIndex < 7 && (
            <>
              {range(0, 6).map((page) =>
                renderPaginationNumberItem(page, pageIndex)
              )}
              {renderPaginationEnd()}
            </>
          )}

          {pageIndex >= 7 && pageIndex <= pageCount - 8 && (
            <>
              {renderPaginationBeginning()}
              {renderPaginationNumberItem(pageIndex - 2, pageIndex)}
              {renderPaginationNumberItem(pageIndex - 1, pageIndex)}
              <Pagination.Item active>{pageIndex + 1}</Pagination.Item>
              {renderPaginationNumberItem(pageIndex + 1, pageIndex)}
              {renderPaginationNumberItem(pageIndex + 2, pageIndex)}
              {renderPaginationEnd()}
            </>
          )}
          {pageCount - pageIndex < 8 && (
            <>
              {renderPaginationBeginning()}
              {range(pageCount - 7, pageCount - 1).map((page) =>
                renderPaginationNumberItem(page, pageIndex)
              )}
            </>
          )}
        </Pagination>
      )}
    </>
  );
};

const DealModal = ({ modalIsOpen, setModalIsOpen, dealId }: any) => {
  const [deal, setDeal] = React.useState(null as any);

  useEffect(() => {
    if (dealId) {
      getDeal(dealId).then((dl) => {
        setDeal(dl);
      });
    }
  }, [dealId]);
  return (
    <>
      {deal && (
        <Modal
          show={modalIsOpen}
          onHide={() => {
            setModalIsOpen(false);
          }}
          scrollable
          size="lg"
        >
          <Modal.Header closeButton>
            <Modal.Title>{deal.id}</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            {deal.bot_events.length > 0 && (
              <Table striped hover>
                <thead>
                  <tr>
                    <th>Date/Time</th>
                    <th>Message</th>
                  </tr>
                </thead>
                <tbody>
                  {deal.bot_events.map((event: any) => (
                    <tr key={event.created_at}>
                      <td>
                        {moment(event.created_at).format("D MMM, YYYY HH:mm")}
                      </td>
                      <td>{event.message}</td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            )}
            {deal.bot_events.length === 0 && (
              <blockquote className="blockquote mb-0">
                <p>
                  “Nothing can last forever. There isn't any memory, no matter
                  how intense, that doesn't fade out at last.”{" "}
                </p>
                <footer className="blockquote-footer">Juan Rulfo</footer>
              </blockquote>
            )}
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="primary"
              onClick={() => {
                window.open(`https://3commas.io/deals/${dealId}`, "_blank");
              }}
            >
              Open in 3commas
            </Button>
          </Modal.Footer>
        </Modal>
      )}
    </>
  );
};
