import React, { useContext, useEffect, useState } from "react";
import { ProgressBar } from "react-bootstrap";
import { BotsContext, DealsContext, ErrorsContext } from "../../../../App";
import { fetchBots } from "../../../../state/bots/BotsEffects";
import { fetchDealsForBotFor30Days } from "../../../../state/deals/DealsEffects";
import { is30daysDealsLoaded } from "../../../../state/deals/DealsSelectors";
import { getDealsLimiter, setDealsLimiter } from "../../../../utils/ApiUtils";
import "./BotsListCharts.css";
import { Bar, Line } from "react-chartjs-2";
import {
  AccumulativeChartOptions,
  LeaderbordChartOptions,
  ProfitChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  calculateProfitPerDayForListOfBots,
  getAccumulativeProfitChartData,
  getProfitChartData,
} from "../../../../utils/DealsUtils";
import {
  getBotsLeaderboardChartData,
  getCurrencyFormat,
  getAllActiveDeals,
  getSummirizedUpnl,
  getSummirizedFundsLocked,
} from "../../../../utils/BotsUtils";
import _ from "lodash";

const BotsListCharts = () => {
  const { botsState, botsDispatch } = useContext(BotsContext);
  const { dealsState, dealsDispatch } = useContext(DealsContext);
  const { deals } = dealsState;
  const { list, hasMore, offset, bots } = botsState;
  const { errorsDispatch } = useContext(ErrorsContext);
  const [dealsLoaded, setDealsLoaded] = useState(false);
  const [botsLoaded, setBotsLoaded] = useState(false);

  const [numberHandled, setNumberHandled] = useState(0);
  const [avgProfit, setAvgProfit] = useState(0);
  const [activeDeals, setActiveDeals] = useState(0);
  const [summirizedUpnl, setSummirizedUpnl] = useState(0);
  const [summirizedFundsLocked, setSummirizedFundsLocked] = useState(0);
  const [profitsChartData, setProfitsChartData] = useState<any>([[], []]);
  const [accumulativeProfitsChartData, setAccumulativeProfitsChartData] =
    useState<any>([[], []]);
  const [botsLeaderboardChartData, setBotsLeaderboardChartData] = useState<any>(
    [[], [], []]
  );

  //first get all bots
  const getBotsList = async () => {
    if (hasMore && offset !== 0) {
      await fetchBots(botsDispatch, botsState, errorsDispatch, false, 100);
    }
  };

  useEffect(() => {
    getBotsList().then(() => {
      if (!hasMore) {
        setBotsLoaded(true);
      }
    });
  }, [offset]);

  // get deals
  const getBoatsDeals = async () => {
    setDealsLimiter(Math.random());

    for (const botId of list) {
      if (!is30daysDealsLoaded(dealsState, botId)) {
        await fetchDealsForBotFor30Days(
          dealsState,
          dealsDispatch,
          errorsDispatch,
          botId
        );
      }
    }
  };

  useEffect(() => {
    if (botsLoaded) {
      getBoatsDeals().then(() => {
        setDealsLoaded(true);
      });
    }
  }, [botsLoaded]);

  // then calculate data for charts
  useEffect(() => {
    if (dealsLoaded) {
      const profitsObject = calculateProfitPerDayForListOfBots(
        dealsState,
        list
      );
      setProfitsChartData(getProfitChartData(profitsObject));
      setAccumulativeProfitsChartData(
        getAccumulativeProfitChartData(profitsObject)
      );
      setBotsLeaderboardChartData(
        getBotsLeaderboardChartData(botsState, dealsState)
      );
      setAvgProfit(_.sum(getProfitChartData(profitsObject)[1]) / 30);
      setActiveDeals(getAllActiveDeals(botsState));
      setSummirizedUpnl(getSummirizedUpnl(botsState));
      setSummirizedFundsLocked(getSummirizedFundsLocked(botsState));
    }
  }, [dealsLoaded, bots]);

  // reset limiter everytime to stop all the jobs
  useEffect(() => {
    return () => {
      const dealsLimiter = getDealsLimiter();
      dealsLimiter
        .stop({ dropWaitingJobs: true })
        .then(() => dealsLimiter.disconnect());
    };
  }, []);

  // update data for loader
  useEffect(() => {
    const botsWithLoadedDeals = list.filter((botId: number) =>
      is30daysDealsLoaded(dealsState, botId)
    );
    setNumberHandled((botsWithLoadedDeals.length / list.length) * 100);
  }, [deals]);

  return (
    <div className="BotsListCharts">
      {!dealsLoaded && (
        <div className="ProgressBar">
          {!botsLoaded ? (
            <ProgressBar animated now={100} />
          ) : (
            <ProgressBar
              now={Math.ceil(numberHandled)}
              label={`${Math.ceil(numberHandled)}%`}
            />
          )}
        </div>
      )}
      {dealsLoaded && (
        <>
          <div className="ChartContainer">
            <div className="SummirizedDataContainer">
              <div className="SummirizedData">
                <div className="SummirizedDataName">Average Daily profit</div>
                <div
                  className="SummirizedDataValue"
                  style={{ color: avgProfit < 0 ? "#FF0000" : "#198754" }}
                >
                  {`${getCurrencyFormat(
                    {
                      value: avgProfit,
                      currency: "USD",
                    },
                    "always"
                  )} $`}
                </div>
              </div>
            </div>
          </div>
          <div className="ChartContainer">
            <div className="SummirizedDataContainer">
              <div className="SummirizedData">
                <div className="SummirizedDataName">Active Deals</div>
                <div className="SummirizedDataValue">{activeDeals}</div>
              </div>
              <div className="SummirizedData">
                <div className="SummirizedDataName">uPNL</div>
                <div
                  className="SummirizedDataValue"
                  style={{ color: summirizedUpnl < 0 ? "#FF0000" : "#198754" }}
                >
                  {`${getCurrencyFormat(
                    {
                      value: summirizedUpnl,
                      currency: "USD",
                    },
                    "always"
                  )} $`}
                </div>
              </div>
            </div>
          </div>
          <div className="ChartContainer">
            <div className="SummirizedDataContainer">
              <div className="SummirizedData">
                <div className="SummirizedDataName">Funds Locked</div>
                <div className="SummirizedDataValue">
                  {`${getCurrencyFormat({
                    value: summirizedFundsLocked,
                    currency: "USD",
                  })} $`}
                </div>
              </div>
            </div>
          </div>
          <div className="ChartContainer">
            <Line
              options={
                AccumulativeChartOptions(
                  "Total Accumulative Profit (30 days), $"
                ) as any
              }
              data={{
                labels: accumulativeProfitsChartData[0],
                datasets: [
                  {
                    label: "Accumulative Profit, $",
                    data: accumulativeProfitsChartData[1],
                    borderColor: "rgb(53, 162, 235)",
                    backgroundColor: "rgba(53, 162, 235, 0.5)",
                    fill: { value: 0 },
                  },
                ],
              }}
            />
          </div>
          <div className="ChartContainer">
            <Bar
              options={ProfitChartOptions("Total Profit (30 days), $") as any}
              data={{
                labels: profitsChartData[0],
                datasets: [
                  {
                    label: "Profit, $",
                    data: profitsChartData[1],
                    borderColor: "rgb(53, 162, 235)",
                    backgroundColor: "rgba(53, 162, 235, 0.5)",
                  },
                ],
              }}
            />
          </div>
          <div className="ChartContainer">
            <Bar
              options={
                LeaderbordChartOptions(
                  "Leaderboard by profit (30 days), $",
                  botsLeaderboardChartData[0]
                ) as any
              }
              data={{
                labels: botsLeaderboardChartData[0],
                datasets: [
                  {
                    label: "Profits, $",
                    data: botsLeaderboardChartData[1],
                    borderColor: "rgb(255, 99, 132)",
                    backgroundColor: "rgba(255, 99, 132, 0.8)",
                  },
                ],
              }}
            />
          </div>
        </>
      )}
    </div>
  );
};

export default BotsListCharts;
