import { getDeals } from "./ApiUtils";
import moment from "moment";
import { IDeal } from "../interfaces/Deal";
import _ from "lodash";
import { IDealsState } from "../state/deals/DealsReducer";
import { selectAllDealsWithin30Days } from "../state/deals/DealsSelectors";
import { IBotsState } from "../state/bots/BotsReducer";

export const differenceInHours = (a: any, b: any) =>
  Math.ceil(a.diff(b) / (1000 * 60 * 60));

export const differenceInDays = (a: any, b: any) =>
  Math.ceil(a.diff(b) / (1000 * 60 * 60 * 24));

export const getDealAgeInHours = (deal: any) =>
  differenceInHours(moment(), moment(deal.closed_at));

export const getDealAgeInDaysSinceOpened = (deal: any) =>
  differenceInDays(moment(), moment(deal.created_at));

export const getAllDealsForOneBot = async (
  botId: number,
  daysWithin = 30,
  ofsetCallback = (offset: number) => {}
) => {
  const hoursWithin = 24 * daysWithin;
  const limit = 1000;
  let offset = 0;
  let deals: any = [];

  const fetchAllDealsInLoop = async () => {
    const newDeals = await getDeals(
      botId,
      offset,
      limit,
      "finished",
      "closed_at"
    );

    if (newDeals.length === 0) {
      return;
    }

    const [lastDeal] = newDeals.slice(-1);

    if (
      newDeals.length === limit &&
      getDealAgeInHours(lastDeal) <= hoursWithin
    ) {
      offset = offset + limit;
      deals = [...deals, ...newDeals];
      ofsetCallback(offset);
      await fetchAllDealsInLoop();
    } else {
      const filteredDeals = newDeals.filter(
        (deal: any) => getDealAgeInHours(deal) <= hoursWithin
      );
      deals = [...deals, ...filteredDeals];
    }
  };

  await fetchAllDealsInLoop();

  return deals;
};

export const getObjectOfProfitPerDay = (deals: Array<IDeal>) => {
  const dateString = (item: IDeal) =>
    moment(item.closed_at).format("YYYY-MM-DD");

  let result = _.groupBy(deals, dateString);
  return _.mapValues(result, (deals: Array<IDeal>) =>
    _.sum(deals.map((deal: IDeal) => parseFloat(deal.usd_final_profit)))
  );
};

export const getObjectOfProfitPerPair = (deals: Array<IDeal>) => {
  let result = _.groupBy(deals, "pair");
  return _.mapValues(result, (deals: Array<IDeal>) =>
    _.sum(deals.map((deal: IDeal) => parseFloat(deal.usd_final_profit)))
  );
};

export const getObjectOfSOPerPair = (deals: Array<IDeal>) => {
  let result = _.groupBy(deals, "pair");
  return _.mapValues(result, (deals: Array<IDeal>) =>
    _.mean(deals.map((deal: IDeal) => deal.completed_safety_orders_count))
  );
};

export const calculateProfitPerDayForListOfBots = (
  dealsState: IDealsState,
  botsIds: Array<number>
) => {
  return botsIds.reduce((currentObj: any, botId: number) => {
    const botsDeals = getObjectOfProfitPerDay(
      selectAllDealsWithin30Days(dealsState, botId)
    );

    return _.mergeWith(
      botsDeals,
      currentObj,
      (profit1: number, profit2: number) => {
        profit1 = profit1 || 0;
        profit2 = profit2 || 0;
        return profit1 + profit2;
      }
    );
  }, {});
};

export const calculateProfitPerBot = (
  dealsState: IDealsState,
  botId: number
) => {
  const botsDeals = selectAllDealsWithin30Days(dealsState, botId);
  return _.sum(
    botsDeals.map((deal: IDeal) => parseFloat(deal.usd_final_profit))
  );
};

export const getLastNDays = (n: number = 30) =>
  [...new Array(n)]
    .map((i, idx) => moment().startOf("day").subtract(idx, "days"))
    .map((date: any) => date.format("YYYY-MM-DD"))
    .reverse();

export const getProfitChartData = (profitObject: any, nDays = 30) => {
  const lastDays = getLastNDays(nDays);

  const profits = lastDays.map((day: string) => {
    return day in profitObject ? profitObject[day] : 0;
  });

  const labels = lastDays.map((day: any) => moment(day, "YYYY-MM-DD"));
  return [labels, profits];
};

export const getAccumulativeProfitChartData = (
  profitObject: any,
  nDays = 30
) => {
  const profits = getProfitChartData(profitObject, nDays);
  let currentvalue = 0;
  const accProfits = profits[1].map((profit: any) => {
    const acc = currentvalue + profit;
    currentvalue = acc;
    return acc;
  });
  return [profits[0], accProfits];
};

export const getDealsCountPerDay = (deals: IDeal[], dates: string[]) => {
  const dateString = (item: IDeal) =>
    moment(item.closed_at).format("YYYY-MM-DD");

  const delsGrouped = _.groupBy(deals, dateString);
  const result = _.mapValues(
    delsGrouped,
    (deals: Array<IDeal>) => deals.length
  );

  return dates.map((day: string) => {
    return day in result ? result[day] : 0;
  });
};

export const dealsStatusesCounts = (deals: IDeal[]) => {
  const countsObj = _.countBy(deals, (deal) => deal.localized_status);
  const keys = [
    "Cancelled",
    "StopLoss",
    "Failed",
    "Completed",
    "Closed at Market Price",
    "Liquidated",
    "Switched",
  ];
  const values = keys.map((key) => countsObj[key]);
  return [keys, values];
};

export const getDealsDurationHistogramData = (deals: IDeal[]) => {
  const dealDurations = deals.map((deal) => {
    let start = moment(deal.created_at);
    let end = deal.closed_at ? moment(deal.closed_at) : moment();
    return differenceInHours(end, start);
  });
  const countsObj = _.countBy(dealDurations);

  const keys = _.keys(countsObj).map((key) => parseInt(key));
  const values = keys.map((key) => countsObj[key]);

  return [keys, values];
};

export const getDealsSOHistogramData = (deals: IDeal[]) => {
  const dealSO = deals.map((deal) => deal.completed_safety_orders_count);
  const countsObj = _.countBy(dealSO);

  const keys = _.keys(countsObj).map((key) => parseInt(key));
  const values = keys.map((key) => countsObj[key]);

  return [keys, values];
};

export const getAverageDealDuration = (deals: IDeal[]) => {
  const dealDurations = deals.map((deal) => {
    let start = moment(deal.created_at);
    let end = deal.closed_at ? moment(deal.closed_at) : moment();
    return end.diff(start);
  });

  const avgDealDurationInMilliseconds =
    _.sum(dealDurations) / dealDurations.length || 0;

  const duration = moment.duration(avgDealDurationInMilliseconds);
  const days = Math.floor(duration.asDays());
  const daysFormatted = days ? `${days}d ` : "";

  //Get Hours
  const hours = duration.hours();
  const hoursFormatted = `${hours}h `;

  //Get Minutes
  const minutes = duration.minutes();
  const minutesFormatted = `${minutes}m`;

  return `${daysFormatted} ${hoursFormatted} ${minutesFormatted}`;
};

export const getOneBotAccumulativeProfitsChartData = (deals: IDeal[]) => {
  const botsDeals = getObjectOfProfitPerDay(deals);
  const difference =
    deals.length > 0
      ? differenceInDays(
          moment(),
          moment(deals.map((deal) => deal).reverse()[0].closed_at)
        )
      : 0;
  return difference < 30
    ? getAccumulativeProfitChartData(botsDeals, 30)
    : getAccumulativeProfitChartData(botsDeals, difference + 1);
};

export const getOneBotProfitsChartData = (deals: IDeal[]) => {
  const botsDeals = getObjectOfProfitPerDay(deals);
  const difference =
    deals.length > 0
      ? differenceInDays(
          moment(),
          moment(deals.map((deal) => deal).reverse()[0].closed_at)
        )
      : 0;
  return difference < 30
    ? getProfitChartData(botsDeals, 30)
    : getProfitChartData(botsDeals, difference + 1);
};

export const getOneBotCurrencyLeaderbord = (deals: IDeal[]) => {
  const profitsByPair = getObjectOfProfitPerPair(deals);
  const sortedProfits = _(profitsByPair)
    .toPairs()
    .orderBy([1], ["desc"])
    .value()
    .slice(0, 10);
  const labels = sortedProfits.map((profit) =>
    profit[0].split("_").reverse().join("/")
  );
  const values = sortedProfits.map((profit) => profit[1]);

  return [labels, values];
};

export const getOneBotSOLeaderbord = (deals: IDeal[], dir = "desc") => {
  const profitsByPair = getObjectOfSOPerPair(deals);
  const sortedProfits = _(profitsByPair)
    .toPairs()
    .orderBy([1], [dir as any])
    .value()
    .slice(0, 10);
  const labels = sortedProfits.map((profit) =>
    profit[0].split("_").reverse().join("/")
  );
  const values = sortedProfits.map((profit) => profit[1]);

  return [labels, values];
};

export const getAverageTp = (deals: IDeal[]) => {
  return {
    value:
      _.mean(deals.map((deal) => parseFloat(deal.final_profit_percentage))) ||
      0,
    currency: "%",
  };
};

export const getAverageProfitFromDeal = (deals: IDeal[]) => {
  return {
    value: _.mean(deals.map((deal) => parseFloat(deal.usd_final_profit))) || 0,
    currency: "$",
  };
};

export const getTotalProfitUsd = (deals: IDeal[]) => {
  return _.sum(deals.map((deal) => parseFloat(deal.usd_final_profit))) || 0;
};

export const getAverageDailyProfit = (
  createdAt: string,
  deals: IDeal[],
  fromFilter: any,
  toFilter: any
) => {
  const startBotDate = moment.utc(createdAt);
  const fromFilterMoment = moment.utc(fromFilter).startOf("day");
  const toFilterMoment = moment.utc(toFilter).startOf("day");

  const maxDate = moment.max([fromFilterMoment, startBotDate]);

  const profitsData = _.unzip(
    _.zip(...getOneBotProfitsChartData(deals)).filter(
      (chartData) =>
        !chartData[0].isBefore(maxDate, "day") &&
        !chartData[0].isAfter(toFilterMoment, "day")
    )
  );

  return {
    value: _.mean(profitsData[1]),
    currency: "$",
  };
};

export const getAverageDailyProfitFromAllDeals = (deals: IDeal[]) => {
  const profitsData = _.unzip(_.zip(...getOneBotProfitsChartData(deals)));

  return {
    value: _.mean(profitsData[1]),
    currency: "$",
  };
};
