import React, { createContext, useEffect, useReducer, useState } from "react";
import "./App.css";
import ErrorsAlerts from "./components/ErrorsAlerts/ErrorsAlerts";
import {
  botsReducer,
  botsStateInitializer,
  initialBotsState,
} from "./state/bots/BotsReducer";
import { dealsReducer, initialDealsState } from "./state/deals/DealsReducer";
import { getStorage, getStrategies, setStorage } from "./utils/ApiUtils";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  TimeScale,
  Filler,
  ArcElement,
} from "chart.js";
import "chartjs-adapter-moment";
import { NavBar } from "./components/NavBar/NavBar";
import { Outlet } from "react-router-dom";
import {
  gridBotsReducer,
  gridBotsStateInitializer,
  initialGridBotsState,
} from "./state/gridBots/GridBotsReducer";
import {
  initialSmartTradesState,
  smartTradesReducer,
  smartTradesStateInitializer,
} from "./state/smartTrades/SmartTradesReducer";
import {
  accountsReducer,
  initialAccountsState,
} from "./state/accounts/AccountsReducer";
import { fetchAccounts } from "./state/accounts/AccountsEffects";
import { Spinner } from "react-bootstrap";
import { TERMS_OF_USE } from "./utils/UserUtils";
import Consent from "./components/Consent/Consent";

ChartJS.register(
  CategoryScale,
  PointElement,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  LineElement,
  Legend,
  TimeScale,
  Filler,
  ArcElement
);

const initialStrategiesState: any = {
  strategies: [],
};

const initialErrorsState: any = {
  errors: [],
};

function errorsReducer(state = initialErrorsState, action: any) {
  switch (action.type) {
    case "addError":
      const errors = new Set(state.errors);
      errors.add(action.payload);
      return { errors: [...Array.from(errors)] };
    case "cleanErrors":
      return { errors: [] };
    default:
      throw new Error();
  }
}

function strategiesReducer(state = initialStrategiesState, action: any) {
  switch (action.type) {
    case "setStrategies":
      return { strategies: [...action.payload] };
    default:
      throw new Error();
  }
}

export const AccountsContext = createContext({
  accountsState: initialAccountsState,
  accountsDispatch: (state: any) => {},
});

export const StrategiesContext = createContext({
  strategiesState: initialStrategiesState,
  strategiesDispatch: (state: any) => {},
});

export const ErrorsContext = createContext({
  errorsState: initialErrorsState,
  errorsDispatch: (state: any) => {},
});

export const BotsContext = createContext({
  botsState: initialBotsState,
  botsDispatch: (state: any) => {},
});
export const GridBotsContext = createContext({
  gridBotsState: initialGridBotsState,
  gridBotsDispatch: (state: any) => {},
});
export const SmartTradesContext = createContext({
  smartTradesState: initialSmartTradesState,
  smartTradesDispatch: (state: any) => {},
});
export const DealsContext = createContext({
  dealsState: initialDealsState,
  dealsDispatch: (state: any) => {},
});

function App() {
  const [botsState, botsDispatch] = useReducer(
    botsReducer,
    initialBotsState,
    botsStateInitializer
  );
  const [gridBotsState, gridBotsDispatch] = useReducer(
    gridBotsReducer,
    initialGridBotsState,
    gridBotsStateInitializer
  );

  const [accountsState, accountsDispatch] = useReducer(
    accountsReducer,
    initialAccountsState
  );
  const [strategiesState, strategiesDispatch] = useReducer(
    strategiesReducer,
    initialStrategiesState
  );
  const [errorsState, errorsDispatch] = useReducer(
    errorsReducer,
    initialErrorsState
  );
  const [smartTradesState, smartTradesDispatch] = useReducer(
    smartTradesReducer,
    initialSmartTradesState,
    smartTradesStateInitializer
  );
  const [dealsState, dealsDispatch] = useReducer(
    dealsReducer,
    initialDealsState
  );

  const getStrategiesData = async () => {
    try {
      const data = await getStrategies();
      strategiesDispatch({ type: "setStrategies", payload: data });
    } catch {
      strategiesDispatch({ type: "setStrategies", payload: [] });
      errorsDispatch({
        type: "addError",
        payload: "Error when fetching strategies list",
      });
    }
  };

  const [showConsent, setShowConsent] = useState(false);
  const [consent, setConsent] = useState({ text: "empty" });

  useEffect(() => {
    getStorage("consent")
      .then((data) => {
        setConsent(JSON.parse(data.data.replaceAll("=>", ":")));
      })
      .catch(() => {
        setShowConsent(true);
      });

    fetchAccounts(accountsDispatch, accountsState, errorsDispatch);
    getStrategiesData();
  }, []);

  useEffect(() => {
    if (consent.text === "empty") {
      return;
    }
    if (consent.text !== TERMS_OF_USE) {
      setShowConsent(true);
    }
  }, [consent]);

  const postConsent = async () => {
    const request = await setStorage("consent", {
      text: TERMS_OF_USE,
      date: new Date().toString(),
    });
    console.log(request);
    if (!request.error) {
      setShowConsent(false);
    }
  };

  const { list } = accountsState;

  return (
    <ErrorsContext.Provider value={{ errorsState, errorsDispatch }}>
      <StrategiesContext.Provider
        value={{ strategiesState, strategiesDispatch }}
      >
        <AccountsContext.Provider value={{ accountsState, accountsDispatch }}>
          <BotsContext.Provider value={{ botsState, botsDispatch }}>
            <SmartTradesContext.Provider
              value={{ smartTradesState, smartTradesDispatch }}
            >
              <GridBotsContext.Provider
                value={{ gridBotsState, gridBotsDispatch }}
              >
                <DealsContext.Provider value={{ dealsState, dealsDispatch }}>
                  {/* <Consent show={showConsent} handleClose={postConsent} /> */}
                  <div className="App">
                    <NavBar />
                    {list.length > 0 ? (
                      <Outlet />
                    ) : (
                      <div className="SpinnerContainer">
                        <Spinner animation="grow" />
                      </div>
                    )}
                    <ErrorsAlerts />
                  </div>
                </DealsContext.Provider>
              </GridBotsContext.Provider>
            </SmartTradesContext.Provider>
          </BotsContext.Provider>
        </AccountsContext.Provider>
      </StrategiesContext.Provider>
    </ErrorsContext.Provider>
  );
}

export default App;
