import _ from "lodash";
import moment from "moment";
import { ISmartTrade } from "../interfaces/SmartTrade";
import {
  differenceInDays,
  getAccumulativeProfitChartData,
  getProfitChartData,
} from "./DealsUtils";
import { convertFilterValueToDate } from "./GeneralUtils";

export const getObjectOfSmartTradesProfitsPerDay = (
  smartTrades: Array<ISmartTrade>
) => {
  const filteredSmartTrades = smartTrades.filter(
    (smartTrade: any) => smartTrade.data.closed_at && smartTrade.profit.usd
  );
  const dateStringProfits = (item: any) =>
    moment(item.data.closed_at).format("YYYY-MM-DD");

  let resultProfits = _.groupBy(filteredSmartTrades, dateStringProfits);

  return _.mapValues(resultProfits, (smartTrades: Array<any>) =>
    _.sum(
      smartTrades.map((smartTrade: any) => parseFloat(smartTrade.profit.usd))
    )
  );
};

export const getSmartTradesProfitsChartData = (
  smartTrades: Array<ISmartTrade>,
  from: any
) => {
  const dateFrom = convertFilterValueToDate(from);
  const smartTradesProfitsByDays =
    getObjectOfSmartTradesProfitsPerDay(smartTrades);
  if (dateFrom) {
    return getProfitChartData(
      smartTradesProfitsByDays,
      differenceInDays(moment(), moment(dateFrom))
    );
  } else {
    const oldestTradeDate = moment.min(
      _.keys(smartTradesProfitsByDays).map((day: any) => moment(day))
    );
    return getProfitChartData(
      smartTradesProfitsByDays,
      differenceInDays(moment(), oldestTradeDate)
    );
  }
};
export const getSmartTradesAccumulativeProfitsChartData = (
  smartTrades: Array<ISmartTrade>,
  from: any
) => {
  const dateFrom = convertFilterValueToDate(from);
  const smartTradesProfitsByDays =
    getObjectOfSmartTradesProfitsPerDay(smartTrades);
  if (dateFrom) {
    return getAccumulativeProfitChartData(
      smartTradesProfitsByDays,
      differenceInDays(moment(), moment(dateFrom))
    );
  } else {
    const oldestTradeDate = moment.min(
      _.keys(smartTradesProfitsByDays).map((day: any) => moment(day))
    );
    return getAccumulativeProfitChartData(
      smartTradesProfitsByDays,
      differenceInDays(moment(), oldestTradeDate)
    );
  }
};

export const getSmartTradesPairsLeaderboardChartData = (
  smartTrades: Array<ISmartTrade>
) => {
  const filteredSmartTrades = smartTrades.filter(
    (smartTrade: any) => smartTrade.data.closed_at && smartTrade.profit.usd
  );

  const groupedResultProfits = _.groupBy(filteredSmartTrades, "pair");

  const resultProfits = _.mapValues(
    groupedResultProfits,
    (groupedSmartTrades: any) =>
      _.sum(
        groupedSmartTrades.map((smartTrade: any) =>
          parseFloat(smartTrade.profit.usd)
        )
      )
  );

  const sortedProfits = _(resultProfits)
    .toPairs()
    .orderBy([1], ["desc"])
    .value()
    .slice(0, 10);
  const labels = sortedProfits.map((profit) =>
    profit[0].split("_").reverse().join("/")
  );
  const values = sortedProfits.map((profit) => profit[1]);

  return [labels, values];
};
