import _ from "lodash";
import { ISmartTrade, ISmartTradeFilters } from "../../interfaces/SmartTrade";
import { SmartTradesAction } from "./SmartTradesActions";

export interface ISmartTradesState {
  list: Array<number>;
  hasMore: boolean;
  offset: number;
  filters: ISmartTradeFilters;
  limit: number;
  smartTrades: { [key: number]: ISmartTrade };
}

export const smartTradesStateInitializer = (
  initialValue = initialSmartTradesState
): ISmartTradesState => {
  let storedFilters = null;
  try {
    storedFilters = JSON.parse(
      (localStorage as any).getItem("smartTradesFiltersState")
    );
  } catch {}
  if (storedFilters) {
    return { ...initialValue, filters: storedFilters };
  } else {
    return initialValue;
  }
};

const initialFiltersState = {
  accountId: "",
  sorting: "created_at-desc",
  type: "",
  status: "all",
  from: { label: "month", from: "", to: "" },
  pair: "",
};

export const initialSmartTradesState: any = {
  list: [],
  hasMore: true,
  limit: 100,
  offset: 1,
  filters: initialFiltersState,
  smartTrades: {},
};

export function smartTradesReducer(
  state: ISmartTradesState = initialSmartTradesState,
  action: SmartTradesAction
): ISmartTradesState {
  switch (action.type) {
    case "setNewSmartTradesList":
      const newList = action.payload.map((smartTrade) => smartTrade.id);
      const newSmartTrades = _.keyBy(action.payload, "id");
      if (newList.length > 0) {
        return { ...state, list: [newList[0]], smartTrades: newSmartTrades };
      } else {
        return { ...state, list: newList, smartTrades: newSmartTrades };
      }
    case "setHasMore":
      return { ...state, hasMore: action.payload };
    case "addToSmartTradesList":
      const list = action.payload.map((smartTrade) => smartTrade.id);
      const smartTrades = _.keyBy(action.payload, "id");
      if (list.length > 0) {
        return {
          ...state,
          list: [list[0]],
          smartTrades: { ...state.smartTrades, ...smartTrades },
        };
      } else {
        return {
          ...state,
          list: list,
          smartTrades: { ...state.smartTrades, ...smartTrades },
        };
      }
    case "setSmartTradesOffset":
      return { ...state, offset: action.payload };
    case "setSmartTradesFilters":
      return { ...state, filters: action.payload };
    case "setSmartTrade":
      return {
        ...state,
        smartTrades: {
          ...state.smartTrades,
          [action.payload.smartTradeId]: action.payload.smartTrade,
        },
      };
    default:
      throw new Error();
  }
}
