import React from "react";
import { Routes, Route } from "react-router-dom";
import App from "./App";
import AccountDetails from "./components/Accounts/AccountDetails/AccountDetails";
import Accounts from "./components/Accounts/Accounts";
import { DCABotDetails } from "./components/DCABots/DCABotDetails/DCABotDetails";
import { DCABots } from "./components/DCABots/DCABots";
import GridBots from "./components/GridBots/GridBots";
import SmartTrades from "./components/SmartTrades/SmartTrades";

const Router = () => {
  return (
    <Routes>
      <Route path="/" element={<App />}>
        <Route index element={<DCABots />} />
        <Route path=":botId" element={<DCABotDetails />} />
        <Route path="/gridbots" element={<GridBots />} />
        <Route path="/smarttrades" element={<SmartTrades />} />
        <Route path="/accounts" element={<Accounts />} />
        <Route path="/accounts/:accountId" element={<AccountDetails />} />
      </Route>
    </Routes>
  );
};

export default Router;
