import { IDeal } from "../../interfaces/Deal";
import { getDealAgeInHours } from "../../utils/DealsUtils";
import { IDealsState } from "./DealsReducer";

export const INFINITY_IN_DAYS = 100000;
const hoursWithin30Days = 24 * 30;

export const is30daysDealsLoaded = (state: IDealsState, botId: number) =>
  botId in state.deals && state.deals[botId] && state.deals[botId].period >= 30;

export const isAllDealsLoaded = (state: IDealsState, botId: number) =>
  botId in state.deals &&
  state.deals[botId] &&
  state.deals[botId].period >= INFINITY_IN_DAYS;

export const selectAllDealsWithin30Days = (
  state: IDealsState,
  botId: number
) => {
  return is30daysDealsLoaded(state, botId)
    ? state.deals[botId].list.filter(
        (deal: any) => getDealAgeInHours(deal) <= hoursWithin30Days
      )
    : [];
};

export const selectAllDeals = (state: IDealsState, botId: number) => {
  return isAllDealsLoaded(state, botId) ? state.deals[botId].list : [];
};

export const filterAllSuccessfulDeals = (deals: IDeal[]) => {
  return deals.filter(
    (deal: IDeal) => deal.status !== "failed" && deal.status !== "cancelled"
  );
};
