import _ from "lodash";
import React, { useEffect, useState } from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { useTranslation } from "react-i18next";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { filterAllSuccessfulDeals } from "../../../../state/deals/DealsSelectors";
import {
  getCurrencyFormat,
  getSafetyOrderSize,
} from "../../../../utils/BotsUtils";
import "./DCABotDetailsSafetyInfo.css";

const calculateMaxPriceDeviation = (
  priceDeviation: any,
  maxSafetyOrders: any,
  stepScale: any
) => {
  return [...Array(maxSafetyOrders)].reduce(
    (previousValue, currentValue, index) => {
      return previousValue + priceDeviation * stepScale ** index;
    },
    0
  );
};

const DCABotDetailsSafetyInfo = ({
  account,
  bot,
  deals,
}: {
  account: any;
  bot: IBot;
  deals: IDeal[];
}) => {
  const { t } = useTranslation();

  const [safetyOrderSize, setSafetyOrderSize] = useState({
    value: 0,
    currency: "",
    additionalCurrency: "",
  });
  const [maxPriceDeviation, setMaxPriceDeviation] = useState(0);

  useEffect(() => {
    if (bot.id && account.id) {
      setSafetyOrderSize(getSafetyOrderSize(bot, account));
      setMaxPriceDeviation(
        calculateMaxPriceDeviation(
          parseFloat(bot.safety_order_step_percentage),
          bot.max_safety_orders,
          parseFloat(bot.martingale_step_coefficient)
        )
      );
    }
  }, [bot, account]);

  const [filteredDeals, setFilteredDeals] = useState<IDeal[]>([]);
  useEffect(() => {
    setFilteredDeals(filterAllSuccessfulDeals(deals));
  }, [deals]);

  const [avgSOCompleted, setAvgSOCompleted] = useState(0);
  const [nonSOCompleted, setNonSOCompleted] = useState(0);
  const [allSOCompleted, setAllSOCompleted] = useState(0);

  useEffect(() => {
    setAvgSOCompleted(
      _.mean(
        filteredDeals.map((deal) => {
          return (
            deal.completed_safety_orders_count +
            deal.completed_manual_safety_orders_count
          );
        })
      )
    );
    setNonSOCompleted(
      (filteredDeals.filter((deal) => {
        return (
          deal.completed_safety_orders_count +
            deal.completed_manual_safety_orders_count ===
          0
        );
      }).length /
        filteredDeals.length) *
        100
    );
    setAllSOCompleted(
      (filteredDeals.filter((deal) => {
        return (
          deal.completed_safety_orders_count +
            deal.completed_manual_safety_orders_count ===
          bot.max_safety_orders
        );
      }).length /
        filteredDeals.length) *
        100
    );
  }, [filteredDeals]);

  return (
    <div className="BotInfoTable">
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Max safety trades (active)</div>
        <div className="BotInfoTableRow">{`${bot.max_safety_orders} (${bot.active_safety_orders_count})`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotSafetyPriceDeviation")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Price deviation to open SO</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">
          {`${getCurrencyFormat({
            value: parseFloat(bot.safety_order_step_percentage),
            currency: "%",
          })}%`}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">SO size</div>
        <div className="BotInfoTableRow">
          {`${getCurrencyFormat(safetyOrderSize)} ${safetyOrderSize.currency}${
            safetyOrderSize.additionalCurrency
              ? ` (${safetyOrderSize.additionalCurrency})`
              : ""
          }`}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotSafetyVolumeScale")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">SO volume scale</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">
          {bot.martingale_volume_coefficient}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotSafetyStepScale")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">SO step scale</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">{bot.martingale_step_coefficient}</div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotSafetyMaxPrice")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Max price deviation</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">
          {`${getCurrencyFormat({
            value: maxPriceDeviation,
            currency: "%",
          })}%`}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Average # SO completed</div>
        <div className="BotInfoTableRow">
          {filteredDeals.length > 0 &&
            getCurrencyFormat({
              value: avgSOCompleted,
              currency: "%",
            })}
        </div>
      </div>

      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">None SO completed</div>
        <div className="BotInfoTableRow">
          {filteredDeals.length > 0 && (
            <>
              <div>{`${nonSOCompleted} deals`}</div>
              <div>
                {`${getCurrencyFormat({
                  value: nonSOCompleted > 100 ? 100 : nonSOCompleted,
                  currency: "%",
                })}%`}
              </div>
            </>
          )}
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">All SO completed</div>
        <div className="BotInfoTableRow">
          {filteredDeals.length > 0 && (
            <>
              <div>{`${allSOCompleted} deals`}</div>
              <div>
                {`${getCurrencyFormat({
                  value: allSOCompleted > 100 ? 100 : allSOCompleted,
                  currency: "%",
                })}%`}
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default DCABotDetailsSafetyInfo;
