import { getSmartTrades, getSmartTradeTrades } from "../../utils/ApiUtils";
import { convertFilterValueToDate } from "../../utils/GeneralUtils";
import {
  addToSmartTradesList,
  setHasMore,
  setNewSmartTradesList,
  setSmartTrade,
} from "./SmartTradesActions";
import { ISmartTradesState } from "./SmartTradesReducer";

export async function fetchSmartTrades(
  smartTradesDispatch: any,
  smartTradesState: ISmartTradesState,
  errorsDispatch: any,
  customLimit?: number
) {
  smartTradesDispatch(setHasMore(true));
  smartTradesDispatch(setNewSmartTradesList([]));
  const {
    limit,
    filters: { sorting, accountId, type, pair, status, from },
  } = smartTradesState;

  const perPage = customLimit ? customLimit : limit;
  const fromDate = convertFilterValueToDate(from);
  try {
    let hasMore = true;
    let page = 1;

    while (hasMore) {
      const data: any = await getSmartTrades(
        page,
        perPage,
        sorting,
        accountId,
        type,
        pair,
        status,
        fromDate
      );
      if (data.error) {
        break;
      }
      smartTradesDispatch(addToSmartTradesList(data));
      page = page + 1;
      if (data.length < perPage) {
        hasMore = false;
      }
    }
    smartTradesDispatch(setHasMore(false));
  } catch {
    smartTradesDispatch(setNewSmartTradesList([]));
    smartTradesDispatch(setHasMore(false));
    errorsDispatch({
      type: "addError",
      payload: "Error when fetching smart trades list",
    });
  }
}

export async function fetchTrades(
  smartTradesState: ISmartTradesState,
  errorsDispatch: any,
  smartTradeId: number
) {
  const { smartTrades } = smartTradesState;
  const smartTradeItem = smartTrades[smartTradeId];
  try {
    if (!("trades" in smartTradeItem) || !smartTradeItem.trades) {
      const smartTradeTrades = await getSmartTradeTrades(smartTradeId);
      return smartTradeTrades;
    } else {
      return smartTradeItem.trades;
    }
  } catch {
    errorsDispatch({
      type: "addError",
      payload: `Error when fetching market orders for grid bot ${smartTradeItem.id}`,
    });
    return {};
  }
}

export async function fetchDetails(
  smartTradesDispatch: any,
  smartTradesState: ISmartTradesState,
  errorsDispatch: any,
  smartTradeId: number
) {
  const trades = await fetchTrades(
    smartTradesState,
    errorsDispatch,
    smartTradeId
  );

  const { smartTrades } = smartTradesState;
  const smartTradeItem = smartTrades[smartTradeId];

  const smartTrade = {
    ...smartTradeItem,
    trades,
  };
  await smartTradesDispatch(setSmartTrade(smartTradeId, smartTrade));
}
