import React, { useContext, useEffect, useState } from "react";
import { OverlayTrigger, Tooltip } from "react-bootstrap";
import { useNavigate } from "react-router-dom";
import {
  DealsContext,
  ErrorsContext,
  StrategiesContext,
} from "../../../../App";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import { filterAllSuccessfulDeals } from "../../../../state/deals/DealsSelectors";
import {
  getCurrencyFormat,
  getFundsLockedInBotCurrency,
  getMaxUsageInBotCurrency,
  getReservedFundsInBotCurrency,
} from "../../../../utils/BotsUtils";
import { getAverageDealDuration } from "../../../../utils/DealsUtils";
import { getBotsStrategyName } from "../../BotsList/BotPreview/BotPreview";
import { useTranslation } from "react-i18next";

import "./DCABotDetailsMainInfo.css";

export const DCABotDetailsMainInfo = ({
  account,
  bot,
  deals,
}: {
  account: any;
  bot: IBot;
  deals: IDeal[];
}) => {
  const { t } = useTranslation();

  const { strategiesState } = useContext(StrategiesContext);
  const { dealsState } = useContext(DealsContext);
  const { activeDeals } = dealsState;

  const navigate = useNavigate();

  const [fundsLocked, setFundsLocked] = useState({
    value: 0,
    currency: "",
  });

  const [fundsLockedShare, setFundsLockedShare] = useState(0);
  const [filteredDeals, setFilteredDeals] = useState<any>([]);
  const [avgDealDuration, setAvgDealDuration] = useState<any>(0);

  const [maxUsage, setMaxUsage] = useState({
    value: 0,
    currency: "",
  });
  const [reservedFunds, setReservedFunds] = useState({
    value: 0,
    currency: "",
  });

  const percentFormatter = new Intl.NumberFormat("en-US", {
    style: "percent",
    minimumFractionDigits: 2,
  });

  const botPairs =
    bot.pairs.length === 1
      ? bot.pairs[0].split("_").reverse().join("/")
      : `${bot.pairs.length} pairs`;
  const botMarket =
    bot.pairs.length === 1 ? "" : `${bot.pairs[0].split("_")[0]} market`;

  const startCondition = bot.strategy_list.map((strategy: any) => (
    <div key={`${Math.random()}`}>{`${getBotsStrategyName(
      strategiesState,
      strategy.strategy
    )}${
      strategy.options && strategy.options.type
        ? `, ${getBotsStrategyName(strategiesState, strategy.options.type)}`
        : ``
    }${
      strategy.options && strategy.options.percent
        ? `, ${strategy.options.percent}%`
        : ``
    }${
      strategy.options && strategy.options.time
        ? `, ${strategy.options.time}`
        : ``
    }${
      strategy.options && strategy.options.points
        ? `, ${strategy.options.points}`
        : ``
    }`}</div>
  ));

  useEffect(() => {
    if (bot.id && account.id) {
      getMaxUsageInBotCurrency(bot, account).then((mxUsg) => {
        setMaxUsage(mxUsg);
      });
    }
  }, [bot, account]);

  useEffect(() => {
    if (bot && bot.id && bot.id in activeDeals) {
      setReservedFunds(
        getReservedFundsInBotCurrency(activeDeals[bot.id].list, bot, account)
      );
      setFundsLocked(
        getFundsLockedInBotCurrency(activeDeals[bot.id].list, bot, account)
      );
    }
  }, [activeDeals, bot]);

  useEffect(() => {
    setFilteredDeals(filterAllSuccessfulDeals(deals));
  }, [deals]);

  useEffect(() => {
    setAvgDealDuration(getAverageDealDuration(filteredDeals));
  }, [filteredDeals]);

  useEffect(() => {
    if (maxUsage.value && fundsLocked.value) {
      setFundsLockedShare(fundsLocked.value / maxUsage.value);
    }
  }, [maxUsage, fundsLocked]);

  return (
    <div className="BotInfoTable">
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Account</div>
        <div className="BotInfoTableRow">
          <div style={{ display: "flex" }}>
            {"market_icon" in account && (
              <div
                className="BotMarketIcon"
                style={{
                  backgroundImage: `url(${account.market_icon})`,
                }}
              ></div>
            )}
            <a
              onClick={(e: any) => {
                e.preventDefault();
                navigate({
                  pathname: `/accounts/${account.id}`,
                  search: window.location.search,
                });
              }}
              href="#"
            >
              {account.name}
            </a>
          </div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Bot pairs</div>
        <div className="BotInfoTableRow">
          <div>{botPairs}</div>
          <div>{botMarket}</div>
        </div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Deal start condition</div>
        <div className="BotInfoTableRow">{startCondition}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Active deals/Max</div>
        <div className="BotInfoTableRow">{`${bot.active_deals_count}/${bot.max_active_deals}`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotFundsUsed")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Funds used</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">
          <div>{`${getCurrencyFormat(fundsLocked)} ${
            fundsLocked.currency
          }`}</div>
          <div style={{ fontSize: "10px" }}>{`${percentFormatter.format(
            fundsLockedShare
          )} of max usage`}</div>
        </div>
      </div>

      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotMaxUsage")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Max usage</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">{`${getCurrencyFormat(maxUsage)} ${
          maxUsage.currency
        }`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <OverlayTrigger
          delay={{ show: 100, hide: 200 }}
          overlay={<Tooltip>{t("dcaBotReservedFunds")}</Tooltip>}
        >
          <div className="BotInfoTableHeader">Reserved funds</div>
        </OverlayTrigger>
        <div className="BotInfoTableRow">{`${getCurrencyFormat(
          reservedFunds
        )} ${maxUsage.currency}`}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Total finished deals</div>
        <div className="BotInfoTableRow">{filteredDeals.length}</div>
      </div>
      <div className="BotInfoTableColumn">
        <div className="BotInfoTableHeader">Avg deal duration</div>
        <div className="BotInfoTableRow">{`${avgDealDuration}`}</div>
      </div>
    </div>
  );
};
