import React, { useEffect, useState } from "react";
import { IBot } from "../../../../interfaces/Bot";
import { IDeal } from "../../../../interfaces/Deal";
import {
  getDealAgeInDaysSinceOpened,
  getLastNDays,
} from "../../../../utils/DealsUtils";
import { DCABotDetailsMainCharts } from "../DCABotDetailsMain/DCABotDetailsMainCharts";
import { DCABotDetailsProfitsCharts } from "../DCABotDetailsProfits/DCABotDetailsProfitsCharts";
import { DCABotDetailsSafetyCharts } from "../DCABotDetailsSafety/DCABotDetailsSafetyCharts";
import "./DCABotDetailsCharts.css";

export const DCABotDetailsCharts = ({
  bot,
  deals,
  account,
  infoKey,
  timeRangeValue,
}: {
  account: any;
  bot: IBot;
  timeRangeValue: string;
  infoKey: string;
  deals: IDeal[];
}) => {
  const [dateLabelsDays, setDateLabelsDays] = useState<string[]>([]);
  useEffect(() => {
    if (deals) {
      if (timeRangeValue === "allTime") {
        const oldestDealAgeInDaysSinceOpened =
          deals.length > 0
            ? Math.ceil(getDealAgeInDaysSinceOpened(deals.slice(-1).pop()))
            : 0;
        setDateLabelsDays(
          getLastNDays(
            oldestDealAgeInDaysSinceOpened > 30
              ? oldestDealAgeInDaysSinceOpened
              : 30
          )
        );
      } else if (timeRangeValue === "30d") {
        setDateLabelsDays(getLastNDays(30));
      }
    }
  }, [deals]);
  return (
    <>
      {deals && dateLabelsDays && bot && account && (
        <div className="DCABotDetailsCharts">
          {infoKey === "main" && (
            <DCABotDetailsMainCharts
              deals={deals}
              timeRangeValue={timeRangeValue}
              dateLabelsDays={dateLabelsDays}
            />
          )}
          {infoKey === "settings" && (
            <DCABotDetailsMainCharts
              deals={deals}
              timeRangeValue={timeRangeValue}
              dateLabelsDays={dateLabelsDays}
            />
          )}
          {infoKey === "profits" && (
            <DCABotDetailsProfitsCharts
              deals={deals}
              timeRangeValue={timeRangeValue}
              dateLabelsDays={dateLabelsDays}
              bot={bot}
            />
          )}
          {infoKey === "safeties" && (
            <DCABotDetailsSafetyCharts
              deals={deals}
              timeRangeValue={timeRangeValue}
              dateLabelsDays={dateLabelsDays}
              bot={bot}
            />
          )}
        </div>
      )}
    </>
  );
};
