import { IAccount } from "../../interfaces/Account";

interface ISetNewAccountsList {
  type: "setNewAccountsList";
  payload: Array<IAccount>;
}

export const setNewAccountsList = (
  accountsList: Array<IAccount>
): ISetNewAccountsList => ({
  type: "setNewAccountsList",
  payload: accountsList,
});

interface ISetHasMore {
  type: "setHasMore";
  payload: boolean;
}

export const setHasMore = (hasMore: boolean): ISetHasMore => ({
  type: "setHasMore",
  payload: hasMore,
});

interface IAddToAccountsList {
  type: "addToAccountsList";
  payload: Array<IAccount>;
}

export const addToAccountsList = (
  accountsList: Array<IAccount>
): IAddToAccountsList => ({
  type: "addToAccountsList",
  payload: accountsList,
});

interface ISetAccountsOffset {
  type: "setAccountsOffset";
  payload: number;
}

export const setAccountsOffset = (offset: number): ISetAccountsOffset => ({
  type: "setAccountsOffset",
  payload: offset,
});

interface ISetAccount {
  type: "setAccount";
  payload: { accountId: number | string; account: any };
}

export const setAccount = (
  accountId: number | string,
  account: any
): ISetAccount => ({
  type: "setAccount",
  payload: { accountId, account },
});

interface ISetAccountTableData {
  type: "setAccountTableData";
  payload: { accountId: number | string; accountTableData: any };
}

export const setAccountTableData = (
  accountId: number | string,
  accountTableData: any
): ISetAccountTableData => ({
  type: "setAccountTableData",
  payload: { accountId, accountTableData },
});

interface ISetAccountPieChartData {
  type: "setAccountPieChartData";
  payload: { accountId: number | string; accountPieChartData: any };
}

export const setAccountPieChartData = (
  accountId: number | string,
  accountPieChartData: any
): ISetAccountPieChartData => ({
  type: "setAccountPieChartData",
  payload: { accountId, accountPieChartData },
});

interface ISetAccountActiveTradingEntities {
  type: "setAccountActiveTradingEntities";
  payload: { accountId: number | string; accountActiveTradingEntities: any };
}

export const setAccountActiveTradingEntities = (
  accountId: number | string,
  accountActiveTradingEntities: any
): ISetAccountActiveTradingEntities => ({
  type: "setAccountActiveTradingEntities",
  payload: { accountId, accountActiveTradingEntities },
});

export type AccountsAction =
  | ISetAccountTableData
  | ISetNewAccountsList
  | ISetAccountActiveTradingEntities
  | ISetAccountPieChartData
  | ISetAccount
  | IAddToAccountsList
  | ISetAccountsOffset
  | ISetHasMore;
