import React, { useContext, useEffect, useState } from "react";
import { Bar, Doughnut } from "react-chartjs-2";
import { IDeal } from "../../../../interfaces/Deal";
import { filterAllSuccessfulDeals } from "../../../../state/deals/DealsSelectors";
import {
  DealsCountBarChartOptions,
  HistogramChartOptions,
} from "../../../../utils/ChartsUtils";
import {
  dealsStatusesCounts,
  getDealsCountPerDay,
  getDealsDurationHistogramData,
} from "../../../../utils/DealsUtils";
import "./DCABotDetailsMainCharts.css";

export const DCABotDetailsMainCharts = ({
  deals,
  timeRangeValue,
  dateLabelsDays,
}: {
  deals: IDeal[];
  timeRangeValue: string;
  dateLabelsDays: string[];
}) => {
  const [filteredDeals, setFilteredDeals] = useState<any>([]);
  const [countsValues, setCountsValues] = useState<any>([[], []]);
  const [dealsDurationHistogramData, setDealsDurationHistogramData] =
    useState<any>([]);

  const dealsStatusData = dealsStatusesCounts(deals);

  useEffect(() => {
    setFilteredDeals(filterAllSuccessfulDeals(deals));
  }, [deals]);

  useEffect(() => {
    setCountsValues(getDealsCountPerDay(filteredDeals, dateLabelsDays));
    setDealsDurationHistogramData(getDealsDurationHistogramData(filteredDeals));
  }, [filteredDeals]);

  return (
    <>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            DealsCountBarChartOptions(
              "Finished deals per day",
              dateLabelsDays.length < 70
            ) as any
          }
          data={{
            labels: dateLabelsDays,
            datasets: [
              {
                label: "Finished deals",
                data: countsValues,
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Doughnut
          options={
            {
              aspectRatio: 1.7,
              plugins: {
                title: {
                  display: true,
                  text: "Finished deals by Status",
                },
                tooltip: {
                  callbacks: {
                    label: (tooltipItem: any) => {
                      const dataset = tooltipItem.dataset;
                      const total = dataset.data.reduce(function (
                        previousValue: any,
                        currentValue: any
                      ) {
                        return currentValue
                          ? previousValue + currentValue
                          : previousValue;
                      },
                      0);
                      const currentValue = dataset.data[tooltipItem.dataIndex];
                      const percentage = Math.floor(
                        (currentValue / total) * 100 + 0.5
                      );
                      const label = tooltipItem.label;

                      return `${label}: ${currentValue} (${percentage}%)`;
                    },
                  },
                },
                legend: {
                  labels: {
                    filter: (legendItem: any, data: any) => {
                      return data.datasets[0].data[legendItem.index];
                    },
                  },
                },
              },
            } as any
          }
          width={300}
          data={{
            labels: dealsStatusData[0] as any,
            datasets: [
              {
                data: dealsStatusData[1],
                backgroundColor: [
                  "rgba(54, 162, 235, 0.2)",
                  "rgba(255, 99, 132, 0.2)",
                  "rgba(255, 206, 86, 0.2)",
                  "rgba(75, 192, 192, 0.2)",
                  "rgba(153, 102, 255, 0.2)",
                  "rgba(255, 159, 64, 0.2)",
                  "rgba(39, 47, 148, 0.2)",
                ],
                borderColor: [
                  "rgba(54, 162, 235, 1)",
                  "rgba(255, 99, 132, 1)",
                  "rgba(255, 206, 86, 1)",
                  "rgba(75, 192, 192, 1)",
                  "rgba(153, 102, 255, 1)",
                  "rgba(255, 159, 64, 1)",
                  "rgba(39, 47, 148, 1)",
                ],
                borderWidth: 1,
              },
            ],
          }}
        />
      </div>
      <div className="DCABotDetailsChartContainer">
        <Bar
          options={
            HistogramChartOptions(
              "Finished deals duration histogram, hours",
              "Duration, hours",
              "Number of deals"
            ) as any
          }
          data={{
            labels: dealsDurationHistogramData[0],
            datasets: [
              {
                label: "Finished deals",
                data: dealsDurationHistogramData[1],
                borderColor: "rgb(53, 162, 235)",
                backgroundColor: "rgba(53, 162, 235, 0.5)",
              },
            ],
          }}
        />
      </div>
    </>
  );
};
