import React, { useContext, useEffect, useState } from "react";
import { OverlayTrigger, Table, Tooltip } from "react-bootstrap";
import { Badge } from "react-bootstrap";
import { Button } from "react-bootstrap";
import "./BotPreview.css";
import { getCurrencyFormat, getFundsLocked } from "../../../../utils/BotsUtils";
import {
  BotsContext,
  DealsContext,
  ErrorsContext,
  StrategiesContext,
} from "../../../../App";
import {
  getFundsLockedAndTodaysProfit,
  getMaxUsageEffect,
  getAvgProfitEffect,
} from "../../../../state/bots/BotsEffects";
import { fetchDealsForBotFor30Days } from "../../../../state/deals/DealsEffects";
import { useNavigate } from "react-router-dom";
import { is30daysDealsLoaded } from "../../../../state/deals/DealsSelectors";
import { setDealsLimiter } from "../../../../utils/ApiUtils";
import moment from "moment";
import { differenceInDays } from "../../../../utils/DealsUtils";
import CurrencyIcon from "../../../CommonComponents/CurrencyIcon/CurrencyIcon";
import { useTranslation } from "react-i18next";

const strategyMap: any = {
  nonstop: "Non-stop",
  qfl: "QFL",
  tv_custom_signal: "TV custom signal",
  trading_view: "TV",
  buy_or_strong_buy: "Buy",
  strong_buy: "Strong Buy",
  sell_or_strong_sell: "Sell",
  strong_sell: "Strong Sell",
  rsi: "RSI",
  manual: "Manual",
  ult: "ULT",
  ta_preset: "TA Preset",
  cqs_telegram: "CQS Scalping",
  krypto_boss_free: "HIRN Crypto Free",
};

export const getBotsStrategyName = (
  strategiesState: any,
  botsStrategy: string
) => {
  if (botsStrategy in strategyMap) {
    return strategyMap[botsStrategy];
  }

  const marketsStrategy = strategiesState.strategies.find(
    (marketStrategy: any) => marketStrategy.strategy_key === botsStrategy
  );

  if (marketsStrategy) {
    return marketsStrategy.name;
  }

  return botsStrategy;
};

export const BotPreview = ({ botItem, account }: any) => {
  const { dealsState, dealsDispatch } = useContext(DealsContext);
  const { botsState, botsDispatch } = useContext(BotsContext);
  const { errorsDispatch } = useContext(ErrorsContext);
  const { strategiesState } = useContext(StrategiesContext);

  const [statsLoaded, setStatsLoaded] = useState(false);

  const navigate = useNavigate();

  const { deals } = dealsState;

  const { t } = useTranslation();

  useEffect(() => {
    getFundsLockedAndTodaysProfit(
      botsDispatch,
      botsState,
      errorsDispatch,
      botItem.id
    ).then(() => {
      setStatsLoaded(true);
    });
  }, [botItem]);

  useEffect(() => {
    if (statsLoaded) {
      getMaxUsageEffect(
        botsDispatch,
        botsState,
        errorsDispatch,
        botItem.id,
        account
      ).then();
    }
  }, [account, botItem, statsLoaded]);

  useEffect(() => {
    if (
      botItem &&
      !("avgProfit" in botItem) &&
      "maxUsage" in botItem &&
      is30daysDealsLoaded(dealsState, botItem.id)
    ) {
      getAvgProfitEffect(
        botsDispatch,
        botsState,
        dealsState,
        errorsDispatch,
        botItem.id
      );
    }
  }, [deals, botItem]);

  const getDeals = async () => {
    setDealsLimiter(Math.random());
    fetchDealsForBotFor30Days(
      dealsState,
      dealsDispatch,
      errorsDispatch,
      botItem.id
    );
  };

  const age = differenceInDays(moment(), moment(botItem.created_at));
  const formatter = new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
  });

  const strategy =
    botItem.strategy_list.length === 1
      ? getBotsStrategyName(strategiesState, botItem.strategy_list[0].strategy)
      : "Combined";

  return (
    <div className="BotPreview">
      <div className="BotPreviewRow">
        <div className="BotPreviewIconsSetContainer">
          <IconsSet bot={botItem} />
          <BotInfo
            strategy={botItem.strategy}
            age={age}
            is_enabled={botItem.is_enabled}
          />
          <div className="BotAvgProfit">
            <BotAvgProfit
              maxUsage={botItem.maxUsage}
              avgProfit={botItem.avgProfit}
              getDeals={getDeals}
              botId={botItem.id}
            />
          </div>
        </div>
        <div className="BotPreviewTableContainer">
          <BotName name={botItem.name} />
          <Table
            striped
            size="sm"
            style={{ fontSize: "12px", marginBottom: "0" }}
          >
            <tbody>
              <tr>
                <td style={{ fontWeight: "bold", width: "170px" }}>BotID</td>
                <td>{botItem.id}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold", width: "170px" }}>Account</td>
                <td>
                  <div
                    style={{
                      display: "flex",
                    }}
                  >
                    {"market_icon" in account && (
                      <div
                        className="BotMarketIcon"
                        style={{
                          backgroundImage: `url(${account.market_icon})`,
                        }}
                      ></div>
                    )}
                    <a
                      onClick={(e: any) => {
                        e.preventDefault();
                        navigate({
                          pathname: `/accounts/${account.id}`,
                          search: window.location.search,
                        });
                      }}
                      href="#"
                    >
                      {account.name}
                    </a>
                  </div>
                </td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Strategy</td>
                <td>{strategy}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Active deals/Max</td>
                <td>{`${botItem.active_deals_count}/${botItem.max_active_deals}`}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Today's profit/Loss</td>
                <td>{formatter.format(botItem.todayUsdProfit)}</td>
              </tr>
              <tr>
                <td style={{ fontWeight: "bold" }}>Total profit/Loss</td>
                <td>{formatter.format(botItem.finished_deals_profit_usd)}</td>
              </tr>
              <tr>
                <>
                  <OverlayTrigger
                    delay={{ show: 100, hide: 200 }}
                    overlay={<Tooltip>{t("dcaBotFundsUsedMaxUsage")}</Tooltip>}
                  >
                    <td style={{ fontWeight: "bold" }}>
                      Funds locked/Max usage
                    </td>
                  </OverlayTrigger>
                  <td>{`${formatter.format(
                    getFundsLocked(botItem)
                  )}/${formatter.format(botItem.maxUsage)}`}</td>
                </>
              </tr>
              <tr>
                <>
                  <OverlayTrigger
                    delay={{ show: 100, hide: 200 }}
                    overlay={<Tooltip>{t("dcaBotCurrentPnl")}</Tooltip>}
                  >
                    <td style={{ fontWeight: "bold" }}>Current uPnL</td>
                  </OverlayTrigger>
                  <td>{formatter.format(botItem.active_deals_usd_profit)}</td>
                </>
              </tr>
            </tbody>
          </Table>
          <div className="BotPreviewButtons">
            <Button
              variant="primary"
              onClick={() => {
                navigate({
                  pathname: `/${botItem.id}`,
                  search: window.location.search,
                });
              }}
            >
              {`View`}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export const IconsSet = ({ bot }: any) => {
  const quoteCurrency = bot.pairs[0].split("_")[0];
  const baseCurrency = bot.pairs[0].split("_")[1];
  const isMulti = bot.type === "Bot::MultiBot";
  return (
    <div className="IconsSetContainer">
      <div className="BotQuoteIconContainer">
        <CurrencyIcon currency={quoteCurrency} size={60} />
      </div>
      {isMulti ? (
        <div className="BotBaseIconContainer">
          <img src={`/Multicoin_white.png`} alt="logo" className="RobotsIcon" />
        </div>
      ) : (
        <div className="BotBaseIconContainer">
          <CurrencyIcon currency={baseCurrency} size={60} />
        </div>
      )}
    </div>
  );
};
export const BotName = ({ name }: any) => (
  <div className="BotNameContainer">
    <span className="BotName">{name}</span>
  </div>
);

export const BotInfo = ({ strategy, age, is_enabled }: any) => (
  <div className="BotInfoContainer">
    <div className="BotInfoRecordContainer">
      <div className="BotInfoLabel">Side:</div>
      {strategy === "long" ? (
        <Badge bg="success">Long</Badge>
      ) : (
        <Badge bg="danger">Short</Badge>
      )}
    </div>
    <div className="BotInfoRecordContainer">
      <div className="BotInfoLabel">Age:</div>
      <div className="BotInfoValue">{`${age} days`}</div>
    </div>
    {!is_enabled && (
      <div className="BotInfoRecordContainer">
        <Badge bg="warning" text="dark">
          Disabled
        </Badge>
      </div>
    )}
  </div>
);

export const BotAvgProfit = ({ maxUsage, avgProfit, getDeals }: any) => {
  const avgProfitCalculated = avgProfit || avgProfit === 0;
  return (
    <div className="BotAvgProfitContainer">
      {avgProfitCalculated && (
        <>
          <div
            className="BotAvgProfitValue"
            style={{ color: avgProfit.value < 0 ? "#FF0000" : "#198754" }}
          >
            {`${getCurrencyFormat(
              {
                value: avgProfit.value || 0,
                currency: "USD",
              },
              "always"
            )} $`}
          </div>
          <div
            className="BotAvgProfitPercent"
            style={{ color: avgProfit.value < 0 ? "#FF0000" : "#198754" }}
          >
            {`${getCurrencyFormat(
              {
                value: (avgProfit.value / maxUsage) * 100 || 0,
                currency: "USD",
              },
              "always"
            )} %`}
          </div>
        </>
      )}
      <div className="BotAvgProfitLabel">Avg. Daily (30d)</div>
      {!avgProfitCalculated && (
        <Button
          onClick={() => {
            getDeals();
          }}
          variant="link"
          size="sm"
          active
        >
          Calculate
        </Button>
      )}
    </div>
  );
};
