import _ from "lodash";
import { IGridBot, IGridBotFilters } from "../../interfaces/GridBot";
import { GridBotsAction } from "./GridBotsActions";

export interface IGridBotsState {
  list: Array<number>;
  hasMore: boolean;
  offset: number;
  filters: IGridBotFilters;
  limit: number;
  gridBots: { [key: number]: IGridBot };
}

export const gridBotsStateInitializer = (
  initialValue = initialGridBotsState
): IGridBotsState => {
  let storedFilters = null;
  try {
    storedFilters = JSON.parse(
      (localStorage as any).getItem("GridBotsFiltersState")
    );
  } catch {}
  if (storedFilters) {
    return { ...initialValue, filters: storedFilters };
  } else {
    return initialValue;
  }
};

const initialFiltersState = {
  accountType: "spot",
  account: "",
  sorting: "created_at-desc",
};

export const initialGridBotsState: any = {
  list: [],
  hasMore: true,
  limit: 10,
  offset: 0,
  filters: initialFiltersState,
  gridBots: {},
};

export function gridBotsReducer(
  state: IGridBotsState = initialGridBotsState,
  action: GridBotsAction
): IGridBotsState {
  switch (action.type) {
    case "setNewGridBotsList":
      const newList = action.payload.map((gridBot) => gridBot.id);
      const newGridBots = _.keyBy(action.payload, "id");
      if (newList.length > 0) {
        return { ...state, list: [newList[0]], gridBots: newGridBots };
      } else {
        return { ...state, list: newList, gridBots: newGridBots };
      }
    case "setHasMore":
      return { ...state, hasMore: action.payload };
    case "addToGridBotsList":
      const list = action.payload.map((gridBot) => gridBot.id);
      const gridBots = _.keyBy(action.payload, "id");
      if (list.length > 0) {
        return {
          ...state,
          list: [list[0]],
          gridBots: { ...state.gridBots, ...gridBots },
        };
      } else {
        return {
          ...state,
          list: list,
          gridBots: { ...state.gridBots, ...gridBots },
        };
      }
    case "setGridBotsOffset":
      return { ...state, offset: action.payload };
    case "setGridBotsFilters":
      return { ...state, filters: action.payload };
    case "setGridBot":
      return {
        ...state,
        gridBots: {
          ...state.gridBots,
          [action.payload.gridBotId]: action.payload.gridBot,
        },
      };
    default:
      throw new Error();
  }
}
